import {BasePreviewer} from "@/core/previewer/BasePreviewer";
import {PreviewerType} from "@/core/const/PreviewerType";
import * as d3 from 'd3';
import {ChartLayout} from "@/core/utils/ChartLayout";

export class CirclePreviewer extends BasePreviewer {
  constructor() {
    super();
    this.previewerType = PreviewerType.CIRCLE_CHART;
  }

  draw() {
    const rootSel = this.previewerContext.rootSel;
    const {width, height} = this.getCanvasSize();

    const scatterdata=[
        [22,11],
        [16,39],
        [22,20],
        [5,5],
        [29,39],
        [30,20],
        [10,18],
    ];
    const xLabels = [0, 15, 30];
    const yLabels = [20, 40];
    const layout = new ChartLayout({
      width, height,
      topRatio: 0.30,
      bottomRatio: 0.17,
      leftRatio: 0.2,
      rightRatio: 0.2
    });
    const barWidthRatio = 0.8;
    const barHeightRatio = 0.8;

    const xScale = d3.scaleLinear()
        .domain([0, d3.max(scatterdata, d => d[0]) / barWidthRatio])
        .range([0, layout.chartWidth])
        // .padding(barWidthRatio);
    const yScale = d3.scaleLinear()
        .domain([0, d3.max(scatterdata, d => d[1]) / barHeightRatio])
        .range([0, layout.chartHeight])

    const chartGrp = rootSel.append('g')
        .classed('chart-group', true);

    // create axis
    const axisGrp = chartGrp.append('g')
        .classed('axis-group', true);

    const xAxisGrp = axisGrp.append('g')
        .classed('x-axis-group', true)
        .attr('transform', `translate(${layout.origin.x},${layout.origin.y})`);

    xAxisGrp.selectAll('text').data(xLabels).enter().append('text')
        .classed(`chart axis y-axis label`, true)
        .attr('transform', d => `translate(${xScale(d)},0)`)
        .attr('dx', 0)
        .attr('dy', 17)
        .style('text-anchor', 'middle')
        .attr('font-weight', 'bold')
        .attr('fill', '#3d3d3d')
        .text(d => d);

    xAxisGrp.append('text')
        .classed(`chart axis x-axis title`, true)
        .attr('transform', `translate(${layout.chartWidth},0)`)
        .attr('dx', 10)
        .attr('dy', 5)
        .attr('font-weight', 'bold')
        .attr('fill', '#3d3d3d')
        .text('X');

    xAxisGrp.append('line')
        .classed('chart axis x-axis domain', true)
        .attr('x1', 0)
        .attr('y1', 0)
        .attr('x2', layout.chartWidth)
        .attr('y2', 0)
        .attr('stroke', '#808080')
        .attr('stroke-width', 4)
        .attr('stroke-linecap', 'round');

    const yAxisGrp = axisGrp.append('g')
        .classed('y-axis-group', true)
        .attr('transform', `translate(${layout.origin.x},${layout.top})`);

    yAxisGrp.selectAll('text').data(yLabels).enter().append('text')
        .classed(`chart axis y-axis label`, true)
        .attr('transform', d => `translate(0,${layout.chartHeight - yScale(d)})`)
        .attr('dx', -14)
        .attr('dy', 0)
        .style('text-anchor', 'middle')
        .attr('font-weight', 'bold')
        .attr('fill', '#3d3d3d')
        .text(d => d);

    yAxisGrp.append('line')
        .classed('chart axis y-axis domain', true)
        .attr('x1', 0)
        .attr('y1', 0)
        .attr('x2', 0)
        .attr('y2', layout.chartHeight)
        .attr('stroke', '#808080')
        .attr('stroke-width', 4)
        .attr('stroke-linecap', 'round');

    yAxisGrp.append('text')
        .classed(`chart axis y-axis title`, true)
        .attr('dy', -10)
        .attr('dx', -5)
        .style('text-anchor', 'middle')
        .attr('font-weight', 'bold')
        .attr('fill', '#3d3d3d')
        .text('Y');

    // create scatter
    const markGrp = chartGrp.append('g')
        .classed('mark-group', true)
        .attr('transform', `translate(${layout.left},${layout.top})`)
    markGrp.selectAll('circle').data(scatterdata).enter().append('circle')
        .classed('chart mark circle', true)
        .attr('cx', d => xScale(d[0]))
        .attr('cy', d => layout.chartHeight-yScale(d[1]))
        .attr('r', 5)
        .attr('fill', '#409eff');

    // create title
    const titleGrp = chartGrp.append('g')
        .classed('title-group', true)
    titleGrp.append('text')
        .classed('chart title main-title', true)
        .attr('transform', `translate(${layout.left + layout.chartWidth / 2},${layout.top})`)
        .attr('dy', -20)
        .style('text-anchor', 'middle')
        .attr('font-weight', 'bold')
        .attr('fill', '#3d3d3d')
        .text('Scatter Chart');
  }
}
