import { computeUUID } from "@/utils/common/uuid";

function updateSVGModel(svgStr, chartId) {
  console.log("targetUtil used")
  const svg = (new DOMParser())
    .parseFromString(svgStr, 'image/svg+xml')
    .querySelector('svg');

  const domElList = Array.from(svg.querySelectorAll('*:not(g,svg,defs,defs *)'));

  // get groups
  var groupList = []
  const groups = Array.from(svg.querySelectorAll('g'));
  groups.forEach(domEl => {
    const childEl = Array.from(domEl.children);
    var subgroupList = [];
    var flag = 1;
    if (childEl.length > 1) {
      for (var i = 0; i < childEl.length; i++) {
        if (childEl[i].tagName === "g") {
          flag = 0;
          break;
        }
        subgroupList.push(childEl[i].id)
      }
      if (flag) {
        groupList.push(subgroupList);
      }
    }
  });
  // console.log("groupList", groupList)


  // fix id
  domElList.forEach(domEl => {
    const elId = domEl.getAttribute('id');
    if (!elId || elId === '') {
      domEl.setAttribute('id', 'ac-' + computeUUID());
    } else {
      domEl.setAttribute('id', elId.replaceAll(' ', '-'));
    }
  });

  // fix class
  domElList.forEach(domEl => {
    if (!domEl.classList.contains(chartId)) {
      domEl.classList.add(chartId);
      domEl.classList.add('unknown-type')
    }
  })




  const { elements, chartType,tableTitle} = getElements(domElList);
  const targetModel = { chartType: chartType, elements, groups: groupList ,tableTitle};
  const newSvgStr = domElToString(svg);

  console.log('svg model', targetModel);

  return {
    targetModel,
    svgStr: newSvgStr,
  };
}

function getCommonColorName(colorCode) {
  // 颜色代码映射到RGB
  const parseColorCode = (code) => {
    if (code.startsWith("rgb")) {
      // 处理 rgb(205, 205, 205) 格式
      const rgbValues = code.match(/\d+/g);
      return rgbValues.map((value) => parseInt(value));
    } else if (code.startsWith("#")) {
      // 处理 #E2E1E5 或 #ddd 格式
      const hex = code.substring(1);
      if (hex.length === 3) {
        // 处理 #ddd 格式
        const r = parseInt(hex[0] + hex[0], 16);
        const g = parseInt(hex[1] + hex[1], 16);
        const b = parseInt(hex[2] + hex[2], 16);
        return [r, g, b];
      } else if (hex.length === 6) {
        // 处理 #E2E1E5 格式
        return [
          parseInt(hex.substring(0, 2), 16),
          parseInt(hex.substring(2, 4), 16),
          parseInt(hex.substring(4, 6), 16)
        ];
      }
    }
    return null;
  };

  // RGB映射到常用颜色名称
  const rgbToColorName = (rgb) => {
    const colorNames = {
      Red: [255, 0, 0],
      Orange: [255, 165, 0],
      Yellow: [255, 255, 0],
      Green: [0, 128, 0],
      Blue: [0, 0, 255],
      Purple: [128, 0, 128],
      Gray: [128, 128, 128],
      Black: [0, 0, 0],
      White: [255, 255, 255]
      // 添加更多颜色名称和对应的RGB值
    };

    let minDistance = Infinity;
    let closestColor = null;

    for (const [name, rgbValue] of Object.entries(colorNames)) {
      const distance = calculateDistance(rgb, rgbValue);
      if (distance < minDistance) {
        minDistance = distance;
        closestColor = name;
      }
    }

    return closestColor;
  };

  // 计算颜色距离
  const calculateDistance = (color1, color2) => {
    const rDiff = color1[0] - color2[0];
    const gDiff = color1[1] - color2[1];
    const bDiff = color1[2] - color2[2];
    return Math.sqrt(rDiff ** 2 + gDiff ** 2 + bDiff ** 2);
  };

  const rgb = parseColorCode(colorCode);
  if (rgb) {
    const colorName = rgbToColorName(rgb);
    return colorName ? colorName : null;
  } else {
    return null;
  }
}


function domElToString(domEl) {
  const tmp = document.createElement("div");
  tmp.appendChild(domEl);
  return tmp.innerHTML;
}

function getElements(domElList) {
  const elements = [];
  var tagSet = new Set()
  var xTitle = null;
  var yTitle = null;
  var title = null;
  let x = null;
  let y = null;
  let color = null; 
  domElList.forEach(domEl => {
    const el = {};
    const id = domEl.getAttribute('id');
    const clazz = domEl.getAttribute('class');
    const colorAttr = domEl.getAttribute('fill');
    color = colorAttr ? getCommonColorName(colorAttr) : null;
    const xAttr = domEl.getAttribute('x');

    if (xAttr) {
        x = parseFloat(xAttr);
        y = parseFloat(domEl.getAttribute("y"));
    } else {
        const transformAttr = domEl.getAttribute("transform");
        if (transformAttr) {
            const translateMatch = transformAttr.match(/translate\(([\d.-]+),([\d.-]+)\)/);
            if (translateMatch) {
                x = parseFloat(translateMatch[1]);
                y = parseFloat(translateMatch[2]);
            }
        }
    }

    const clazzList = clazz.split(' ');
    const type = clazzList[clazzList.length - 1];
    const tag = domEl.tagName;
    tagSet.add(type.toLowerCase());

    if (tag === 'text') {
      if (domEl.children.length !== 0) {
        el.text = Array.from(domEl.children)
          .reduce((str, node) => str + ' ' + node.innerHTML, '')
          .trim();
      } else {
        el.text = domEl.innerHTML;
      }
    } else {
      el.text = null;
    }

    if (type === "x-title") {
      xTitle = el.text;
    } else if (type === "y-title") {
      yTitle = el.text;
    } else if (type === "main-title" || type === "sub-title") {
      title = title + el.text + " ";
    }


    if (type != "unknown-type") {
      var data = domEl.getAttribute('ac-data');
      if (data) {
        data = JSON.parse(data);
      }

      el.type = type;
      el.id = id;
      el.clazz = clazz;
      el.data = data;
      el.tag = tag;
      el.color = color;
      el.x = x
      el.y = y

      elements.push(el);
    }
  });

  // detect charttype
  const tagList = Array.from(tagSet);
  var chartType = 'unknown';
  const arcKey = ["arc", "sec", "donut", "pie"];
  const barKey = ["bar"];
  const mapKey = ["area"];
  const scatterKey = ["scatter", "point", "circle"];
  const lineKey = ["line"];
  if (tagList.filter(v => arcKey.includes(v)).length > 0) {
    chartType = "pie-chart";
  } else if (tagList.filter(v => barKey.includes(v)).length > 0) {
    chartType = "bar-chart";
  } else if (tagList.filter(v => mapKey.includes(v)).length > 0) {
    chartType = "map-chart";
  } else if (tagList.filter(v => lineKey.includes(v)).length > 0) {
    chartType = "line-chart";
  } else if (tagList.filter(v => scatterKey.includes(v)).length > 0) {
    chartType = "scatter-chart";
  }
  const tableTitle={title:title,xtitle:xTitle,ytitle:yTitle}
  return { elements, chartType, tableTitle};
}

export const TargetUtils = {
  updateSVGModel, domElToString,
}