export class TemplateLibrary {
  /** @type {Map<string, BaseTemplate>} */
  buildInTemplateMap
  /** @type {Map<string, BaseTemplate>} */
  templateMap

  constructor() {
    this.buildInTemplateMap = new Map();
    this.templateMap = new Map();
  }

  /**
   * @param {BaseTemplate} template
   */
  registerBuildInTemplate(template) {
    if (this.buildInTemplateMap.has(template.templateName)) {
      console.warn('Duplicated template name in build-in scope' + template.templateName);
    }
    this.buildInTemplateMap.set(template.templateName, template);
  }

  /**
   * @param {...BaseTemplate} template
   */
  registerBuildInTemplates(...template) {
    template.forEach(t => this. registerBuildInTemplate(t));
  }

  /**
   * @param {BaseTemplate} template
   */
  registerTemplate(template) {
    if (this.templateMap.has(template.templateName)) {
      console.warn('Duplicated template name in custom scope' + template.templateName);
    }
    this.templateMap.set(template.templateName, template);
  }

  /**
   * @param {...BaseTemplate} template
   */
  registerTemplates(...template) {
    template.forEach(t => this.registerTemplate(t));
  }

  /**
   * @param {string} templateName
   * @returns {BaseTemplate}
   */
  getTemplate(templateName) {
    const prototype = this.templateMap.get(templateName)
        ?? this.buildInTemplateMap.get(templateName);
    if (!prototype) {
      return null;
    }
    return prototype.clone();
  }

  /**
   * @returns {BaseTemplate[]}
   */
  getAllBuildInTemplates() {
    return Array.from(this.buildInTemplateMap.values());
  }

  /**
   * @returns {BaseTemplate[]}
   */
  getAllCustomTemplate() {
    return Array.from(this.templateMap.values());
  }

  reset() {
    this.templateMap = new Map();
  }
}
