// eslint-disable-next-line
import { Effect } from '@/utils/entities-core/Effect'
import { effects } from '@/utils/preset'
import { computeUUID } from '../common/uuid'

class EffectFactory {
  /** @type {Map<string,Effect>}> */
  effectMap = new Map()

  constructor() {
    this.registerPresetEffects()
  }

  registerPresetEffects() {
    effects.forEach(effect => {
      this.registerEffect(effect)
    })
  }

  /**
   * Register an EffectGen with the factory
   * @param {Effect} effect
   * @return {EffectFactory} This object for the chain call
   */
  registerEffect(effect) {
    if (this.effectMap.has(effect.type)) {
      alert(`Effect ${effect.type} exists. Register failed.`)
    } else {
      this.effectMap.set(effect.type, effect)
    }
    return this
  }

  /**
   * Get a shadow copy of the effect
   * @param {(string|number)} type The type of the effect
   * @returns {Effect} A shadow copy of the effect generator
   */
  getEffect(type) {
    let effect = this.effectMap.get(type)
    if (!effect) {
      console.log('No such effect', type)
    }
    return new Effect(computeUUID(), effect.type, effect.genFunc,
      effect.duration, effect.offset, effect.defaults, effect.effLabels)
  }
}

const effectFactory = new EffectFactory()

export {
  effectFactory
}
