import {AbstractAniTemplate} from "@/utils/entities-core/AbstractAniTemplate";
import {ObjectUtils} from "@/utils/common/ObjectUtils";
import {Slot} from "@/utils/entities/Slot";
import {ArrayUtils} from "@/utils/common/ArrayUtils";
import {Animation} from "@/utils/entities-core/Animation";
import {deepCopy} from "@/utils/common/copy";
import {TemplateType} from "@/utils/const/TemplateType";

export class CanisAniTemplate extends AbstractAniTemplate {
  canisTemplate
  metaData

  constructor(id, name, tempLabels, defaults, canisTemplate) {
    super(id, name, TemplateType.UNKNOWN, tempLabels, defaults)
    this.canisTemplate = canisTemplate
  }

  createInstance() {
    this.metaData = deepCopy(this.canisTemplate)

    const configs = this.getMergedConfigs()
    const slotInfoList = this.getSlotInfoList()

    for (let slotInfo of slotInfoList) {
      const value = configs[slotInfo.slot['$slot']]
      if (value !== undefined) {
        slotInfo.object[slotInfo.key] = value
      }
    }

    const instance = Animation.from(this.metaData)
    instance.templateId = this.templateId

    return instance
  }

  checkValid() {
    const configs = this.getMergedConfigs()
    const slotInfoList = this.getSlotInfoList()
    return ArrayUtils.includes(Array.from(configs.keys()),
        slotInfoList.map(item => item.slot['$slot']))
  }

  getSlotInfoList() {
    /** @type {Array<{object: object, key: string, slot: object}>} */
    const slotInfoList = []
    ObjectUtils.traversalObject(this.metaData, (key, item, parent) => {
      if (Slot.isSlot(item)) {
        slotInfoList.push({object: parent, key, slot: item})
      }
    })
    return slotInfoList
  }
}