import { EffectGsap } from "@/utils/entities-core/EffectGsap";
import { Effect } from "@/utils/entities-core/Effect";
import { Direction } from "@/utils/const/Direction";

const defaults = {
  mode: 'in',
  direction: Direction.UP,
  ease: 'power1.out',
}

const gen = function (context, config) {
  let defs = context.container.select('defs')
  if (defs.empty()) {
    defs = context.container.append('defs')
  }

  let offsetFactorX, offsetFactorY;

  switch (config.direction) {
    case Direction.UP:
      offsetFactorX = 0;
      offsetFactorY = -1;
      break;
    case Direction.DOWN:
      offsetFactorX = 0;
      offsetFactorY = 1;
      break;
    case Direction.RIGHT:
      offsetFactorX = -1;
      offsetFactorY = 0;
      break;
    case Direction.LEFT:
      offsetFactorX = 1;
      offsetFactorY = 0;
      break;
  }

  if (config.mode === 'out') {
    offsetFactorX *= -1
    offsetFactorY *= -1
  }

  const maskId = context.target.id + '-mask'

  const bbox = context.target.getBBox();
  // the height of line is 0 so...
  const strokeWidth = parseFloat(context.target.getAttribute('stroke-width') ?? '0');
 /* eslint-disable */
  let originX, originY, width, height, finalX, finalY;
  width = bbox.width + strokeWidth;
  height = bbox.height + strokeWidth;
  originX =
      bbox.x +
      offsetFactorX * (bbox.width + strokeWidth * 2);
  originY =
      bbox.y +
      offsetFactorY * (bbox.height + strokeWidth * 2);
  finalX = bbox.x;
  finalY = bbox.y;

  let maskRect = defs.select(`#${maskId}`).select('rect')
  if (maskRect.empty()) {
    maskRect = defs.append('mask')
        .attr('id', maskId)
        .attr('maskUnits', 'userSpaceOnUse')
        .append('rect')
        .attr('transform', 'translate(' + [finalX, finalY] + ')')
        .attr('class', 'mask')
        .attr('width', width)
        .attr('height', height)
        .attr('stroke-width', strokeWidth)
        .attr('fill', 'white')
        .attr('stroke', 'white')
    context.target.setAttribute('mask', `url(#${maskId})`);
  }

  let from, to
  if (config.mode === 'in') {
    from = {x: originX, y: originY, ease: config.ease}
  } else if (config.mode === 'out') {
    to = {x: originX, y: originY, ease: config.ease}
  } else {
    return new EffectGsap({valid: false,
      message: "Wrong mode param: " + config.mode})
  }

  return new EffectGsap({from, to, target: maskRect.node()})
}

/* cite: https://greensock.com/forums/topic/17566-create-a-radial-wipe/?tab=comments#comment-79421 */
function getArcPath(cx, cy, rx, ry, startAngle, sweepAngle) {
  // 0 degrees, no path
  if (!sweepAngle) return `M${cx} ${cy}`;

  // mod angles to 360 degrees
  startAngle %= 360;
  sweepAngle %= 360;

  // If arc is 360 degrees, the sweep angle is 0 due to mod
  const isClosed = sweepAngle === 0;

  // If closed, we'll need to use 2 arcs with an angle of 180 degrees
  if (isClosed) {
    sweepAngle = 180;
  }

  // Make angle positive to simplify large arc flag
  if (sweepAngle < 0) {
    sweepAngle += 360;
  }

  const largeArc = sweepAngle > 180 ? 1 : 0;

  // Convert degrees to radians
  const a1 = (startAngle * Math.PI) / 180;
  const a2 = ((startAngle + sweepAngle) * Math.PI) / 180;

  // Calculate start and end coords for arc. Starts at 12 o'clock
  const x1 = cx + rx * Math.sin(a1);
  const y1 = cy - ry * Math.cos(a1);

  const x2 = cx + rx * Math.sin(a2);
  const y2 = cy - ry * Math.cos(a2);

  /*
  NOTE: To orient the starting point to 3 o'clock instead of 12 o'clock,
  add 90 degrees to the start angle, or change the code above to this...

  var x1 = cx + rx * Math.cos(a1);
  var y1 = cy + ry * Math.sin(a1);

  var x2 = cx + rx * Math.cos(a2);
  var y2 = cy + ry * Math.sin(a2);
  */

  // Related demo using arc path
  // https://codepen.io/osublake/pen/VbWKMj/
  if (isClosed) {
    return `
      M ${x1} ${y1}
      A ${rx} ${ry} 0 1 1 ${x2} ${y2}
      A ${rx} ${ry} 0 1 1 ${x1} ${y1}z`;
  }

  return `
    M ${x1} ${y1}
    A ${rx} ${ry} 0 ${largeArc} 1 ${x2} ${y2}
    L ${cx} ${cy}z`;
}


const defaultDuration = 1
const defaultOffset = 0

const wheel = new Effect('preset-wheel', 'wheel', gen,
  defaultDuration, defaultOffset, defaults)

export {
  wheel
}
