export class AniTemplateFactory {
  constructorMap = new Map()

  register(aniTemplate) {
    this.constructorMap.set(aniTemplate.templateId, aniTemplate.constructor)
  }

  createAniTemplate(templateId) {
    const constructor = this.constructorMap.get(templateId)
    if (constructor == null) {
      return null
    }
    return new constructor()
  }
}
