// eslint-disable-next-line
import {AniAtom} from "@/utils/entities-core/AniAtom"
// eslint-disable-next-line
import {AniUnit} from "@/utils/entities-core/AniUnit"
// eslint-disable-next-line
import {ConsEdge} from "./ConsEdge"
import {Relation} from "./Relation"
import {extent} from 'd3'
import {Position} from "@/utils/const/Position"

/**
 * Enum for the type of ConsNode
 * @readonly
 * @enum {string}
 */
const NodeType = {
  UNKNOWN: 'unknown',
  ROOT: 'root',
  UNIT_NODE: 'ani-unit-node',
  INTERNAL_NODE: 'internal-node',
  EL_NODE: 'element-node',
  ATOM_NODE: 'ani-atom-node',
  ADDED_NODE: 'added-node',
}

class ConsNode {
  /** @type {(number|string)} */
  id
  /** @type {NodeType} */
  type
  /** @type {ConsEdge[]} */
  outEdges

  /** @type {AniUnit[]} */
  aniUnits
  /** @type {object[]} */
  elGroup
  /** @type {AniAtom[]} */
  aniAtoms
  /** @type {object} */
  constraint
  /** @type {object} */
  alignCons

  /** @type {number} */
  computedPosition = 0
  /** @type {number} */
  computedDuration = 0

  /**
   * @param {(number|string)} id
   * @param {NodeType} [type=NodeType.UNKNOWN]
   */
  constructor(id, type=NodeType.UNKNOWN) {
    this.id = id
    this.type = type
    this.outEdges = []

    this.aniUnits = []
    this.elGroup = []
    this.aniAtoms = []
    this.constraint = {position: Position.WITH, stagger: 0, offset: 0, duration: null}
  }

  /**
   * @param {?Relation} relation
   * @returns {ConsEdge[]}
   */
   getOutEdges(relation=Relation.ANY) {
    if (relation !== Relation.ANY) {
      // check relation
      if (!Object.values(Relation).includes(relation)) {
        console.error('No such relation: ' + relation)
      }
      return this.outEdges.filter(e => e.relation === relation)
    } else {
      return [...this.outEdges]
    }
  }

  /**
   * @returns {null|ConsNode}
   */
  getParentNode() {
    let edge = this.getOutEdges(Relation.PARENT)[0]
    if (!edge) {
      console.error('Get parent failed', this);
      return null
    }
    return edge.dst
  }

  getChildNodes() {
    return this.getOutEdges(Relation.CHILD).map(e => e.dst)
  }

  getLastSiblingNode() {
    let edge = this.getOutEdges(Relation.LAST_SIBLING)[0]
    return edge ? edge.dst : null
  }

  getNextSiblingNode() {
    let edge = this.getOutEdges(Relation.NEXT_SIBLING)[0]
    return edge ? edge.dst : null
  }

  /**
   * Computes absolute position and saves it to computedPosition field.
   * Called when expand this node
   */
  computeAbsPosition() {
    let absPosition
    if (this.alignCons) {
      absPosition = this.alignCons.timestamp
      if (this.type === NodeType.ATOM_NODE) {
        this.constraint.offset = 0
      }
    } else if (this.type === NodeType.ROOT) {
      return
    // } else if (this.type === NodeType.ATOM_NODE) {
    //   // let parent = this.getParentNode()
    //   // // the delay will be considered when adding into timeline
    //   // absPosition = parent.getAbsPosition()
    //   let lastSibling = this.getLastSiblingNode()
    //   if (lastSibling) {
    //     absPosition = lastSibling.getAbsPosition()
    //   } else {
    //     // is first one
    //     let parent = this.getParentNode()
    //     absPosition = parent.getAbsPosition()
    //   }
    } else {
      let offset = this.constraint.offset || 0
      let lastSibling = this.getLastSiblingNode()
      if (lastSibling) {
        let stagger = this.constraint.stagger || 0
        absPosition = lastSibling.getAbsPosition() + offset + stagger
        if (this.constraint.position === Position.AFTER) {
          absPosition += lastSibling.getDuration()
        }
      } else {
        // is first one
        let parent = this.getParentNode()
        absPosition = parent.getAbsPosition() + offset
      }
    }
    this.computedPosition = absPosition
  }

  /**
   * Computes duration and saves it to computedDuration field.
   * Called when finishing processing all the children and returning to this node.
   */
  computeDuration() {
    let children = this.getChildNodes()
    if (this.type === NodeType.ATOM_NODE) {
      this.computedDuration = this.aniAtoms[0].effect.duration
      // let parent = this.getParentNode()
      // // the delay will be considered when adding into timeline
      // this.computedPosition = parent.getAbsPosition()
      return
    }
    if (children.length === 0) {
      return
    }

    let timestamps = []
    children.forEach(child => {
      timestamps.push(child.getAbsPosition())
      timestamps.push(child.getEndAbsPosition())
    })
    let [minTime, maxTime] = extent(timestamps)

    this.computedPosition = minTime
    this.computedDuration = maxTime - minTime
  }

  getAbsPosition() {
    return this.computedPosition
  }

  getEndAbsPosition() {
    return this.computedPosition + this.computedDuration
  }

  getDuration() {
    return this.computedDuration
  }

  appendAniAtomTo(tl) {
    if (this.type !== NodeType.ATOM_NODE) {
      console.error('Append error')
      return
    }
    let aniAtom = this.aniAtoms[0]
    aniAtom.appendTo(tl, this.computedPosition)
    let times = aniAtom.getAllottedTime()
    this.computedPosition = times[0]
    this.computedDuration = times[1] - times[0]
  }
}

export {
  ConsNode, NodeType
}
