import {NativeTemplate} from "@/core/entities/spec/template/NativeTemplate";
import {TemplateUsage} from "@/core/entities/spec/animation/TemplateUsage";
import {SyncAnimation} from "@/core/entities/spec/animation/SyncAnimation";
import {ParamType} from "@/core/entities/spec/parameter/ParamType";
import {ParamDefinition} from "@/core/entities/spec/parameter/ParamDefinition";

export class LineChartEnterTemplate extends NativeTemplate {
  constructor() {
    super();
    this.templateName = 'Line chart';
    this.parameters = [
      new ParamDefinition('titleStagger', ParamType.NUMBER, false, 0.2),
      new ParamDefinition('symbolStagger', ParamType.NUMBER, false, 0),
      new ParamDefinition('axisElementStagger', ParamType.NUMBER, false, 0),
      new ParamDefinition('ease', ParamType.STRING, false, 'power1.out'),
      new ParamDefinition('duration', ParamType.NUMBER, false, 1.5),
    ]
  }

  createAnimation() {
    const chartFadeIn = new TemplateUsage('Axes fade in')
        .setTarget('.chart')
        .updateSettings(this.getParamValues());
    
  
    const circleFadeIn = new TemplateUsage('Fade-in')
        .setTarget('.circle, .point, .scatter')
        .updateTiming({offset: 1})
        .set('duration', this.getParamValue('duration'));        

    const lineWipe = new TemplateUsage('Wipe')
        .setTarget('.mark.line')
        .updateTiming({start: 1})
        .updateSettings({
          stagger: 0.5,
          ease: this.getParamValue('ease'),
          direction: 'left',
        });

    const labelFadeIn = new TemplateUsage('Fade-in')
        .setTarget('.mark.label')
        .updateTiming({start: 1})
        .updateSettings({stagger: 0.1});


    return new SyncAnimation()
        .append(chartFadeIn)
        .append(circleFadeIn)
        .append(lineWipe)
        .append(labelFadeIn)
  }
}
