import {FilterTransform} from "@/core/entities/spec/transform/FilterTransform";
import {SortTransform} from "@/core/entities/spec/transform/SortTransform";

export class TransformParser {
  static reviver(key, value) {
    return key === "" ? TransformParser.fromJSON(value) : value;
  }

  /**
   * @param {Object} json
   * @returns {BaseTransform}
   */
  static fromJSON(json) {
    if (json == null || typeof json !== "object") {
      return null;
    }
    switch (json.type) {
      case 'filter':
        return Object.assign(new FilterTransform(), json);
      case 'sort':
        return Object.assign(new SortTransform(), json);
      default:
        console.warn('Unsupported transform type ' + json.type);
        return null;
    }
  }

  /**
   * @param {Object | Object[]} json
   */
  static fromObjectOrArray(json) {
    if (json == null || typeof json !== "object") {
      return [];
    }
    if (!Array.isArray(json)) {
      json = [json];
    }
    const resultArray = json.map(item => TransformParser.fromJSON(item));
    resultArray.filter(item => item != null);
    return resultArray;
  }
}