import { NativeTemplate } from "@/core/entities/spec/template/NativeTemplate";
import { ParamType } from "@/core/entities/spec/parameter/ParamType";
import { ParamDefinition } from "@/core/entities/spec/parameter/ParamDefinition";
import { NativeAnimation } from "@/core/entities/spec/animation/NativeAnimation";
import gsap from "gsap";
import { Timing } from "@/core/entities/spec/Timing";
import { ORIGINAL_DATA_STORE_PREFIX, ORIGINAL_FILL_ALIAS } from "@/core/const/Data";

export class ChangeColorTemplate extends NativeTemplate {
  constructor() {
    super();
    this.templateName = 'Change-color';
    this.parameters = [
      new ParamDefinition('fill', ParamType.STRING, true, '#ff0000'),
      new ParamDefinition('duration', ParamType.NUMBER, false, 1),
      new ParamDefinition('ease', ParamType.STRING, false, 'power1.out'),
      new ParamDefinition('stagger', ParamType.NUMBER, false, 0),
      new ParamDefinition('yoyo', ParamType.OBJECT, true, null),
    ];
  }

  createAnimation() {
    const animation = new ChangeColorAnimation();
    // transfer template settings to this native animation instance
    // as their parameters are the same
    animation.updateSettings(this.getParamValues());
    return animation;
  }
}

class ChangeColorAnimation extends NativeAnimation {
  getDefaultSettings() {
    return {
      duration: 1,
      ease: 'power1.out',
      stagger: 0,
      yoyo: null
    };
  }
  /**
   * @param {import('@/core/entities/context/Context').Context} context
   * @returns {gsap.core.Timeline | gsap.core.Tween}
   */
  create(context) {
    const { stagger } = this.settings;
    const glyphs = context.getGlyphs(this.target);
    const { duration, yoyo } = this.timing.getComputedTiming();
    const tl = gsap.timeline()
    glyphs.forEach((glyph, i) => {
      const tw = this.createTwForOneGlyph(glyph);
      if (yoyo) {
        tw.yoyo(true);
        tw.repeat(yoyo.repeat);
        tw.repeatDelay(yoyo.delay);
      }
      const position = i === 0 ? 0 : ('<' + stagger);
      tl.add(tw, position);
    });
    tl.duration(duration);
    return tl;
  }

  /**
   * @param {SVGGraphicsElement} glyph
   * @returns {gsap.core.Tween|null}
   */
  createTwForOneGlyph(glyph) {
    let { fill, duration, ease, yoyo } = this.settings;

    const yoyoVars = {};
    if (yoyo) {
      if (yoyo === true) {
        // not object, use default
        yoyo = Timing.getDefaultYoyo();
      }
      yoyoVars.yoyo = true;
      yoyoVars.repeat = yoyo.repeat;
      yoyoVars.repeatDelay = yoyo.delay;
      duration /= (yoyoVars.repeat + 1);
    }
    const originalColorAttrName = ORIGINAL_DATA_STORE_PREFIX + 'color'
    if (fill === ORIGINAL_FILL_ALIAS) {
      fill = glyph.getAttribute(originalColorAttrName);
    } else {
      const curFill = glyph.getAttribute('fill');
      glyph.setAttribute(originalColorAttrName, curFill);
    }
    const strokeList = ["line", "tick", "rule", "circle"];
    var toVars = { fill, duration, ease, ...yoyoVars };
    if (strokeList.indexOf(glyph.tagName) > 0) {
      toVars = {stroke: fill, duration, ease, ...yoyoVars };
    } else if(glyph.tagName==="path"){
      const glyphClass = glyph.getAttribute('class');
      strokeList.forEach((stroke) => {
        if (glyphClass.includes(stroke)){
          toVars = {stroke: fill, duration, ease, ...yoyoVars };
        }
      })
    }

    return gsap.to(glyph, toVars);
  }
}
