import {NativeTemplate} from "@/core/entities/spec/template/NativeTemplate";
import {ParamType} from "@/core/entities/spec/parameter/ParamType";
import {ParamDefinition} from "@/core/entities/spec/parameter/ParamDefinition";
import {NativeAnimation} from "@/core/entities/spec/animation/NativeAnimation";
import gsap from "gsap";

export class BasicZoomTemplate extends NativeTemplate {
  constructor() {
    super();
    this.templateName = 'basic-zoom';
    this.parameters = [
      new ParamDefinition('enterOrExit', ParamType.STRING, false, 'enter'),
      new ParamDefinition('duration', ParamType.NUMBER, false, 1),
      new ParamDefinition('ease', ParamType.STRING, false, 'back.out'),
      new ParamDefinition('stagger', ParamType.NUMBER, false, 0),
    ];
  }

  createAnimation() {
    const animation = new BasicZoomAnimation();
    // transfer template settings to this native animation instance
    // as their parameters are the same
    animation.updateSettings(this.getParamValues());
    return animation;
  }
}

class BasicZoomAnimation extends NativeAnimation {
  getDefaultSettings() {
    return {enterOrExit: 'enter', ease: 'power1.out', stagger: 0, duration: 1};
  }

  /**
   * @param {import('@/core/entities/context/Context').Context} context
   * @returns {gsap.core.Timeline | gsap.core.Tween}
   */
  create(context) {
    const {enterOrExit, ease, stagger} = this.settings;

    const tl = gsap.timeline();
    const glyphs = context.getGlyphs(this.target);

    glyphs.forEach((glyph) => {
      let tw;
      const vars = {scale: 0, transformOrigin: 'center center', ease};
      if (enterOrExit === 'enter') {
        tw = gsap.from(glyph, vars);
      } else if (enterOrExit === 'exit') {
        tw = gsap.to(glyph, vars);
      } else {
        console.warn("Wrong value of enterOrExit: " + enterOrExit);
        return null;
      }

      const {duration, yoyo} = this.timing.getComputedTiming();
      tw.duration(duration);
      if (yoyo) {
        tw.yoyo(true);
        tw.repeat(yoyo.repeat);
        tw.repeatDelay(yoyo.delay);
      }

      tl.add(tw, '<' + stagger);
    })

    return tl;
  }
}
