import {BaseTargetModel} from "@/core/generator/BaseTargetModel";

export class SimpleTargetModel extends BaseTargetModel {
  /** @type {d3.Selection} */
  globalSelection

  /**
   * @param {d3.Selection} globalSelection
   */
  constructor(globalSelection) {
    super();
    this.globalSelection = globalSelection;
  }

  /**
   * @param {d3.Selection} parentSelection
   * @param {Target} target
   * @returns {d3.Selection}
   */
  getGlyphsWithoutTransform(parentSelection, target) {
    return parentSelection.filter(target.selector);
  }

  /**
   * @param {d3.Selection} parentSelection
   * @param {Target} target
   * @returns {d3.Selection}
   */
  getGlyphs(parentSelection, target) {
    // console.log(target, parentSelection, parentSelection.filter(target.selector));
    let selection = parentSelection.filter(target.selector);
    target.transform.forEach(trans => {
      selection = trans.execute(selection);
    })
    return selection;
  }

  /**
   * @param {d3.Selection} parentSelection
   * @param {Target} target
   * @returns {d3.Selection}
   */
   getExclusiveGlyphs(parentSelection, target) {
    // console.log(target, parentSelection, parentSelection.filter(target.selector));
    let selection = parentSelection.filter(`:not(${target.selector})`);
    target.transform.forEach(trans => {
      selection = trans.execute(selection);
    })
    return selection;
  }
}