import {NativeTemplate} from "@/core/entities/spec/template/NativeTemplate";
import {ParamType} from "@/core/entities/spec/parameter/ParamType";
import {ParamDefinition} from "@/core/entities/spec/parameter/ParamDefinition";
import {NativeAnimation} from "@/core/entities/spec/animation/NativeAnimation";
import gsap from "gsap";
import {Direction} from "@/core/const/Direction";

export class GrowTemplate extends NativeTemplate {
  constructor() {
    super();
    this.templateName = 'Grow';
    this.parameters = [
      new ParamDefinition('direction', ParamType.STRING, false, Direction.BOTTOM),
      new ParamDefinition('duration', ParamType.NUMBER, false, 1),
      new ParamDefinition('ease', ParamType.STRING, false, 'power1.out'),
      new ParamDefinition('stagger', ParamType.NUMBER, false, 0),
    ];
  }

  createAnimation() {
    const animation = new GrowAnimation();
    // transfer template settings to this native animation instance
    // as their parameters are the same
    animation.updateSettings(this.getParamValues());
    return animation;
  }
}

class GrowAnimation extends NativeAnimation {
  getDefaultSettings() {
    return {direction: Direction.BOTTOM, ease: 'power1.out', stagger: 0, duration: 1};
  }

  /**
   * @param {import('@/core/entities/context/Context').Context} context
   * @returns {gsap.core.Timeline | gsap.core.Tween}
   */
  create(context) {
    const {stagger} = this.settings;
    const glyphs = context.getGlyphs(this.target);
    const tl = gsap.timeline()
    
    glyphs.forEach((glyph, i) => {
      const tw = this.createTwForOneGlyph(glyph);
      const position = i === 0 ? 0 : ('<' + stagger);
      tl.add(tw, position);
    });
    return tl;
  }
  /**
   * @param {SVGGraphicsElement} glyph
   * @returns {gsap.core.Tween|null}
   */
  createTwForOneGlyph(glyph) {
    const {direction, ease, duration} = this.settings;
    const bbox = glyph.getBBox();
    
    // console.log("glyph = ", glyph);
    // console.log("glyph.width = ", glyph.getAttribute("width"));
    // console.log("bbox = ", bbox);
    // if (glyph.getAttribute("width")>glyph.getAttribute("height") && glyph.nodeName=="rect"){
    //     this.updateSettings({
          // direction: Direction.LEFT,
        // });
    // }

    let attrName, transFactorX, transFactorY;
    switch (direction) {
      case Direction.TOP:
        attrName = 'scaleY';
        transFactorX = 0.5;
        transFactorY = 0;
        break;
      case Direction.BOTTOM:
        attrName = 'scaleY';
        transFactorX = 0.5;
        transFactorY = 1;
        break;
      case Direction.LEFT:
        attrName = 'scaleX';
        transFactorX = 0;
        transFactorY = 0.5;
        break;
      case Direction.RIGHT:
        attrName = 'scaleX';
        transFactorX = 1;
        transFactorY = 0.5;
        break;
      default: return null;
    }
    const transformOrigin = `${transFactorX * bbox.width}px ${transFactorY * bbox.height}px`
    const fromVars = {transformOrigin, duration, ease};
    fromVars[attrName] = 0;

    // console.log('Grow', glyph, fromVars);
    return gsap.from(glyph, fromVars);
  }
}