import {BasePreviewer} from "@/core/previewer/BasePreviewer";
import {PreviewerType} from "@/core/const/PreviewerType";
import * as d3 from 'd3';
import {ChartLayout} from "@/core/utils/ChartLayout";

export class LineWithLegendPreviewer extends BasePreviewer {
  constructor() {
    super();
    this.previewerType = PreviewerType.LINE_CHART_WITH_LEGEND;
  }

  draw() {
    const rootSel = this.previewerContext.rootSel;
    const {width, height} = this.getCanvasSize();

    const data1=[
        [5,15],
        [15,18],
        [22,20],
        [29,39],
    ];
    const data2=[
        [5,26],
        [15,28],
        [22,36],
        [29,10],
    ];
    const xLabels = [0, 15, 30];
    const yLabels = [20, 40];
    const layout = new ChartLayout({
      width, height,
      topRatio: 0.30,
      bottomRatio: 0.17,
      leftRatio: 0.2,
      rightRatio: 0.2
    });
    const barWidthRatio = 0.8;
    const barHeightRatio = 0.8;

    const xScale = d3.scaleLinear()
        .domain([0, d3.max(data1, d => d[0]) / barWidthRatio])
        .range([0, layout.chartWidth])
        // .padding(barWidthRatio);
    const yScale = d3.scaleLinear()
        .domain([0, d3.max(data1, d => d[1]) / barHeightRatio])
        .range([0, layout.chartHeight])

    const chartGrp = rootSel.append('g')
        .classed('chart-group', true);

    // create axis
    const axisGrp = chartGrp.append('g')
        .classed('axis-group', true);

    const xAxisGrp = axisGrp.append('g')
        .classed('x-axis-group', true)
        .attr('transform', `translate(${layout.origin.x},${layout.origin.y})`);

    xAxisGrp.selectAll('text').data(xLabels).enter().append('text')
        .classed(`chart axis y-axis label`, true)
        .attr('transform', d => `translate(${xScale(d)},0)`)
        .attr('dx', 0)
        .attr('dy', 17)
        .style('text-anchor', 'middle')
        .attr('font-weight', 'bold')
        .attr('fill', '#3d3d3d')
        .text(d => d);

    xAxisGrp.append('text')
        .classed(`chart axis x-axis title`, true)
        .attr('transform', `translate(${layout.chartWidth},0)`)
        .attr('dx', 10)
        .attr('dy', 5)
        .attr('font-weight', 'bold')
        .attr('fill', '#3d3d3d')
        .text('X');

    xAxisGrp.append('line')
        .classed('chart axis x-axis domain', true)
        .attr('x1', 0)
        .attr('y1', 0)
        .attr('x2', layout.chartWidth)
        .attr('y2', 0)
        .attr('stroke', '#808080')
        .attr('stroke-width', 4)
        .attr('stroke-linecap', 'round');

    const yAxisGrp = axisGrp.append('g')
        .classed('y-axis-group', true)
        .attr('transform', `translate(${layout.origin.x},${layout.top})`);

    yAxisGrp.selectAll('text').data(yLabels).enter().append('text')
        .classed(`chart axis y-axis label`, true)
        .attr('transform', d => `translate(0,${layout.chartHeight - yScale(d)})`)
        .attr('dx', -14)
        .attr('dy', 0)
        .style('text-anchor', 'middle')
        .attr('font-weight', 'bold')
        .attr('fill', '#3d3d3d')
        .text(d => d);

    yAxisGrp.append('line')
        .classed('chart axis y-axis domain', true)
        .attr('x1', 0)
        .attr('y1', 0)
        .attr('x2', 0)
        .attr('y2', layout.chartHeight)
        .attr('stroke', '#808080')
        .attr('stroke-width', 4)
        .attr('stroke-linecap', 'round');

    yAxisGrp.append('text')
        .classed(`chart axis y-axis title`, true)
        .attr('dy', -3)
        .attr('dx', -5)
        .style('text-anchor', 'middle')
        .attr('font-weight', 'bold')
        .attr('fill', '#3d3d3d')
        .text('Y');

    // create legend
    const legendGrp = chartGrp.append('g')
    .classed('mark-group', true)
    // .attr('transform', `translate(${layout.right},${layout.top})`);

    legendGrp.append('text')
        .classed(`chart mark legend`, true)
        .attr('transform', `translate(${width-layout.right},${layout.top})`)
        .attr('dx', 0)
        .attr('dy', 8)
        .attr('font-weight', 'bold')
        .attr('fill', '#409eff')
        .text('ta');

    legendGrp.append('rect')
        .classed('chart mark legend', true)
        .attr('transform', `translate(${width-layout.right},${layout.top})`)
        .attr('width', 18)
        .attr('height', 8)
        .attr('x', 20)
        .attr('y', 0)
        .attr('fill', '#409eff');
    
    legendGrp.append('text')
        .classed(`chart mark legend`, true)
        .attr('transform', `translate(${width-layout.right},${layout.top})`)
        .attr('dx', 0)
        .attr('dy', 26)
        .attr('font-weight', 'bold')
        .attr('fill', '#9ecae1')
        .text('tb');

    legendGrp.append('rect')
        .classed('chart mark legend', true)
        .attr('transform', `translate(${width-layout.right},${layout.top})`)
        .attr('width', 18)
        .attr('height', 8)
        .attr('x', 20)
        .attr('y', 18)
        .attr('fill', '#9ecae1');

    // create mark
    const markGrp = chartGrp.append('g')
        .classed('mark-group', true);

    const scatterGrp = markGrp.append('g')
        .classed('mark-group', true)
        .attr('transform', `translate(${layout.left},${layout.top})`)

    const scatter1Grp = scatterGrp.append('g')
        .classed('scatter1-group', true)

    scatter1Grp.selectAll('circle').data(data1).enter().append('circle')
        .classed('chart mark circle', true)
        .attr('cx', d => xScale(d[0]))
        .attr('cy', d => layout.chartHeight-yScale(d[1]))
        .attr('r', 5)
        .attr('fill', '#409eff');

    const scatter2Grp = scatterGrp.append('g')
        .classed('scatter2-group', true)

    scatter2Grp.selectAll('circle').data(data2).enter().append('circle')
        .classed('chart mark circle', true)
        .attr('cx', d => xScale(d[0]))
        .attr('cy', d => layout.chartHeight-yScale(d[1]))
        .attr('r', 5)
        .attr('fill', '#9ecae1');
    
    for (var i=0;i<data1.length;i++){
        data1[i][0]=xScale(data1[i][0]);
        data1[i][1]=layout.chartHeight-yScale(data1[i][1]);
    }
    
    for (i=0;i<data2.length;i++){
        data2[i][0]=xScale(data2[i][0]);
        data2[i][1]=layout.chartHeight-yScale(data2[i][1]);
    }
    
    const lineGrp = markGrp.append('g')
        .classed('line-group', true)
    
    const line1Grp = lineGrp.append('g')
        .classed('line1-group', true)

    // for (var i=0;i<data1.length-1;i++){
    //     line1Grp.append('line')
    //     .classed('chart mark line', true)
    //     .attr('transform', `translate(${layout.left},${layout.top})`)
    //     .attr('x1', xScale(data1[i][0]))
    //     .attr('y1', layout.chartHeight-yScale(data1[i][1]))
    //     .attr('x2', xScale(data1[i+1][0]))
    //     .attr('y2', layout.chartHeight-yScale(data1[i+1][1]))
    //     .attr('stroke', '#808080')
    //     .attr('stroke-width', 4)
    //     .attr('stroke-linecap', 'round');
    // }

    line1Grp.append('polyline')
        .classed('chart mark line first', true)
        .attr('transform', `translate(${layout.left},${layout.top})`)
        .attr("points",data1)
        .attr('fill','none')
        .attr('stroke', '#409eff')
        .attr('stroke-width', 4)
        .attr('stroke-linecap', 'round');

    const line2Grp = lineGrp.append('g')
        .classed('line2-group', true)

    // for (i=0;i<data2.length-1;i++){
    //     line2Grp.append('line')
    //     .classed('chart mark line', true)
    //     .attr('transform', `translate(${layout.left},${layout.top})`)
    //     .attr('x1', xScale(data2[i][0]))
    //     .attr('y1', layout.chartHeight-yScale(data2[i][1]))
    //     .attr('x2', xScale(data2[i+1][0]))
    //     .attr('y2', layout.chartHeight-yScale(data2[i+1][1]))
    //     .attr('stroke', '#808080')
    //     .attr('stroke-width', 4)
    //     .attr('stroke-linecap', 'round');
    // }

    line2Grp.append('polyline')
        .classed('chart mark line second', true)
        .attr('transform', `translate(${layout.left},${layout.top})`)
        .attr("points",data2)
        .attr('fill','none')
        .attr('stroke', '#9ecae1')
        .attr('stroke-width', 4)
        .attr('stroke-linecap', 'round');

    // create title
    const titleGrp = chartGrp.append('g')
        .classed('title-group', true)
    titleGrp.append('text')
        .classed('chart title main-title', true)
        .attr('transform', `translate(${layout.left + layout.chartWidth / 2},${layout.top})`)
        .attr('dy', -20)
        .style('text-anchor', 'middle')
        .attr('font-weight', 'bold')
        .attr('fill', '#3d3d3d')
        .text('Line Chart with Legend');
  }
}
