import { EffectGsap } from "@/utils/entities-core/EffectGsap";
import { Effect } from "@/utils/entities-core/Effect";
import { Direction } from "@/utils/const/Direction";

const defaults = {
  direction: Direction.UP,
  ease: 'power1.out',
}

const gen = function (target, config) {
  let attrName, origin

  switch (config.direction) {
    case Direction.UP: attrName = 'scaleY'; origin = 'bottom'; break;
    case Direction.DOWN: attrName = 'scaleY'; origin = 'top'; break;
    case Direction.LEFT: attrName = 'scaleX'; origin = 'right'; break;
    case Direction.RIGHT: attrName = 'scaleX'; origin = 'left'; break;
    default: return new EffectGsap({valid: false,
      message: "Wrong direction param: " + config.direction})
  }

  let from = {transformOrigin: origin, ease: config.ease}
  from[attrName] = 0

  return new EffectGsap({from})
}

const defaultDuration = 1
const defaultOffset = 0

const grow = new Effect('preset-grow', 'grow', gen,
  defaultDuration, defaultOffset, defaults)

export {
  grow
}
