import {NativeTemplate} from "@/core/entities/spec/template/NativeTemplate";
import {TemplateUsage} from "@/core/entities/spec/animation/TemplateUsage";
import {SyncAnimation} from "@/core/entities/spec/animation/SyncAnimation";

export class ArcWheelWithLegendTemplate extends NativeTemplate {
  constructor() {
    super();
    this.templateName = 'arc-wheel-with-legend';
    this.parameters = [
    ]
  }

  createAnimation() {

    const arcWheel = new TemplateUsage('simple-sector-wheel')
        .setTarget('.arc')
        .updateSettings({
          centerSelector: '.arc',
          endAngle: 360,
          startAngle: 0,
        });

    const legendFlyIn = new TemplateUsage('Fly-in')
        .setTarget('.legend')
        .updateSettings({
            direction: 'right',
        });

    return new SyncAnimation()
        .append(arcWheel)
        .append(legendFlyIn);
  }
}
