import {NativeTemplate} from "@/core/entities/spec/template/NativeTemplate";
import {ParamType} from "@/core/entities/spec/parameter/ParamType";
import {ParamDefinition} from "@/core/entities/spec/parameter/ParamDefinition";
import {NativeAnimation} from "@/core/entities/spec/animation/NativeAnimation";
import gsap from "gsap";
import * as d3 from 'd3';
import {computeUUID} from "@/core/utils/uuid";
import {CLIP_PATH_CLASS} from "@/core/const/Element";

export class SectorWheelTemplate extends NativeTemplate {
  constructor() {
    super();
    this.templateName = 'sector-wheel';
    this.parameters = [
      new ParamDefinition('enterOrExit', ParamType.STRING, false, 'enter'),
      new ParamDefinition('duration', ParamType.NUMBER, false, 1),
      new ParamDefinition('ease', ParamType.STRING, false, 'power1.out'),
      new ParamDefinition('stagger', ParamType.NUMBER, false, 0),
      new ParamDefinition('valueMapper', ParamType.STRING, false, '@datum.value'),
      new ParamDefinition('extraDegree', ParamType.NUMBER, false, 5),
    ];
  }

  createAnimation() {
    const animation = new SectorWheelAnimation();
    // transfer template settings to this native animation instance
    // as their parameters are the same
    animation.updateSettings(this.getParamValues());
    return animation;
  }
}

class SectorWheelAnimation extends NativeAnimation {
  x
  y
  width
  height

  /**
   * @param {import('@/core/entities/context/Context').Context} context
   * @returns {gsap.core.Timeline | gsap.core.Tween}
   */
  create(context) {
    const glyphs = context.getGlyphs(this.target);
    const defs = context.getDefsSel();
    const tl = gsap.timeline()
    const {stagger, valueMapper, extraDegree} = this.settings;

    let x = Infinity, y = Infinity, width = 0, height = 0;
    glyphs.forEach(glyph => {
      const bbox = glyph.getBBox();
      x = Math.min(x, bbox.x);
      y = Math.min(y ,bbox.y);
      width = Math.max(width, bbox.x + bbox.width - x);
      height = Math.max(height, bbox.y + bbox.height - y);
    });
    this.x = x;
    this.y = y;
    this.width = width;
    this.height = height;
    console.log('sector', x, y, width, height);

    const expr = valueMapper
        .replaceAll('@classList', '__node__.classList')
        .replaceAll('@id', '__node__.getAttribute("id")')
        .replaceAll('@index', '__index__')
        .replaceAll('@datum', '(JSON.parse(__node__.getAttribute("ac-data")) ?? {})')
        .replaceAll('@el', '__node__');
    console.log('sector mapper expr', expr)
    // eslint-disable-next-line no-unused-vars
    const values = glyphs.map((__node__, __index__) => {
      // console.log(__node__, eval(expr));
      return eval(expr);
    });
    const tot = values.reduce((sum, v) => sum + v, 0);
    const sums = [0];
    values.forEach((v, i) => sums.push(sums[i] + v));
    const startDegrees = values.map((v, i) => sums[i] / tot * 360),
          endDegrees = values.map((v, i) => (sums[i + 1]) / tot * 360);
    console.log({values, startDegrees, endDegrees})

    glyphs.forEach((glyph, i) => {
      const clip = this.addClipPathToGlyph(defs, glyph);
      const tw = this.createTwForClip(clip, glyph, startDegrees[i] - extraDegree, endDegrees[i] + extraDegree);
      const position = i === 0 ? 0 : ('<' + stagger);
      tl.add(tw, position);
    });

    return tl;
  }

  /**
   * @param {d3.Selection} defs
   * @param {SVGGraphicsElement} glyph
   * @returns {SVGGraphicsElement} the clip path of glyph
   */
  addClipPathToGlyph(defs, glyph) {
    if (!glyph.getAttribute('id')) {
      glyph.setAttribute('id', computeUUID());
    }
    const clipId = 'clip-path-' + glyph.getAttribute('id');
    let clip = defs.select(`#${clipId}`);
    if (clip.empty()) {
      clip = defs.append('clipPath')
          .attr('id', clipId)
          .classed(CLIP_PATH_CLASS, true);
      glyph.setAttribute('clip-path', `url(#${clipId})`);
    }
    const wheelPathSel = clip.select('.sector-wheel');
    if (!wheelPathSel.empty()) {
      return wheelPathSel.node();
    }

    let strokeWidth = parseFloat(glyph.getAttribute('stroke-width') ?? '0');
    d3.select(glyph).selectAll('*').each(function () {
      const tempWidth = parseFloat(this.getAttribute('stroke-width') ?? '0');
      strokeWidth = Math.max(tempWidth, strokeWidth);
    });

    const newClipPath = clip.append('path')
        .attr('class', 'sector-wheel')
        .attr('d', '')

    return newClipPath.node();
  }

  /**
   * Same as Grow effect, but applied on mark
   * @param {SVGGraphicsElement} clip
   * @param {SVGGraphicsElement} glyph
   * @param startDegree
   * @param endDegree
   * @returns {gsap.core.Tween|null}
   */
  createTwForClip(clip, glyph, startDegree, endDegree) {
    const {ease, duration} = this.settings;
    const startAngle = startDegree, sweepAngle = endDegree - startDegree;

    let arc, finalSweepAngle;
    arc = {
      startAngle: startAngle ?? 0,
      sweepAngle: 0,
      cx: this.x + this.width / 2,
      cy: this.y + this.height / 2,
      rx: Math.max(this.width, this.height),
      ry: Math.max(this.width, this.height),
    };
    console.log(arc.cx, arc.cy, arc)
    finalSweepAngle = sweepAngle;

    const updatePath = function updatePath() {
      const path = getArcPath(
          arc.cx,
          arc.cy,
          arc.rx,
          arc.ry,
          arc.startAngle,
          arc.sweepAngle
      );
      clip.setAttribute('d', path);
    };

    updatePath();

    // set animation
    return gsap.to(arc, {
      sweepAngle: finalSweepAngle,
      ease,
      onUpdate: updatePath,
      duration,
    });
  }
}

/* cite: https://greensock.com/forums/topic/17566-create-a-radial-wipe/?tab=comments#comment-79421 */
function getArcPath(cx, cy, rx, ry, startAngle, sweepAngle) {
  // 0 degrees, no path
  if (!sweepAngle) return `M${cx} ${cy}`;

  // mod angles to 360 degrees
  startAngle %= 360;
  sweepAngle %= 360;

  // If arc is 360 degrees, the sweep angle is 0 due to mod
  const isClosed = sweepAngle === 0;

  // If closed, we'll need to use 2 arcs with an angle of 180 degrees
  if (isClosed) {
    sweepAngle = 180;
  }

  // Make angle positive to simplify large arc flag
  if (sweepAngle < 0) {
    sweepAngle += 360;
  }

  const largeArc = sweepAngle > 180 ? 1 : 0;

  // Convert degrees to radians
  const a1 = (startAngle * Math.PI) / 180;
  const a2 = ((startAngle + sweepAngle) * Math.PI) / 180;

  // Calculate start and end coords for arc. Starts at 12 o'clock
  const x1 = cx + rx * Math.sin(a1);
  const y1 = cy - ry * Math.cos(a1);

  const x2 = cx + rx * Math.sin(a2);
  const y2 = cy - ry * Math.cos(a2);

  /*
  NOTE: To orient the starting point to 3 o'clock instead of 12 o'clock,
  add 90 degrees to the start angle, or change the code above to this...

  var x1 = cx + rx * Math.cos(a1);
  var y1 = cy + ry * Math.sin(a1);

  var x2 = cx + rx * Math.cos(a2);
  var y2 = cy + ry * Math.sin(a2);
  */

  // Related demo using arc path
  // https://codepen.io/osublake/pen/VbWKMj/
  if (isClosed) {
    return `
      M ${x1} ${y1}
      A ${rx} ${ry} 0 1 1 ${x2} ${y2}
      A ${rx} ${ry} 0 1 1 ${x1} ${y1}z`;
  }

  return `
    M ${x1} ${y1}
    A ${rx} ${ry} 0 ${largeArc} 1 ${x2} ${y2}
    L ${cx} ${cy}z`;
}
