import { NativeTemplate } from "@/core/entities/spec/template/NativeTemplate";
import { TemplateUsage } from "@/core/entities/spec/animation/TemplateUsage";
import { ParamType } from "@/core/entities/spec/parameter/ParamType";
import { ParamDefinition } from "@/core/entities/spec/parameter/ParamDefinition";
import { NormalTarget } from "@/core/entities/spec/Target";
import { ConcatAnimation } from "@/core/entities/spec/animation/ConcatAnimation";

export class ShowAnnoZoomTemplate extends NativeTemplate {
  constructor() {
    super();
    this.templateName = 'show-annotation-zoom';
    this.parameters = [
      new ParamDefinition('displayTime', ParamType.NUMBER, false, 3),
      new ParamDefinition('enter-ease', ParamType.STRING, false, 'back.out'),
      new ParamDefinition('exit-ease', ParamType.STRING, false, 'back.in'),
      new ParamDefinition('effect-duration', ParamType.NUMBER, false, 1),
    ]
  }

  createAnimation() {
    const zoomEnter = new TemplateUsage()
      .useTemplate('Zoom-enter')
      .set('Zoom-ease', this.getParamValue('enter-ease'))
      .set('duration', this.getParamValue('effect-duration'));

    const zoomExit = new TemplateUsage()
      .useTemplate('Zoom-exit')
      .set('Zoom-ease', this.getParamValue('exit-ease'))
      .set('duration', this.getParamValue('effect-duration'))
      .updateTiming({ offset: this.getParamValue('displayTime') });

    return new ConcatAnimation()
      .append(zoomEnter)
      .append(zoomExit)
      .setTarget(new NormalTarget('.annotation'));
  }
}
