import {Context} from "@/core/entities/context/Context";
import {MASK_CLASS} from "@/core/const/Element";
import {ANNOTATION_CLASS} from "@/core/const/Element";

export class RenderContext {
  /** @type {d3.Selection} */
  rootSel
  /** @type {d3.Selection} */
  defsSel
  /** @type {TemplateLibrary} */
  templateLib
  /** @type {d3.Selection} */
  globalSelection
  /** @type {BaseTargetModel} */
  targetModel

  /**
   * @param {d3.Selection} rootSel
   * @param {TemplateLibrary} templateLib
   */
  constructor(rootSel, templateLib) {
    this.rootSel = rootSel;
    this.defsSel = null;    // will auto create if not exist at the first usage
    this.templateLib = templateLib;
    this.globalSelection = rootSel.selectAll('*');
  }

  /**
   * @param templateName
   * @returns {BaseTemplate}
   */
  getTemplate(templateName) {
    return this.templateLib.getTemplate(templateName);
  }

  /**
   * @param {boolean} fakeRender
   * @returns {Context}
   */
  createRootContext(fakeRender=false) {
    return new Context(this, this.globalSelection, null, fakeRender);
  }

  clean() {
    if (!this.defsSel || this.defsSel.empty()) {
      return;
    }
    this.defsSel.selectAll(MASK_CLASS).remove();
    this.defsSel.selectAll(ANNOTATION_CLASS).remove();
  }

  /**
   * @returns {d3.Selection}
   */
  getDefsSel() {
    if (!this.defsSel) {
      let defsSel = this.rootSel.select('defs')
      if (defsSel.empty()) {
        defsSel = this.rootSel.append('defs')
      }
      this.defsSel = defsSel;
    }
    return this.defsSel;
  }
}
