import {ChartShotAnimationParser} from "@/core/parser/ChartShotAnimationParser";

export class Parser {
  /** @type {string | Object} */
  json

  /**
   * @param {string | Object} json
   */
  init(json) {
    this.json = json
  }

  /**
   * @returns {ChartShotAnimation}
   */
  parse() {
    return ChartShotAnimationParser.fromJSON(this.json)
  }
}