import {BaseAnimation} from "@/core/entities/spec/BaseAnimation";

export class NativeAnimation extends BaseAnimation {
  /**
   * @param {Object<string, any>} settings
   */
  constructor(settings={}) {
    super(settings);
    this.type = 'native';
  }

  /**
   * @param {Target | string} target
   * @returns {NativeAnimation} this
   */
  setTarget(target) {
    super.setTarget(target);
    return this;
  }

  /**
   * @param {Object} target
   * @returns {NativeAnimation} this
   */
  updateTarget(target) {
    super.updateTarget(target);
    return this;
  }

  /**
   * @param {Timing} timing
   * @returns {NativeAnimation} this
   */
  setTiming(timing) {
    super.setTarget(timing);
    return this;
  }

  /**
   * @param {Timing | Object} timing
   * @returns {NativeAnimation} this
   */
  updateTiming(timing) {
    super.updateTiming(timing);
    return this;
  }

  /**
   * @returns {Object<string, any>}
   */
  getDefaultSettings() {
    return {};
  }

  /**
   * @param {Object<string, any>} settings
   * @returns {NativeAnimation} this
   */
  updateSettings(settings) {
    Object.assign(this.settings, settings);
    return this;
  }

  /**
   * @param {String} key
   * @param {*} value
   * @returns {NativeAnimation} this
   */
  set(key, value) {
    this.settings[key] = value;
    return this;
  }

  /**
   * @param {import('@/core/entities/context/Context').Context} context
   * @returns {Set<HTMLElement>}
   */
  computePossibleElements(context) {
    const glyphs = context.getGlyphs(this.target);
    return new Set(glyphs);
  }

  // eslint-disable-next-line no-unused-vars
  computeDuration(context) {
    const {yoyo} = this.timing.getComputedTiming();
    if (this.timing?.duration) {
      return this.timing.duration;
    }

    const animation = this.create(context);
    if (!animation) {
      return 0;
    }
    if (yoyo) {
      animation.yoyo(true);
      animation.repeat(yoyo.repeat);
      animation.repeatDelay(yoyo.delay);
    }
    const duration = animation.duration();

    if (animation.clear) {
      // is timeline not tween
      animation.clear(true);
    }
    animation.kill();

    return duration;
  }
}
