import {NativeTemplate} from "@/core/entities/spec/template/NativeTemplate";
import {ParamType} from "@/core/entities/spec/parameter/ParamType";
import {ParamDefinition} from "@/core/entities/spec/parameter/ParamDefinition";
import {NativeAnimation} from "@/core/entities/spec/animation/NativeAnimation";
import gsap from "gsap";
import * as d3 from 'd3';
import { ANNOTATION_CLASS } from "@/core/const/Element";

export class ContourTemplate extends NativeTemplate {
  constructor() {
    super();
    this.templateName = 'Contour';
    this.parameters = [
      new ParamDefinition('reverseDirection', ParamType.BOOLEAN, false, false),
      new ParamDefinition('duration', ParamType.NUMBER, false, 1),
      new ParamDefinition('ease', ParamType.STRING, false, 'power1.out'),
      new ParamDefinition('stagger', ParamType.NUMBER, false, 0),
      new ParamDefinition('displayTime', ParamType.NUMBER, false, 3),
      new ParamDefinition('stroke', ParamType.STRING, false, "#ff0"),
      new ParamDefinition('customTransform', ParamType.STRING, false, ""),
    ];
  }

  createAnimation() {
    const animation = new ContourAnimation();
    // transfer template settings to this native animation instance
    // as their parameters are the same
    animation.updateSettings(this.getParamValues());
    return animation;
  }
}

class ContourAnimation extends NativeAnimation {
  /**
   * @param {import('@/core/entities/context/Context').Context} context
   * @returns {gsap.core.Timeline | gsap.core.Tween}
   */
  create(context) {
    const {displayTime, ease, stagger, duration, reverseDirection, stroke, customTransform} = this.settings;

    const tl = gsap.timeline();
    const glyphs = context.getGlyphs(this.target);

    glyphs.forEach((glyph, i) => {
      if(!glyph.getAttribute('id').includes('label')) {
        const contour = this.addMaskRectToGlyph(glyph, stroke, customTransform);
        let len = 0;
        try {
          len = glyph.getTotalLength();
          // eslint-disable-next-line no-empty
        } catch(e) {
        }
        contour.setAttribute('stroke-dasharray', len);

        let tw;

        tw = gsap.fromTo(contour, {strokeDashoffset: reverseDirection ? -len : len},
            {strokeDashoffset: 0, ease, duration});
        tl.add(tw, i === 0 ? 0 : ('<' + stagger));

        tw = gsap.to(contour, {strokeDashoffset: reverseDirection ? len : -len, ease, duration});
        tl.add(tw, '+=' + displayTime);
      }
      else{
        return
      }
    });

    return tl;
  }
  /**
     * @param {import('@/core/entities/context/Context').Context} context
     * @param {SVGGraphicsElement} glyph
     * @returns {SVGGraphicsElement} the mask rect of glyph
     */
  addMaskRectToGlyph(glyph, stroke, customTransform) {
    console.log(glyph)
    let newMaskRect = d3.select(glyph.parentNode).append('path')
        .attr('d', glyph.getAttribute('d'))
        .classed(ANNOTATION_CLASS, true)
        .attr('stroke-linejoin', "round")
        .attr('stroke-width', 3)
        .attr('fill', 'none')
        .attr('stroke', stroke);
    if(customTransform === "") {
      newMaskRect.attr('transform', glyph.getAttribute('transform'))
    }
    else if(customTransform !== "none"){
      newMaskRect.attr('transform', customTransform)
    }

    return newMaskRect.node();
  }
}
