import { getDefaultTargetTree } from "@/core/target/TargetUtils";

const mainClassSet = new Set(['mark', 'axis', 'title', 'annotation', 'background', 'legend', 'unknown-type']);

// initial state
const state = () => ({
  chartType: 'unknown',
  fullTargetList: [],
  selectedTargets: [],
  groupedTargetsMap: new Map(),
  dataGroupUpdateKeyword: null,

  fullTargetLoadSign: 0,
  tableSelectionChangeSign: 0,
  dataGroupChangeSign: 0,
  dataGroupClearSign: 0,
  svgSelectionChangeSign: 0,
  visibilityChangeSign: 0,
  new_counter: 0,

  dragDropSVG: null,
  clientX: 0,
  clientY: 0,

  targetTree: getDefaultTargetTree(),

  activeFilters: [],
})

// getters
const getters = {
  isDragging: state => state.dragDropSVG != null,
}

// actions
const actions = {
}

// mutations
const mutations = {
  initTargetModel(state, targetModel) {
    state.chartType = targetModel.chartType;
    state.fullTargetList = targetModel.elements;
    state.groupedTargetsMap = new Map();
    state.selectedTargets = [];

    // compute classList
    state.fullTargetList.forEach(target => {
      target.classList = target.clazz.split(' ').reverse();
    });

    // compute target default name
    state.fullTargetList.forEach(target => {
      // find a name
      let name = target.classList.find(cls => cls.match(`${target.type}-\\d*`));
      if (name) {
        const group = name.match(/^.*-(\d*)$/);
        if (group) {
          const no = group[1];
          name = target.type + ' #' + no;
        }
      } else {
        name = target.type;
      }
      target.name = name;
    });
    const counters = new Map();
    state.fullTargetList.forEach(target => {
      counters.set(target.name, (counters.get(target.name) ?? 0) + 1);
    });
    counters.forEach((value, key) => {
      if (value > 1) {
        state.fullTargetList
          .filter(target => target.name === key)
          .forEach((target, i) => target.name = `${key} #${i}`);
      }
    });

    // compute main class
    state.fullTargetList.forEach(target => {
      const mainClass = target.classList.find(cls => mainClassSet.has(cls));
      target.mainClass = mainClass ?? null;
    });

    state.fullTargetList.forEach(target => {
      target.visibility = true;
    });

    state.fullTargetList.forEach(target => {
      if (target.data === null) {
        return;
      }
      var tgt_data = target.data;
      if (tgt_data.constructor != Array) {
        tgt_data = new Array(tgt_data);
      }
      tgt_data = tgt_data.map(t => JSON.stringify(t).toLowerCase())
      target.related_targets = state.fullTargetList.filter(t => {
        if (t.id == target.id || t.data === null) {
          return false;
        }
        var data = t.data;
        if (data.constructor != Array) {
          data = new Array(data);
        }
        data = data.map(t => JSON.stringify(t).toLowerCase())
        return data.every(d => tgt_data.includes(d)) && data.length > 0;
      });
      // var ids = target.related_targets.filter(obj => obj.id).map(obj => obj.id); 
      // console.log(`target id: ${target.id}, related ids: ${ids.join(", ")}`);
    });

    state.fullTargetList.forEach(target => {
      var keyword = null;
      if (target.text != null) {
        keyword = target.text.toLowerCase().trim();
      } else if (target.data != null) {
        if (target.type.includes('symbol') || target.type.includes('sector-line')) {
          if (typeof target.data === 'object' && target.data.name != undefined) {
            keyword = target.data.name.toLowerCase().trim();
          } else if (typeof target.data === 'string' || target.data instanceof String) {
            keyword = target.data.toLowerCase().trim();
          }
        }
      }
      target.keyword = keyword;
    });

    state.fullTargetList.forEach(target => {
      if (target.keyword === null || target.keyword === undefined) {
        return;
      }
      if (!state.groupedTargetsMap.has(target.keyword)) {
        state.groupedTargetsMap.set(target.keyword, new Set());
      }
      var tmp = state.groupedTargetsMap.get(target.keyword);
      tmp.add(target.id);
      if (target.related_targets != null) {
        target.related_targets.forEach(t => tmp.add(t.id));
      }
      state.groupedTargetsMap.set(target.keyword, tmp);
    });
    console.log("groupedTargetsMap:", state.groupedTargetsMap)

    this.commit('target/changeFullTargetLoadSign');
    this.commit('target/changeTableSelectionSign');
    // console.log("fullTargetList:", state.fullTargetList)

  },
  updateDataGroup(state, { targets, keyword, oldkeyword }) {
    state.groupedTargetsMap.set(keyword, new Set(targets));
    this.commit('target/changeDataGroupSign');
    if (oldkeyword)
      this.commit('target/changeDataGroupKeyword', keyword);
  },
  changeFullTargetLoadSign(state) {
    state.fullTargetLoadSign ^= 1;
  },
  changeSelectedTargets(state, selectedTargets) {
    state.selectedTargets = selectedTargets;
  },
  changeSelectedTargetsById(state, targetIds) {
    const idSet = new Set(targetIds)
    state.selectedTargets = state.fullTargetList.filter(t => idSet.has(t.id));
  },
  changeSelectedTargetsByIcon(state, iconObj) {
    if (typeof iconObj.target === "string") {
      const idSet = new Set(iconObj.target.replace(/[#,]/g, "").split(/[\s\n]/));
      state.selectedTargets = state.fullTargetList.filter(t => idSet.has(t.id));
    }
    else if (Array.isArray(iconObj.target)) {
      const idSet = new Set(iconObj.target);
      state.selectedTargets = state.fullTargetList.filter(t => idSet.has(t.id));
    }
  },
  changeActiveFilters(state, activeFilters) {
    state.activeFilters = activeFilters;
  },
  changeTableSelectionSign(state) {
    state.tableSelectionChangeSign ^= 1;
  },
  changeDataGroupSign(state) {
    state.dataGroupChangeSign ^= 1;
  },
  changeDataGroupKeyword(state, keyword) {
    state.dataGroupUpdateKeyword = keyword;
  },
  resetDataGroupKeyword(state) {
    state.dataGroupUpdateKeyword = null;
  },
  changeSvgSelectionChangeSign(state) {
    state.svgSelectionChangeSign ^= 1;
  },
  changeVisibilityChangeSign(state) {
    state.visibilityChangeSign ^= 1;
  },
  changeDragDropSVG(state, svg) {
    state.dragDropSVG = svg;
  },
  changeCursorPosition(state, { x, y }) {
    state.clientX = x;
    state.clientY = y;
  },
  setTargetType(state, { targetIdList, targetNode }) {
    const idSet = new Set(targetIdList);
    const targets = state.fullTargetList.filter(target => idSet.has(target.id));
    const chartId = this.state.editor.scene.chartId;
    targets.forEach(target => {
      target.clazz = chartId + ' ' + targetNode.fullClasses;
      target.classList = targetNode.fullClasses.split(' ').reverse();
      target.type = targetNode.type;

      const mainClass = target.classList.find(cls => mainClassSet.has(cls));
      target.mainClass = mainClass ?? null;
    });

    state.fullTargetList = [...state.fullTargetList];   // self-assign to trigger the setter function
    this.commit('target/changeTableSelectionSign');
  },
  setTargetClass(state, { targetIdList, targetClass }) {
    const idSet = new Set(targetIdList);
    state.fullTargetList.forEach((target, index) => {
      if (idSet.has(target.id)) {
        target.mainClass = targetClass;
        state.fullTargetList[index] = target;
      }
    });
    state.fullTargetList = [...state.fullTargetList];
  },
  changeVisibility(state, { showIdList, hideIdList }) {
    const showIdSet = new Set(showIdList);
    const hideIdSet = new Set(hideIdList);
    for (let i = 0; i < state.fullTargetList.length; i++) {
      const t = state.fullTargetList[i];
      if (showIdSet.has(t.id)) {
        t.visibility = true;
        state.fullTargetList[i] = t;    // self-assign
      } else if (hideIdSet.has(t.id)) {
        t.visibility = false;
        state.fullTargetList[i] = t;    // self-assign
      }
    }
    this.commit('target/changeVisibilityChangeSign');
  },
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
