import {NativeTemplate} from "@/core/entities/spec/template/NativeTemplate";
import {ParamType} from "@/core/entities/spec/parameter/ParamType";
import {ParamDefinition} from "@/core/entities/spec/parameter/ParamDefinition";
import {NativeAnimation} from "@/core/entities/spec/animation/NativeAnimation";
import gsap from "gsap";
import {Timing} from "@/core/entities/spec/Timing";

export class MoveTemplate extends NativeTemplate {
  constructor() {
    super();
    this.templateName = 'move';
    this.parameters = [
      new ParamDefinition('from', ParamType.OBJECT, false, null),
      new ParamDefinition('to', ParamType.OBJECT, false, null),
      new ParamDefinition('duration', ParamType.NUMBER, false, 1),
      new ParamDefinition('ease', ParamType.STRING, false, 'power1.out'),
      new ParamDefinition('stagger', ParamType.NUMBER, false, 0),
      new ParamDefinition('yoyo', ParamType.OBJECT, false, null),
    ];
  }

  createAnimation() {
    const animation = new MoveAnimation();
    // transfer template settings to this native animation instance
    // as their parameters are the same
    animation.updateSettings(this.getParamValues());
    return animation;
  }
}

class MoveAnimation extends NativeAnimation {
  getDefaultSettings() {
    return {duration: 1, ease: 'power1.out', stagger: 0, yoyo: null};
  }

  /**
   * @param {import('@/core/entities/context/Context').Context} context
   * @returns {gsap.core.Timeline | gsap.core.Tween}
   */
  create(context) {
    const {from, to, ease, stagger} = this.settings;
    let {duration, yoyo} = this.settings;

    let tw;
    const glyphs = context.getGlyphs(this.target);

    const yoyoVars = {};
    if (yoyo) {
      if (yoyo === true) {
        // not object, use default
        yoyo = Timing.getDefaultYoyo();
      }
      yoyoVars.yoyo = true;
      yoyoVars.repeat = yoyo.repeat;
      yoyoVars.repeatDelay = yoyo.delay;
      duration /= (yoyoVars.repeat + 1);
    }

    if (from != null && to != null) {
      const fromVars = {...from};
      const toVars = {...to, ease, duration, stagger, ...yoyoVars};
      tw = gsap.fromTo(glyphs, fromVars, toVars);

    } else if (from != null) {
      const fromVars = {...from, ease, duration, stagger, ...yoyoVars};
      tw = gsap.from(glyphs, fromVars);

    } else if (to != null) {
      const toVars = {...to, ease, duration, stagger, ...yoyoVars};
      tw = gsap.to(glyphs, toVars);

    } else {
      return null;
    }

    return tw;
  }
}
