import {BaseAnimation} from "@/core/entities/spec/BaseAnimation";

export class TemplateUsage extends BaseAnimation {
  /** @type {string} */
  use

  /**
   * @param {string} use
   * @param {Object<string, any>} settings
   */
  constructor(use='', settings={}) {
    super(settings);
    this.use = use;
    this.type = 'usage';
  }

  /**
   * @param {string} use
   * @returns {TemplateUsage}
   */
  useTemplate(use) {
    this.use = use;
    return this;
  }

  computeDuration(context) {
    const {yoyo} = this.timing.getComputedTiming();
    if (this.timing?.duration) {
      return this.timing.duration;
    }
    const ani = this.instantiateUsingTemplate(context);
    let duration = ani?.computeDuration(context.deriveBy(this.target)) ?? 0;
    if (yoyo) {
      return duration * (yoyo.repeat + 1) + yoyo.delay * yoyo.repeat;
    }
    return duration;
  }

  /**
   * @param {import('@/core/entities/context/Context').Context} context
   * @returns {Set<HTMLElement>}
   */
  computePossibleElements(context) {
    const ani = this.instantiateUsingTemplate(context, false);
    if (!ani) {
      return new Set();
    }
    return ani.computePossibleElements(context.deriveByWithoutTransform(this.target));
  }

  /**
   * @param {import('@/core/entities/context/Context').Context} context
   * @returns {gsap.core.Timeline | gsap.core.Tween}
   */
  create(context) {
    const ani = this.instantiateUsingTemplate(context);
    if (!ani) {
      return null;
    }
    const tl = ani.create(context.deriveBy(this.target));
    const {yoyo} = this.timing.getComputedTiming();
    if (this.timing?.duration) {
      tl.duration(this.timing.duration);
    }
    if (yoyo) {
      tl.yoyo(true);
      tl.repeat(yoyo.repeat);
      tl.repeatDelay(yoyo.delay);
    }
    return tl;
  }

  /**
   * @param {import('@/core/entities/context/Context').Context} context
   * @param {boolean=true} toCheck
   * @returns {BaseAnimation}
   */
  instantiateUsingTemplate(context, toCheck=true) {
    if (!this.use || this.use === '') {
      return null;
    }
    const template = context.getTemplate(this.use);
    if (template != null) {
      return template.instantiate(this.settings, toCheck);
    } else {
      console.warn(`Template ${this.use} not found.`)
      return null;
    }
  }
}
