import {BasePreviewer} from "@/core/previewer/BasePreviewer";
import {PreviewerType} from "@/core/const/PreviewerType";
import * as d3 from "d3";

export class AnnotationArrowPreviewer extends BasePreviewer {
  constructor() {
    super();
    this.previewerType = PreviewerType.ANNOTATION_ARROW;
  }

  draw() {
    const rootSel = this.previewerContext.rootSel;
    const {width, height} = this.getCanvasSize();

    const barWidth = 25, barGap = 5;
    const barHeightList = [70, 83, 70, 110];
    const graphWidth = barHeightList.length * barWidth + (barHeightList.length + 3) * barGap;
    const left = (width - graphWidth) / 2;
    const bottom = (height + Math.max(...barHeightList)) / 2;
    const numTexts = ['first', 'second', 'third', 'forth'];

    const chartGrp = rootSel.append('g')
        .classed('chart group', true)
        .attr('transform', 'scale(1.4)')
        .style('transform-origin', `${width/2}px 0px`);

    const barGrp = chartGrp.append('g')
        .classed('mark-group', true);
    barGrp.selectAll('.bar.mark').data(barHeightList).enter().append('rect')
        .each(function(d, i) {
          d3.select(this).classed(`mark bar ${numTexts[i]}`, true);
        })
        .attr('width', barWidth)
        .attr('height', d => d)
        .attr('x', (d, i) => left + (i + 2) * barGap + i * barWidth)
        .attr('y',  (d) => bottom - d)
        .attr('fill', '#409eff')
        .attr('rx', 5)
        .attr('ry', 5);

    chartGrp.append('line')
        .classed('line axis domain', true)
        .attr('x1', left)
        .attr('y1', bottom)
        .attr('x2', left + graphWidth)
        .attr('y2', bottom)
        .attr('stroke', '#808080')
        .attr('stroke-width', 4)
        .attr('stroke-linecap', 'round');

    const annoGrp = chartGrp.append('g')
        .classed('annotation-group', true);

    const bar1TopX = left + barGap*2 + barWidth/2,
        bar2TopY = bottom - barHeightList[0];

    annoGrp.append('text')
        .classed('annotation label', true)
        .attr('transform', `translate(${bar1TopX},${bar2TopY - 30})`)
        .style('text-anchor', 'middle')
        .attr('font-size', 20)
        .attr('font-weight', 'bold')
        .attr('font-family', 'system-ui')
        .attr('fill', '#3b3b3b')
        .text('21');

    annoGrp.append('g')
        .classed('annotation arrow', true)
        .attr('transform', `translate(${bar1TopX - 16},${bar2TopY - 30})`)
        .append('svg')
        .attr('width', 30)
        .attr('height', 30)
        .attr('viewBox', '0 0 1024 1024')
        .append('path')
        .attr('d', 'M819.07712 636.39552l-225.4848 191.488a81.73568 81.73568 0 0 1-53.0432 19.47648 81.77664 81.77664 0 0 1-53.02272-19.47648l-225.4848-191.488a81.92 81.92 0 1 1 106.06592-124.86656l90.5216 76.88192v-329.8304a81.92 81.92 0 0 1 163.84 0v329.8304l90.54208-76.88192a81.89952 81.89952 0 1 1 106.06592 124.86656z')
        .attr('stroke', '#3b3b3b')
  }
}
