import gsap from 'gsap'

class EffectGsap {
  valid
  from
  to
  target

  message

  constructor({valid=true, from, to, message='', target}) {
    this.valid = valid
    this.from = from
    this.to = to
    this.target = target
    this.message = message
  }

  getTween(target, duration) {
    if (this.target) {
      target = this.target
    }
    let tw
    let from = this.from, to = this.to
    if (from != null && to != null) {
      to = {...to, duration}
      tw = gsap.fromTo(target, from, to)
    } else if (from != null) {
      from = {...from, duration}
      tw = gsap.from(target, from)
    } else {
      to = {...to, duration}
      tw = gsap.to(target, to)
    }
    return tw
  }
}

export {
  EffectGsap
}
