import {Animation} from "@/utils/entities-core/Animation"
import {AnimationTemplate} from "@/utils/entities-core/AnimationTemplate"
import {TemplateType} from "@/utils/const/TemplateType";

const animationJSON = [
  {
    "selector": ".axis.domain.x",
    "effects": [
      {
        "type": "grow",
        "direction": "right",
        "ease": "circ.inOut",
        "duration": 1.5
      },
    ],
    "labels": ["enter"],
    "position": "start after previous",
  },
  {
    "selector": ".axis.domain.y",
    "effects": [
      {
        "type": "grow",
        "direction": "up",
        "ease": "circ.inOut",
        "duration": 1.5
      },
    ],
    "labels": ["enter"],
    "position": "start with previous",
  },
  {
    "selector": ".axis.label,.axis.tick,.axis-label",
    "effects": [
      {
        "type": "fade",
        "mode": "in",
        "duration": 1.5
      },
    ],
    "labels": ["enter"],
    "position": "start after previous",
  },
  {
    "selector": ".bar",
    "effects": [
      {
        "type": "grow",
        "direction": "up",
        "ease": "circ.inOut",
        "duration": 1.5
      },
    ],
    "labels": ["enter"],
    "position": "start after previous",
    "offset": -1,
  },
  {
    "selector": ".bar-label",
    "effects": [
      {
        "type": "fade",
        "mode": "in",
        "duration": 1
      },
    ],
    "labels": ["enter", "fade-in"],
    "position": "start after previous",
  },
  {
    "selector": ".decoration.group",
    "effects": [
      {
        "type": "fade",
        "mode": "in",
        "duration": 1
      },
    ],
    "labels": ["enter", "fade-in"],
    "position": "start after previous",
  },
]

const animationGen = function() {
  let jsonStr = JSON.stringify(animationJSON)
  return Animation.from(JSON.parse(jsonStr))
}

const barChartEnter = new AnimationTemplate(
  'bar chart',
  'Bar chart enter',
  animationGen,
  TemplateType.BAR_CHART,
  ['chart-level', 'enter']
)

export {
  barChartEnter
}
