import {NativeTemplate} from "@/core/entities/spec/template/NativeTemplate";
import {ParamType} from "@/core/entities/spec/parameter/ParamType";
import {ParamDefinition} from "@/core/entities/spec/parameter/ParamDefinition";
import {NativeAnimation} from "@/core/entities/spec/animation/NativeAnimation";
import {Direction} from "@/core/const/Direction";
import gsap from "gsap";
import * as SvgUtils from "@/core/utils/SvgUtils";

export class GrowBarTemplate extends NativeTemplate {
  constructor() {
    super();
    this.templateName = 'Grow-bar';
    this.parameters = [
      new ParamDefinition('barDirection', ParamType.STRING, false, 'auto'),
      new ParamDefinition('ease', ParamType.STRING, false, 'power1.out'),
    ]
  }

  createAnimation() {
    const animation = new GrowBarAnimation();
    // transfer template settings to this native animation instance
    // as their parameters are the same
    animation.updateSettings(this.getParamValues());
    return animation;
  }
}

class GrowBarAnimation extends NativeAnimation {
  getDefaultSettings() {
    return {direction: Direction.AUTO, ease: 'power1.out', stagger: 0, duration: 1};
  }

  /**
   * @param {import('@/core/entities/context/Context').Context} context
   * @returns {gsap.core.Timeline | gsap.core.Tween}
   */
  create(context) {
    const {stagger, direction} = this.settings;
    const glyphs = context.getGlyphs(this.target);
    const tl = gsap.timeline()

    if (direction === Direction.AUTO) {
      this.settings.direction = this.inferDirection(context);
      // console.log('computed direction for bar:', this.settings.direction)
    }

    glyphs.forEach((glyph, i) => {
      const tw = this.createTwForOneGlyph(glyph);
      const position = i === 0 ? 0 : ('<' + stagger);
      tl.add(tw, position);
    });
    return tl;
  }

  /**
   * @param {SVGGraphicsElement} glyph
   * @returns {gsap.core.Tween|null}
   */
  createTwForOneGlyph(glyph) {
    const {ease, duration, direction} = this.settings;
    const bbox = glyph.getBBox();

    let attrName, transFactorX, transFactorY;
    switch (direction) {
      case Direction.TOP:
        attrName = 'scaleY';
        transFactorX = 0.5;
        transFactorY = 0;
        break;
      case Direction.BOTTOM:
        attrName = 'scaleY';
        transFactorX = 0.5;
        transFactorY = 1;
        break;
      case Direction.LEFT:
        attrName = 'scaleX';
        transFactorX = 0;
        transFactorY = 0.5;
        break;
      case Direction.RIGHT:
        attrName = 'scaleX';
        transFactorX = 1;
        transFactorY = 0.5;
        break;
      default: return null;
    }
    const transformOrigin = `${transFactorX * bbox.width}px ${transFactorY * bbox.height}px`
    const fromVars = {transformOrigin, duration, ease};
    fromVars[attrName] = 0;

    // console.log('Grow', glyph, fromVars);
    return gsap.from(glyph, fromVars);
  }

  /**
   * @param {import('@/core/entities/context/Context').Context} context
   * @return Direction
   */
  inferDirection(context) {
    const rootSel = context.getRootSel();

    const bars = rootSel.selectAll(".bar");
    const xAxisElements = rootSel.selectAll(".x-axis");

    if (xAxisElements.length !== 0 && bars.length !== 0) {
      return this.inferDirectionByBarsAndAxis(context, bars, xAxisElements);
    } else if (bars.length > 1) {
      return this.inferDirectionByBars(context, bars);
    } else {
      return Direction.BOTTOM;
    }
  }

  /**
   * Compute and set the direction using the position of bars and x-axis
   *
   * @param {import('@/core/entities/context/Context').Context} context
   * @param {d3.Selection} bars
   * @return Direction
   */
  inferDirectionByBars(context, bars) {
    const barArray = Array.from(bars.nodes());
    const barBoxes = barArray.map(b => SvgUtils.getSvgBox(context.getRootSvg(), b));

    const x0List = barBoxes.map(b => b.x);
    const x1List = barBoxes.map(b => b.x + b.width);
    const y0List = barBoxes.map(b => b.y);
    const y1List = barBoxes.map(b => b.y + b.height);

    // let y1 (bottom) have a higher priority
    const varianceList = [x0List, x1List, y1List, y0List].map(lst => GrowBarAnimation.getVariance(lst));

    let index = 0, minVal = varianceList[0];
    for (let i = 1; i < 4; i++) {
      if (varianceList[i] < minVal) {
        minVal = varianceList[i];
        index = i;
      }
    }

    switch (index) {
      case 0: return Direction.LEFT;
      case 1: return Direction.RIGHT;
      case 2: return Direction.BOTTOM;
      case 3: return Direction.TOP;
    }
  }

  /**
   * Compute and set the direction using the position of bars and x-axis
   *
   * @param {import('@/core/entities/context/Context').Context} context
   * @param {d3.Selection} bars
   * @param {d3.Selection} xAxisElements
   * @return Direction
   */
  inferDirectionByBarsAndAxis(context, bars, xAxisElements) {
    const barArray = Array.from(bars.nodes());
    const xElArray = Array.from(xAxisElements.nodes());

    const barBoxes = barArray.map(b => SvgUtils.getSvgBox(context.getRootSvg(), b));
    const xElBoxes = xElArray.map(el => SvgUtils.getSvgBox(context.getRootSvg(), el));

    // get the avg x y of the center of x-axis elements
    const sumX = xElBoxes.map(box => box.x + box.width / 2)
        .reduce((a, b) => a + b, 0);
    const sumY = xElBoxes.map(box => box.y + box.height / 2)
        .reduce((a, b) => a + b, 0);
    const avgX = sumX / xElBoxes.length;
    const avgY = sumY / xElBoxes.length;

    const x0List = barBoxes.map(b => b.x);
    const x1List = barBoxes.map(b => b.x + b.width);
    const y0List = barBoxes.map(b => b.y);
    const y1List = barBoxes.map(b => b.y + b.height);

    const lst1 = [x0List, x1List].map(lst => GrowBarAnimation.getMSE(lst, avgX));
    // let y1 (bottom) have a higher priority
    const lst2 = [y1List, y0List].map(lst => GrowBarAnimation.getMSE(lst, avgY));
    const varianceList = [...lst1, ...lst2];

    // console.log('>>', {xElBoxes, barBoxes, varianceList, avgX, avgY});
    let index = 0, minVal = varianceList[0];
    for (let i = 1; i < 4; i++) {
      if (varianceList[i] < minVal) {
        minVal = varianceList[i];
        index = i;
      }
    }

    switch (index) {
      case 0: return Direction.LEFT;
      case 1: return Direction.RIGHT;
      case 2: return Direction.BOTTOM;
      case 3: return Direction.TOP;
    }
  }

  /**
   * @param {number[]} array
   */
  static getVariance(array) {
    let sum = array.reduce((a, b) => a + b, 0);
    const avg = sum / array.length;
    return GrowBarAnimation.getMSE(array, avg);
  }

  /**
   * @param {number[]} array
   * @param {number} val
   */
  static getMSE(array, val) {
    let sum = 0;
    for (let i = 0; i < array.length; i++) {
      sum += Math.pow(array[i] - val, 2);
    }
    return sum / array.length;
  }
}
