import {BasePreviewer} from "@/core/previewer/BasePreviewer";
import {PreviewerType} from "@/core/const/PreviewerType";
import * as d3 from 'd3';
// import {ChartLayout} from "@/core/utils/ChartLayout";

export class ArcPreviewer extends BasePreviewer {
  constructor() {
    super();
    this.previewerType = PreviewerType.PIE_CHART;
  }

  draw() {
    const rootSel = this.previewerContext.rootSel;
    const {width, height} = this.getCanvasSize();

    const  dataset = [ 20 , 25, 55] 
    const outerRadius = 55; 
    const innerRadius = 20; 

    // const layout = new ChartLayout({
    //   width, height,
    //   topRatio: 0.30,
    //   bottomRatio: 0.17,
    //   leftRatio: 0.2,
    //   rightRatio: 0.35
    // });

    const piedata = d3.pie()(dataset);
    const colorlist = ['#409eff','#9ecae1','#2171b5'];
    const color = d3.scaleOrdinal(colorlist);
    const numTexts = ['first', 'second', 'third'];

    const arc = d3.arc()
        .innerRadius(innerRadius)
        .outerRadius(outerRadius)
        .padAngle(0.02);

    const chartGrp = rootSel.append('g')
        .classed('chart-group', true);

    const arcs = chartGrp.append('g')
        .classed('mark-group', true)

    arcs.selectAll('path').data(piedata).enter().append('path')
        .each(function(d, i) {
          d3.select(this).classed(`chart mark sector sector-wheel ${numTexts[i]}`, true);
        })
        .attr('transform', 'translate(' + (width / 2) + ',' + (height / 2+10)+')')
        .attr('fill', (d, i) => {
            return color(i);
        })
        .attr('d', (d) => {
            return arc(d);
        });

    const arcslabel = chartGrp.append('g')
        .classed('label-group', true)

    arcslabel.selectAll('text').data(piedata).enter().append('text')
        .each(function(d, i) {
          d3.select(this).classed(`cchart label sector-label ${numTexts[i]}`, true);
        })
        // .classed('chart label sector-label', true)
        .attr('transform', (d) => {
          return 'translate(' + arc.centroid(d) + ')';
        })
        .attr('dx', 100)
        .attr('dy', 90)
        .attr('fill', 'white')
        .attr('text-anchor', 'middle')
        .text((d) => {
          return d.data + '%';
        });


    // create title
    // const titleGrp = chartGrp.append('g')
    //     .classed('title-group', true)
    // titleGrp.append('text')
    //     .classed('chart title main-title', true)
    //     .attr('transform', `translate(${layout.left + layout.chartWidth / 2},${layout.top})`)
    //     .attr('dy', -30)
    //     .attr('dx', 5)
    //     .attr('font-size', 13)
    //     .style('text-anchor', 'middle')
    //     .attr('font-weight', 'bold')
    //     .attr('fill', '#3d3d3d')
    //     .text('Arc Chart');
  }
}
