import { AniUnit } from "@/utils/entities-core/AniUnit"
import {AbstractAniInstance} from "@/utils/entities-core/AbstractAniInstance";

class Animation extends AbstractAniInstance {
  /** @type {AniUnit[]} */
  aniUnits = []
  /** @type {Map<(string|number), AniUnit>} */
  aniUnitMap = new Map()

  /**
   * Add an AniUnit to this animation
   * @param {AniUnit} aniUnit
   */
  addAniUnit(aniUnit) {
    this.aniUnits.push(aniUnit)
    this.aniUnitMap.set(aniUnit.aniUnitId, aniUnit)
  }

  toJsonObj() {
    let result = {aniUnits:[]}
    this.aniUnits.forEach(aniUnit => {
      result.aniUnits.push(aniUnit.toJsonObj())
    })
    return result
  }

  /**
   * Creat an animation from a raw object.
   * @param {object} rawObj Raw json object of animaion class
   */
  static from(rawObj) {
    try {
      let animation = new Animation()
      rawObj.forEach(aniUnitRaw => {
        let aniUnit = AniUnit.from(aniUnitRaw)
        animation.addAniUnit(aniUnit)
      })
      return animation
    } catch (exception) {
      console.error(exception)
    }
  }
}

export {
  Animation
}
