/**
 * Represent the relation: Src is a XXX of Dst
 * @readonly
 * @enum {string}
 */
const Relation = {
  ANY: 'any',
  CHILD: 'child',
  PARENT: 'parent',
  LAST_SIBLING: 'last-sibling',
  NEXT_SIBLING: 'next-sibling',
  CONSTRAINT: 'constraint',
  CONSTRAINT_REV: "constraint-reverse"
}

/**
 * Get the reversed relation
 * @param {Relation} relation
 * @returns {Relation}
 */
function reverseRelation(relation) {
  switch(relation) {
    case Relation.ANY: return Relation.ANY
    case Relation.CHILD: return Relation.PARENT
    case Relation.PARENT: return Relation.CHILD
    case Relation.LAST_SIBLING: return Relation.NEXT_SIBLING
    case Relation.NEXT_SIBLING: return Relation.LAST_SIBLING
    case Relation.CONSTRAINT: return Relation.CONSTRAINT_REV
    default: console.error('Unknown relation', relation);
  }
}

export {
  Relation, reverseRelation,
}
