import { EffectGsap } from "@/utils/entities-core/EffectGsap";
import { Effect } from "@/utils/entities-core/Effect";

const defaults = {
  mode: 'from',   // from, to, yoyo
  x: '+=0',
  y: '+=0',
  yoyoDelay: 0,
  ease: 'power1.out',
}

const gen = function (target, config) {
  let from = null, to = null

  if (config.mode === 'from') {
    from = {x: config.x, y: config.y, ease: config.ease}
  } else if (config.mode === 'to') {
    to = {x: config.x, y: config.y, ease: config.ease}
  } else if (config.mode === 'yoyo') {
    to = {x: config.x, y: config.y, ease: config.ease,
      yoyo: true, repeat: 1, repeatDelay: config.yoyoDelay}
  } else {
    return new EffectGsap({valid: false,
      message: "Wrong mode param: " + config.mode})
  }

  return new EffectGsap({from, to})
}

const defaultDuration = 1
const defaultOffset = 0

const move = new Effect('preset-move', 'move', gen,
  defaultDuration, defaultOffset, defaults)

export {
  move
}
