export class BaseFilter {
    name
    hardElement
    softElement
    componentType
    animationType
    effectList

    constructor(
        name,
        hardElement,
        softElement,
        componentType,
        animationType,
        effectList
    ) {
        this.name = name;
        this.hardElement = hardElement;
        this.softElement = softElement;
        this.componentType = componentType;
        this.animationType = animationType;
        this.effectList = effectList;
    }

    clone() {
        return new BaseFilter(
            this.name,
            this.hardElement,
            this.softElement,
            this.componentType,
            this.animationType,
            this.effectList
        );
    }

    getName() {
        return this.name;
    }

    getHardElement() {
        return this.hardElement;
    }

    getSoftElement() {
        return this.softElement;
    }

    getComponentType() {
        return this.componentType;
    }

    getAnimationType() {
        return this.animationType;
    }

    getEffectList() {
        return this.effectList;
    }

    relatedTargets(fullTargetList) {
        const relatedTargets = [];
        fullTargetList.forEach(ele => {
            const clist = ele.classList;
            if (this.hardElement.some(el => clist.includes(el))
            ||  this.softElement.some(el => clist.includes(el))) {
                relatedTargets.push(ele);
            }
        })
        return relatedTargets;
    }

    hasLabel(target) {
        const cl = target.classList;
        return cl.includes('x-label') || cl.includes('y-label')
            || cl.includes('sector-label') || cl.includes('legend-label');
    }

    isWanted(target, elementList) {
        const cl = target.classList;
        let elements=[];
        elementList.forEach((elem)=> {
            const elArr = elem.split(' | ');
            elements=elements.concat(elArr)
        })
        return elements.some(el => {
            // if (el === 'label') {
            //     return this.hasLabel(target);
            // }
            // else {
                return cl.includes(el);
            // }
        });
    }

    shouldAppear(selectedTargets) {
        // console.log('selecttarget = ', selectedTargets);
        // general filter
        if (this.hardElement.length === 0 && this.softElement.length === 0) {
            return true;
        }
        // includes all hard elements
        if (!this.hardElement.every(el => {
            // extra lebel process
            const elArr = el.split(' | ');
            const len = elArr.length;
            // do not include OR element
            // console.log('ActiveElArr = ', elArr);
            if (len === 1) {
                // if (el === 'label') {
                //     return selectedTargets.some(t => this.hasLabel(t));
                // } else {
                    return selectedTargets.some(t => t.classList.includes(el));
                // }   
            } else {
                // len > 1
                // console.log('len > 1  | elArr = ', elArr);
                let isMeetOr = false;
                elArr.forEach((elem)=> {
                    // if (elem === 'label') {
                    //     isMeetOr ||= selectedTargets.some(t => this.hasLabel(t));
                    // } else {
                        isMeetOr ||= selectedTargets.some(t => t.classList.includes(elem));
                    // }  
                })
                // console.log('isMeetOr = ', isMeetOr);
                return isMeetOr;
            }   
        })) {
            // do not include all hardElement in this filter
            return false;
        }
        // else
        // return true;

        // has no other elements except soft elements
        return selectedTargets.every(el => {
            return this.isWanted(el, this.hardElement) || this.isWanted(el, this.softElement);
        });
    }
}