import {CanisAniTemplate} from "@/utils/entities-core/ani-template/CanisAniTemplate";
import {deepCopy} from "@/utils/common/copy";

const templateId = 'float'
const name = 'Float Template'
const tempLabels = ['??-level', 'enter']
const defaults = {
  "sorter": [],
  "position": "start after previous",
  "stagger": 0,
  "mode": "in",
  "offset": 0,
  "duration": 1.5,
  "labels": ["float"]
}

const canisTemplate = [
  {
    "selector": {
      "selector": {"$slot": "target"},
      "sort": {"$slot": "sorter"},
    },
    "grouping": {
      "groupBy": "id",
      "position": "start with previous",
      "stagger": {"$slot": "stagger"},
    },
    "effects": [
      {
        "type": "fade",
        "mode": {"$slot": "mode"},
        "duration": {"$slot": "duration"},
      },
      {
        "attribute": "y",
        "from": "+=20",
        "duration": {"$slot": "duration"},
      }
    ],
    "labels": {"$slot": "labels"},
    "offset": {"$slot": "offset"},
    "position": {"$slot": "position"},
  },
]

export class FloatTemplate extends CanisAniTemplate {
  constructor() {
    super(templateId, name, tempLabels, defaults, canisTemplate)
  }

  createInstance() {
    const configs = this.getMergedConfigs()
    if (configs.mode === 'out') {
      this.canisTemplate = deepCopy(this.canisTemplate)
      const effect = this.canisTemplate[0].effects[1]
      delete effect.from
      effect.to = "-=50"
    }
    return super.createInstance()
  }
}