import {AniAtom} from "../entities-core/AniAtom"
// eslint-disable-next-line
import {AniUnit} from "../entities-core/AniUnit"
// eslint-disable-next-line
import {Animation} from "../entities-core/Animation"
import {ConsGraph} from "./cons-graph/ConsGraph"
// eslint-disable-next-line
import {ConsNode, NodeType} from "./cons-graph/ConsNode"
import {deepCopy} from "@/utils/common/copy";
import {ArrayUtils} from "@/utils/common/ArrayUtils";
import * as d3 from "d3";

class GraphCreator {
  // input

  /** @type {Animation} */
  animation
  /** @type {d3.Selection} */
  container

  // output

  /** @type {AniAtom[]} */
  aniAtoms
  /** @type {ConsNode} */
  root
  /** @type {ConsGraph} */
  consGraph

  /** @type {Map<(number|string), (number|string)>} */
  atomId2nodeId

  /** @type {number} */
  globalAniAtomId = 0

  /**
   *
   * @param {Animation} animation
   * @param {d3.Selection} container
   */
  constructor(animation, container) {
    this.animation = animation
    this.container = container
    this.aniAtoms = []
    this.atomId2nodeId = new Map()
    this.consGraph = new ConsGraph()
    this.root = this.consGraph.addNode(NodeType.ROOT)
  }

  execute() {
    let unitNodes = []
    this.animation.aniUnits.forEach(au => {
      let unitNode = this.createAniUnitNode(au)
      unitNodes.push(unitNode)
      this.root.elGroup.push(...unitNode.elGroup)
      this.root.aniUnits.push(au)
    })
    this.consGraph.createParentChildRelation(this.root, unitNodes)
    return this
  }

  /**
   * @param {AniUnit} aniUnit
   * @returns {ConsNode} The aniUnit node
   */
  createAniUnitNode(aniUnit) {
    let sel = this.getElements(this.container, aniUnit.selector)
    let elGroup = sel.nodes()
    // console.log('Process', aniUnit);

    let unitNode = this.consGraph.addNode(NodeType.UNIT_NODE)
    unitNode.elGroup = elGroup
    unitNode.aniUnits.push(aniUnit)
    unitNode.constraint = aniUnit

    let childNodes = this.createChildNodes(aniUnit, elGroup, aniUnit.grouping)
    this.consGraph.createParentChildRelation(unitNode, childNodes)

    return unitNode
  }

  getElements(container, selector) {
    if (typeof selector === 'string') {
      return container.selectAll(selector)
    } else {
      // beta version

      let domObjs = container.selectAll(selector.selector).nodes()

      if (selector.sort !== undefined) {
        if (!(selector.sort instanceof Array)) {
          selector.sort = [selector.sort]
        }
        selector.sort.reverse().forEach(s => {
          if (s.attr) {
            domObjs.sort((n1, n2) => n1.getAttribute(s.attr) - n2.getAttribute(s.attr))
          } else if (s.data) {
            domObjs.sort((n1, n2) => {
              const index1 = s.order.indexOf(n1.__data__[s.data])
              const index2 = s.order.indexOf(n2.__data__[s.data])
              return index1 - index2
            })
          }
          if (s.order === 'dec') domObjs.reverse()
        })
      }
      let selection
      if (selector.index !== undefined) {
        selection = [domObjs[selector.index]]
      } else if (selector.range !== undefined) {
        selection = ArrayUtils.slice(domObjs, ...selector.range)
      } else if (selector.ratioRange !== undefined) {
        selector.ratioRange[0] *= domObjs.length
        selector.ratioRange[1] *= domObjs.length
        selection = ArrayUtils.slice(domObjs, ...selector.ratioRange)
      } else {
        selection = domObjs
      }

      return d3.selectAll(selection)
    }
  }

  /**
   * Create the child nodes of the ani-unit node or internal node
   * @param {AniUnit} aniUnit
   * @param {object[]} elGroup
   * @param {!object} grouping
   * @returns {ConsNode[]}
   */
   createChildNodes(aniUnit, elGroup, grouping) {
    // console.log('Create children', elGroup, grouping)
    if (elGroup.length === 0) {
      console.log('No element was selected');
      return []
    }

    let childNodes
    let newGroups = this.createNewGroups(elGroup, grouping)

    if (grouping.grouping) {
      childNodes = newGroups.map(newGrp =>
        this.createInternalNode(aniUnit, newGrp, grouping))
    } else {
      // last recursion. Here newGrp.length === 1
      childNodes = newGroups.map(newGrp =>
        this.createElNode(aniUnit, newGrp[0], grouping))
    }

    return childNodes
  }

  createNewGroups(elGroup, grouping) {
    let mapFunc
    if (grouping.groupByClass != null) {
      mapFunc = el => {
        let elClasses = el.getAttribute('class').split(' ')
        let reqClasses = grouping.groupByClass.split(' ')
        return reqClasses.every(req => elClasses.includes(req))
      }
    } else if (grouping.groupByIndex != null) {
      mapFunc = el => {
        let elClasses = el.getAttribute('class').split(' ')
        return elClasses.find(clz => clz.match(/index-\d*/) !== null)
      }
    } else if (grouping.groupByReg != null) {
      mapFunc = el => {
        let elClasses = el.getAttribute('class').split(' ')
        return elClasses.find(clz => clz.match(grouping.groupByReg) !== null)
      }
    } else {
      mapFunc = el => el[grouping.groupBy]
    }

    let groupMap = new Map()
    elGroup.forEach(el => {
      let key = mapFunc(el)
      if (groupMap.has(key)) {
        groupMap.get(key).push(el)
      } else {
        groupMap.set(key, [el])
      }
    })

    return Array.from(groupMap.values())
  }

  /**
   * Create a internal node and its children
   * @param {AniUnit} aniUnit
   * @param {object[]} elGroup
   * @param {object} grouping
   * @return {ConsNode} The internal node
   */
   createInternalNode(aniUnit, elGroup, grouping) {
    let internalNode = this.consGraph.addNode(NodeType.INTERNAL_NODE)
    internalNode.elGroup = elGroup
    internalNode.aniUnits.push(aniUnit)
    internalNode.constraint = deepCopy(grouping)

    let childNodes = this.createChildNodes(aniUnit, elGroup, grouping.grouping)
    this.consGraph.createParentChildRelation(internalNode, childNodes)

    return internalNode
  }

  /**
   * Create a element node and its ani-atom children
   * @param {AniUnit} aniUnit
   * @param {object} element
   * @param {object} grouping
   * @returns {ConsNode} The element node
   */
  createElNode(aniUnit, element, grouping) {
    let elNode = this.consGraph.addNode(NodeType.EL_NODE)
    elNode.elGroup.push(element)
    elNode.aniUnits.push(aniUnit)
    elNode.constraint = deepCopy(grouping)
    let atomNodes = []

    const context = {
      target: element,
      container: this.container
    }

    aniUnit.effects.forEach(effect => {
      let id = this.globalAniAtomId++
      let aniAtom = new AniAtom(id, context, effect, aniUnit.labels)
      this.aniAtoms.push(aniAtom)

      let atomNode = this.consGraph.addNode(NodeType.ATOM_NODE)
      atomNode.elGroup.push(element)
      atomNode.aniAtoms.push(aniAtom)
      atomNode.constraint.offset = effect.offset
      atomNode.constraint.duration = effect.duration
      this.atomId2nodeId.set(aniAtom.aniAtomId, atomNode.id)

      atomNodes.push(atomNode)
    })

    this.consGraph.createParentChildRelation(elNode, atomNodes)

    return elNode
  }

  // /**
  //  * @param {ConsNode} parent
  //  * @param {ConstNode} children
  //  */
  // createSiblingRelation(parent, children) {
  //   for (let i = 0; i < children.length - 1; ++i) {
  //     let c1 = children[0], c2 = children[1]
  //     parent.constraint
  //   }
  // }

  getResult() {
    return {
      aniAtoms: this.aniAtoms,
      atomId2nodeId: this.atomId2nodeId,
      root: this.root,
      consGraph: this.consGraph,
    }
  }
}

export {
  GraphCreator
}
