import {NativeTemplate} from "@/core/entities/spec/template/NativeTemplate";
import {ParamType} from "@/core/entities/spec/parameter/ParamType";
import {ParamDefinition} from "@/core/entities/spec/parameter/ParamDefinition";
import {TemplateUsage} from "@/core/entities/spec/animation/TemplateUsage";
import {ConcatAnimation} from "@/core/entities/spec/animation/ConcatAnimation";

export class ShowDynamicNumberTemplate extends NativeTemplate {
  constructor() {
    super();
    this.templateName = 'show-dynamic-number';
    this.parameters = [
      new ParamDefinition('start', ParamType.NUMBER, false, 0),
      new ParamDefinition('end', ParamType.NUMBER, false, 100),
      new ParamDefinition('formatter', ParamType.STRING, false, '@num'),
      new ParamDefinition('toFixed', ParamType.NUMBER, false, 0),
      new ParamDefinition('numChangeDuration', ParamType.NUMBER, false, 1.9),
      new ParamDefinition('FadeInDuration', ParamType.NUMBER, false, 0.1),
      new ParamDefinition('FadeOutDuration', ParamType.NUMBER, false, 1),
      new ParamDefinition('notExit', ParamType.BOOLEAN, false, false),
      new ParamDefinition('displayTime', ParamType.NUMBER, false, 3),
      new ParamDefinition('ease', ParamType.STRING, false, 'power3.out'),
      new ParamDefinition('stagger', ParamType.NUMBER, false, 0),
    ];
  }

  createAnimation() {
    const fadeIn = new TemplateUsage('Fade-in')
        .set('duration', this.getParamValue('FadeInDuration'));

    const dynamicNumber = new TemplateUsage('Dynamic-number');
    const params = {...this.getParamValues()};
    params['duration'] = params['numChangeDuration'];
    delete params['numChangeDuration'];
    delete params['FadeInDuration'];
    delete params['FadeOutDuration'];
    dynamicNumber.updateSettings(this.getParamValues());

    const concat = [fadeIn, dynamicNumber];

    if (!this.getParamValue('notExit')) {
      // exit
      const fadeOut = new TemplateUsage('Fade-out')
          .set('duration', this.getParamValue('FadeOutDuration'))
          .updateTiming({'offset': this.getParamValue('displayTime')});
      concat.push(fadeOut);
    }

    return new ConcatAnimation(concat);
  }
}
