// getters
const getters = {
}

// actions
const actions = {
  queryTTSAudioGoogle(context, {el}) {
    context.commit('changeAudioReady', false)
    let input = {}
    if (context.state.context.ssml) {
      input.ssml = context.state.context.ssml
    } else {
      input.text = context.state.context.text
    }
    const params = {
      "audioConfig": {
        "audioEncoding": "LINEAR16",
        "pitch": 0,
        "speakingRate": 0.8
      },
      "input": input,
      "voice": {
        "languageCode": "en-US",
        "name": "en-US-Wavenet-D"
      }
    }
    queryTTSAudioGoogle(context.state.url, params, resp => {
      console.log('audio rep', resp)
      const audioSrc = "data:audio/wav;base64," + resp.data.audioContent
      context.commit('setAudioSrc', audioSrc)
      const blob = new Blob([audioSrc], {type: "text/plain;charset=utf-8"})
      console.log(context.state.context)
      el.$saveAs(blob, (context.state.context.ctxName || 'tts') + '.txt')
      el.$message({
        message: "Generate audio successfully",
        type: 'success',
      })
    }, error => {
      console.error(error)
      el.$message({
        message: "Fail to generate audio",
        type: 'fail',
      })
    })
  },

  queryTTSAudioBaidu(context, {el, text}) {
    context.commit('changeAudioReady', false)
    const params = {
      lan: 'en',
      ie: 'UTF-8',
      spd: 1,
      text: text
    }
    queryTTSAudioBaidu(params, resp => {
      console.log('audio rep', resp)
      const audioSrc = "data:audio/mp3;base64," + resp.data
      context.commit('setAudioSrc', audioSrc)
      el.$message({
        message: "Generate audio successfully",
        type: 'success',
      })
    }, error => {
      console.error(error)
      el.$message({
        message: "Fail to generate audio",
        type: 'fail',
      })
    })
    // const audioSrc = "http://tts.baidu.com/text2audio?lan=en&ie=UTF-8&spd=1&text" + encodeURI(text)
    // context.commit('setAudioSrc', audioSrc)
  },

  queryStaticAudio({state, commit}, el) {
    commit('changeAudioReady', false)
    if (!state.context) {
      el.$notify({title: "Fail to get static audio.", message: "Context are not loaded", type: 'warning'})
      return
    }
    queryStaticAudio(state.context.ctxName + '.txt', resp => {
      // console.log(resp)
      commit('setAudioSrc', resp.data)
    }, errorResp => {
      el.$notify({title: "Fail to get static audio.", message: errorResp.message, type: 'error'})
      console.log(errorResp)
    })
  },

  initContext({commit, dispatch}, {/** @type Context */context, el}) {
    commit('changeContext', context)
    dispatch('queryStaticAudio', el)
    // dispatch('queryTTSAudioGoogle', {el, text: context.text})
    // dispatch('queryTTSAudioBaidu', {el, text: context.text})
  },
}

// mutations
const mutations = {
  compile(state, onUpdateFunc) {
    state.context.onUpdateFunc = onUpdateFunc   // ugly but anyway it works

    if (state.audioEl) {
      state.context.audioDuration = state.audioEl.duration
    }
    let prs = new Parser(state.context, {
      templateUsageText: state.templateUsageText,
      animationText: state.animationText,
      refinementsText: state.refinementEnable ? state.refinementsText : '[]',
      constraintsText: state.constraintEnable ? state.constraintsText : '[]',
      alignConstraintsText: state.alignConsEnable ? state.alignConstraintsText : '[]',
    })
    try {
      prs.execute()
    } catch (exception) {
      alert(exception)
      console.error(exception)
      return
    }

    let gen = new Generator(state.context)
    try {
      gen.execute()
    } catch (exception) {
      alert(exception)
      console.error(exception)
      return
    }

    state.tlUpdateSign ^= true
  },

  addTemplate(state, aniTemplate) {
    state.context.templateUsage.push({use: aniTemplate.id})
    state.context.addTemplate(aniTemplate)
    const aniJsonObj = state.context.getAnimationJsonObj()
    console.log('aniJsonObj', aniJsonObj)
    state.animationText = JSON.stringify(aniJsonObj, null, 2)
    state.templateUsageText = JSON.stringify(state.context.templateUsage, null, 2)
  },

  emptyAnimation(state) {
    state.context.templateUsage = []
    state.context.emptyAnimation()
    let aniJsonObj = state.context.getAnimationJsonObj()
    state.animationText = JSON.stringify(aniJsonObj, null, 2)
    state.templateUsageText = JSON.stringify(state.context.templateUsage, null, 2)
  },

  changeContext(state, /** @type {Context} */context) {
    state.context = context
    console.log('vuex ', context)
    let aniJsonObj = context.getAnimationJsonObj()
    state.templateUsageText = JSON.stringify(context.templateUsage, null, 2)
    state.animationText = JSON.stringify(aniJsonObj, null, 2)
    state.refinementsText = JSON.stringify(context.refinements, null, 2)
    state.constraintsText = JSON.stringify(context.constraints, null, 2)
    state.alignConstraintsText = JSON.stringify(context.alignConstraints, null, 2)
  },

  changeTemplateText(state, templateUsageText) {
    state.templateUsageText = templateUsageText
  },

  changeAnimationText(state, animationText) {
    state.animationText = animationText
    // console.log("animationText",animationText)
  },

  changeRefinementsText(state, refinementsText) {
    state.refinementsText = refinementsText
  },

  changeConstraintsText(state, constraintsText) {
    state.constraintsText = constraintsText
  },

  changeAlignConstraintsText(state, alignConstraintsText) {
    state.alignConstraintsText = alignConstraintsText
  },

  changeRefinementEnable(state, refinementEnable) {
    state.refinementEnable = refinementEnable
  },

  changeConstraintEnable(state, constraintEnable) {
    state.constraintEnable = constraintEnable
  },

  changeAlignConsEnable(state, alignConsEnable) {
    state.alignConsEnable = alignConsEnable
  },

  enableAllSettings(state) {
    state.refinementEnable = true
    state.constraintEnable = true
    state.alignConsEnable = true
  },

  changeUseAudio(state, useAudio) {
    state.useAudio = useAudio
  },

  changeRenderingSign(state) {
    state.renderingSign ^= true
  },

  registerAudioEl(state, audioEl) {
    state.audioEl = audioEl
  },

  setAudioSrc(state, audioSrc) {
    // may have bug here
    state.audioSrc = audioSrc
    console.log('load audio src')
    if (state.setAudioReadyFunc) {
      state.audioEl.removeEventListener("canplaythrough", state.setAudioReadyFunc, false)
    } else {
      state.setAudioReadyFunc = function() {
        state.audioReady = true
      }
    }
    state.audioReady = true
    state.audioEl.addEventListener("canplaythrough", state.setAudioReadyFunc, false);
  },

  changeAudioReady(state, isReady) {
    state.audioReady = isReady
  },
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
