import {Animation} from "@/utils/entities-core/Animation"
import {AnimationTemplate} from "@/utils/entities-core/AnimationTemplate"
import {TemplateType} from "@/utils/const/TemplateType";

const animationJSON = [
  {
    "selector": ".axis.domain,.axis.tick,.axis-label",
    "effects": [
      {
        "type": "fade"
      },
    ],
    "labels": ["enter"],
  },
  {
    "selector": ".axis.label",
    "effects": [
      {
        "type": "scale",
        "ease": "back.out",
      },
    ],
    "labels": ["enter"],
    "offset": 0.2,
  },
  {
    "selector": ".group.line-mark",
    "grouping": {
      "groupByReg": "series\\-\\d",
      "stagger": 1,
      "grouping": {
        "groupBy": "id",
        "stagger": 0.2,
      },
    },
    "effects": [
      {
        "type": "fade",
      },
    ],
    "labels": ["enter"],
    "position": "start after previous"
  },
  {
    "selector": ".mark.line, .legend-group",
    "grouping": {
      "groupByIndex": true,
      "stagger": 1,
    },
    "effects": [
      {
        "type": "wipe",
        "direction": "right",
      },
    ],
    "labels": ["enter"],
  },
]

const animationGen = function() {
  let jsonStr = JSON.stringify(animationJSON)
  return Animation.from(JSON.parse(jsonStr))
}

const lineChartEnter2 = new AnimationTemplate(
  'line-chart-basic-enter-2',
  'Line chart enter 2',
  animationGen,
  TemplateType.LINE_CHART,
  ['chart-level', 'enter']
)

export {
  lineChartEnter2
}
