import {Animation} from "@/utils/entities-core/Animation"
import {AnimationTemplate} from "@/utils/entities-core/AnimationTemplate"
import {TemplateType} from "@/utils/const/TemplateType";

const animationJSON = [
  {
    "selector": ".axis.domain,.axis.tick,.axis-label",
    "effects": [
      {
        "type": "fade",
        "mode": "in",
        "duration": 1.5
      },
    ],
    "labels": ["enter"],
    "position": "start after previous",
  },
  {
    "selector": ".axis.label",
    "effects": [
      {
        "type": "scale",
        "mode": "in",
        "ease": "back.out",
        "duration": 1
      },
    ],
    "labels": ["enter"],
    "position": "start with previous",
    "offset": 0.2,
  },
  {
    "selector": ".bar",
    "effects": [
      {
        "type": "grow",
        "direction": "up",
        "ease": "power1.inOut",
        "duration": 1.5
      },
    ],
    "labels": ["enter"],
    "position": "start after previous",
    "offset": -1,
  },
  {
    "selector": ".bar-label",
    "effects": [
      {
        "type": "fade",
        "mode": "in",
        "duration": 1
      },
    ],
    "labels": ["enter"],
    "position": "start after previous",
  }
]

const animationGen = function() {
  let jsonStr = JSON.stringify(animationJSON)
  return Animation.from(JSON.parse(jsonStr))
}

const barChartEnter2 = new AnimationTemplate(
  'bar chart-2',
  'Bar chart enter 2',
  animationGen,
  TemplateType.BAR_CHART,
  ['chart-level', 'enter']
)

export {
  barChartEnter2
}
