// eslint-disable-next-line
import { ConsEdge } from "./ConsEdge"
// eslint-disable-next-line
import { ConsNode, NodeType } from "./ConsNode"
// eslint-disable-next-line
import { Relation, reverseRelation } from "./Relation"
import {ArrayUtils} from "@/utils/common/ArrayUtils";

class ConsGraph {
  /** @type {ConsNode[]} */
  nodes
  /** @type {Map<(number|string), ConsNode>} */
  nodeMap
  /** @type {ConsEdge[]} */
  edges
  /** @type {Map<string, ConsEdge>} */
  edgeMap

  /** @type {number} */
  autoIncrId = 0

  constructor() {
    this.nodes = []
    this.nodeMap = new Map()
    this.edges = []
    this.edgeMap = new Map()
  }

  /**
   * Use auto-incr id strategy to create a node
   * @param {NodeType} type
   * @returns {ConsNode}
   */
  addNode(type) {
    let newNode = new ConsNode(this.autoIncrId++, type)
    this.nodes.push(newNode)
    this.nodeMap.set(newNode.id, newNode)
    return newNode
  }

  /**
   * Add two edges having opposite direction, using node id
   * @param {(number|string)} vId
   * @param {Relation} relation Relation of v->w
   * @param {(number|string)} wId
   * @returns {ConsEdge[]} The edge v->w and w->v
   */
  addEdgeBothSidesById(vId, relation, wId) {
    let v = this.nodeMap.get(vId)
    let w = this.nodeMap.get(wId)
    if (!v || !w) {
      console.error('Add ConsEdge failed, ' + v
        + ' or ' + wId + ' doesn\'t exist');
    }
    return this.addEdgeBothSides(v, relation, w)
  }

  /**
   * Add two edges having opposite direction
   * @param {ConsNode} v
   * @param {Relation} relation Relation of v -> w
   * @param {ConsNode} w
   * @returns {[ConsEdge, ConsEdge]} The edge v->w and w->v
   */
  addEdgeBothSides(v, relation, w) {
    let e1 = new ConsEdge(v, w, relation)
    let e2 = new ConsEdge(w, v, reverseRelation(relation))
    let ret = [e1, e2]
    ret.forEach(e => {
      this.edges.push(e)
      this.edgeMap.set(e.id, e)
      e.src.outEdges.push(e)
    })
    return ret
  }

  /**
   * @param {ConsNode} parent
   * @param {ConsNode} child
   * @param {number} [index]
   */
  addParentChildEdge(parent, child, index=-1) {
    let e1 = new ConsEdge(parent, child, Relation.CHILD)
    let e2 = new ConsEdge(child, parent, reverseRelation(Relation.CHILD))
    this.edges.push(e1, e2)
    this.edgeMap.set(e1.id, e1)
    this.edgeMap.set(e2.id, e2)
    if (index !== -1) {
      parent.outEdges.splice(index, 0, e1)
    } else {
      parent.outEdges.push(e1)
    }
    child.outEdges.push(e2)
  }

  /**
   * @param {ConsNode} subRoot
   */
  removeSubTree(subRoot) {
    if (subRoot.type === NodeType.ROOT) {
      return
    }

    // remove aniAtoms in parent node
    let parent = subRoot.getParentNode()
    subRoot.aniAtoms.forEach(ani => ArrayUtils.removeItem(parent.aniAtoms, ani))

    this.removeEdgeBothSides(subRoot.getOutEdges(Relation.PARENT)[0])

    let last = subRoot.getLastSiblingNode()
    let next = subRoot.getNextSiblingNode()
    if (last !== null) {
      this.removeEdgeBothSides(subRoot.getOutEdges(Relation.LAST_SIBLING)[0])
    }
    if (next !== null) {
      this.removeEdgeBothSides(subRoot.getOutEdges(Relation.NEXT_SIBLING)[0])
    }
    if (last !== null && next !== null) {
      this.addEdgeBothSides(last, Relation.NEXT_SIBLING, next)
    }
  }

  /**
   * @param {ConsNode} subRoot
   * @param {ConsNode} parent
   * @param {number} index
   */
  mountSubTree(subRoot, parent, index) {
    if (subRoot.type === NodeType.ROOT) {
      return
    }
    let children = parent.getChildNodes()
    index = Math.max(0, index)
    index = Math.min(children.length, index)

    this.addParentChildEdge(parent, subRoot, index)
    if (children.length === 0) {
      return
    }
    if (index === children.length) {
      this.addEdgeBothSides(ArrayUtils.getLastItem(children), Relation.NEXT_SIBLING, subRoot)
    } else if (index === 0) {
      this.addEdgeBothSides(subRoot, Relation.NEXT_SIBLING, children[0])
    } else {
      let last = children[index - 1], next = children[index]
      this.removeEdgeBothSides(last.getOutEdges(Relation.NEXT_SIBLING)[0])
      this.addEdgeBothSides(last, Relation.NEXT_SIBLING, subRoot)
      this.addEdgeBothSides(subRoot, Relation.NEXT_SIBLING, next)
    }
  }

  /**
   * @param {ConsEdge} edge
   */
  removeEdgeBothSides(edge) {
    let edgeRev = this.edgeMap.get(ConsEdge.getId(edge.dst, edge.src))
    ArrayUtils.removeItem(this.edges, edge)
    ArrayUtils.removeItem(this.edges, edgeRev)
    this.edgeMap.delete(edge.id)
    this.edgeMap.delete(edgeRev.id)

    ArrayUtils.removeItem(edge.src.outEdges, edge)
    ArrayUtils.removeItem(edgeRev.src.outEdges, edgeRev)
  }

  /**
   * @param {ConsEdge} edge
   * @return {[ConsNode, ConsNode]}
   */
  getParentChildByEdge(edge) {
    if (edge.relation === Relation.CHILD) {
      return [edge.src, edge.dst]
    } else if (edge.relation === Relation.PARENT){
      return [edge.dst, edge.src]
    } else {
      return [null, null]
    }
  }

  /**
   * @param {ConsNode} parent
   * @param {ConsNode[]} children
   */
  createParentChildRelation(parent, children) {
    children.forEach(child => {
      this.addParentChildEdge(parent, child)
      parent.aniAtoms.push(...child.aniAtoms)
    })
    for (let i = 0; i < children.length - 1; ++i) {
      let c1 = children[i], c2 = children[i + 1]
      this.addEdgeBothSides(c1, Relation.NEXT_SIBLING, c2)
    }
  }

  /**
   * @param {(number|string)} nodeId
   */
  getNodeById(nodeId) {
    return this.nodeMap.get(nodeId)
  }

  /**
   * @param {ConsNode} node
   * @param {string} word
   * @param {number} timestamp
   */
  addAlignMark(node, word, timestamp) {
    node.alignCons = {word, timestamp}
  }
}

export {
  ConsGraph
}
