import {NormalTarget, Target} from "@/core/entities/spec/Target";
import {TransformParser} from "@/core/parser/TransformParser";

export class TargetParser {
  static reviver(key, value) {
    return key === "" ? TargetParser.fromJSON(value) : value;
  }

  /**
   * @param {Object} json
   * @returns {Target}
   */
  static fromJSON(json) {
    if (json == null || json === '') {
      return Target.getDefaultTarget();
    }
    if (typeof json === 'string') {
      return new NormalTarget(json);
    }
    if (typeof json !== 'object') {
      return Target.getDefaultTarget();
    }
    if ('selector' in json ||
        ('transform' in json && Object.keys(json).length === 1)) {
      // NormalTarget
      const transform = TransformParser.fromObjectOrArray(json.transform);
      return Object.assign(Target.getDefaultTarget(), json, {transform});
    } else {
      // AnonymousTarget
      throw 'Target type is not supported';
    }
  }
}