import {CanisAniTemplate} from "@/utils/entities-core/ani-template/CanisAniTemplate";

const templateId = 'zoom'
const name = 'Zoom Template'
const tempLabels = ['??-level', 'enter']
const defaults = {
  "offset": 0,
  "position": "start after previous",
  "scaleFactor": 1.3,
  "transformOrigin": "50% 50%",
  "duration": 1,
  "labels": ["zoom"],
}

const canisTemplate = [
  {
    "selector": ".chart",
    "effects": [
      {
        "attribute": "scale",
        "to": {"$slot": "scaleFactor"},
        "duration": {"$slot": "duration"},
        "transformOrigin": {"$slot": "transformOrigin"}
      },
    ],
    "labels": {"$slot": "labels"},
    "offset": {"$slot": "offset"},
    "position": {"$slot": "position"},
  },
]

export class ZoomTemplate extends CanisAniTemplate {
  constructor() {
    super(templateId, name, tempLabels, defaults, canisTemplate)
  }
}