import {TemplateType} from "@/utils/const/TemplateType";

class AnimationTemplate {
  /** @type {(number|string)} */
  id
  /** @type {string} */
  name
  /** @type {TemplateType} */
  type
  /** @type {string[]} */
  tempLabels

  /**
   * @function animationGen
   * @return {Animation}
   */
  /** @type {animationGen} */
  animationGen

  constructor(id, name, animationGen, type=TemplateType.UNKNOWN, tempLabels=[]) {
    this.id = id
    this.name = name
    this.animationGen = animationGen
    this.type = type
    this.tempLabels = tempLabels

    if (!tempLabels.includes(type)) {
      tempLabels.unshift(type)
    }
  }
}

export {
  AnimationTemplate
}
