import {deepCopy} from "@/utils/common/copy";

class Effect {
  /** @type {(number|string)} */
  effectId
  /** @type {string} */
  type
  /** @type {object} */
  defaults

  /** @type {string[]} */
  effLabels

  /** @type {object}*/
  config

  /** @type {object}*/
  custom

  /**
   * @callback effectGenFunc
   * @param {object} target
   * @param {object} config
   * @return {EffectGsap}
   */
  /** @type {effectGenFunc} */
  genFunc

  /** @type {number} */
  offset
  /** @type {number} */
  duration

  /**
   * @constructor
   * @param {(number|string)} effectId
   * @param {string} type
   * @param {effectGenFunc} genFunc
   * @param {number} duration
   * @param {number} offset
   * @param {object} [defaults={}]
   * @param {string[]} [effLabels=[]]
   */
  constructor(effectId, type, genFunc,
              duration, offset, defaults = {},
              effLabels = []) {
    this.effectId = effectId
    this.type = type
    this.genFunc = genFunc
    this.duration = duration
    this.offset = offset || 0
    this.defaults = defaults
    this.effLabels = effLabels
  }

  /**
   * @param {object} context
   * @returns {EffectGsap}
   */
  generateEffectGsap(context) {
    let effectGsap = this.genFunc(context, this.getMergedConfig())
    if (!effectGsap.valid) {
      console.error("EffectGsap is not valid", this, effectGsap)
      return null
    }
    return effectGsap
  }

  /**
   * @returns {object}
   */
  getMergedConfig() {
    let config = deepCopy(this.config) || {}
    Object.keys(this.defaults).forEach(k => {
      if (config[k] === undefined) {
        config[k] = this.defaults[k]
      }
    })
    return config
  }

  /**
   * @param {object} context
   * @param duration
   * @returns {gsap.Tween}
   */
  generateTween(context, duration=null) {
    let effectGsap = this.generateEffectGsap(context)
    return effectGsap.getTween(context.target,
      duration != null ? duration : this.duration)
  }

  // /**
  //  * @returns {Effect}
  //  */
  // copy() {
  //   let copy = new Effect(computeUUID(), this.type, this.genFunc)
  //   let excludeItems = ['effectId', 'genFunc']
  //   Object.keys(this).forEach(k => {
  //     if (excludeItems.includes(k)) return
  //     copy[k] = deepCopy(this[k])
  //   })
  //   return copy
  // }

  toJsonObj() {
    if (this.type == null) {
      return this.custom
    }
    let result = {}
    let excludeItems = ['effectId', 'genFunc', 'defaults', 'config', 'custom']
    Object.keys(this).forEach(k => {
      if (excludeItems.includes(k)) return
      result[k] = this[k]
    })
    if (this.config != null) {
      result = {...result, ...this.config}
    }
    return result
  }
}

export {
  Effect
}
