import {TemplateUsage} from "@/utils/entities/TemplateUsage";

/**
 * @param {{}} rawObject
 * @returns {TemplateUsage}
 */
function from(rawObject) {
  const result = new TemplateUsage()
  result.use = rawObject.use
  result.configs = rawObject.configs
  return result
}

/**
 * @param {TemplateUsage} object
 * @returns {{}}
 */
function jsonify(object) {
  const result = {}
  Object.keys(object).forEach(k => result[k] = object[k])
  return result
}

export const TemplateUsageParser = {from, jsonify}