function formatInsightsFromGPT(JSONFromGPTSrc, spec) {
    const JSONFromGPT = JSON.parse(JSON.stringify(JSONFromGPTSrc))
    let formatInsights = []
    const description = JSONFromGPT['d1'] + ' ' + JSONFromGPT['d2'] + ' ' + JSONFromGPT['insight1']['insightSentence'] + ' ' + JSONFromGPT['insight2']['insightSentence'] + ' ' + JSONFromGPT['insight3']['insightSentence']
    console.log(description)

    let currentPos = 0
    const titleDesc = JSONFromGPT['d1'] + ' '
    // console.log(titleDesc.length)
    // title
    formatInsights.push({
        insightType: 'title',
        subType: 'title',
        insightWords: titleDesc,
        pos: [currentPos, currentPos + titleDesc.length],
        insightData: []
    })
    currentPos += titleDesc.length

    // axis
    const axisDesc = JSONFromGPT['d2'] + ' '
    if(spec.type === 'pie'){
        formatInsights.push({
            insightType: 'axis',
            subType: 'arc',
            insightWords: axisDesc,
            pos: [currentPos, currentPos + axisDesc.length],
            insightData: []
        })
        currentPos += axisDesc.length
    }
    else {
        // spilt y axis
        let axis = axisDesc.split('y-axis')
        let xAxisDesc = axis[0]

        // split legend
        let legend = axis[1].split('legend')
        let legendDesc = "legend" + legend[1]
        if(legend.length === 1){
            legend = axis[1].split('color')
            legendDesc = "color" + legend[1]
        }
        let yAxisDesc = 'y-axis' + legend[0]
        // console.log(JSONFromGPT['description2'], axis, yAxisDesc)

        // x-axis
        formatInsights.push({
            insightType: 'axis',
            subType: 'x',
            insightWords: axis[0],
            pos: [currentPos, currentPos + xAxisDesc.length],
            insightData: []
        })
        currentPos += xAxisDesc.length
        // y-axis
        if(spec.row > 1){
            formatInsights.push({
                insightType: 'axis',
                subType: 'y',
                insightWords: description.slice(currentPos, currentPos  + yAxisDesc.length),
                pos: [currentPos, currentPos + yAxisDesc.length],
                insightData: []
            })
            currentPos += yAxisDesc.length
        }
        // legend
        formatInsights.push({
            insightType: 'axis',
            subType: 'legend',
            insightWords: description.slice(currentPos, currentPos + legendDesc.length),
            pos: [currentPos, currentPos + legendDesc.length],
            insightData: []
        })
        currentPos += legendDesc.length


        // const showPos = parseInt(yAxisDesc.length/2)
        // console.log(showPos)
        // formatInsights.push({
        //     insightType: 'axis',
        //     subType: 'y',
        //     insightWords: description.slice(currentPos, currentPos + showPos),
        //     pos: [currentPos, currentPos + showPos],
        //     insightData: []
        // })
        // // show line/bar/pie
        // formatInsights.push({
        //     insightType: 'show',
        //     subType: 'show',
        //     insightWords: description.slice(currentPos + showPos, currentPos+yAxisDesc.length),
        //     pos: [currentPos + showPos, currentPos+yAxisDesc.length],
        //     insightData: []
        // })
        // currentPos += yAxisDesc.length
    }


    let sortedInsights = [JSONFromGPT['insight1'], JSONFromGPT['insight2'], JSONFromGPT['insight3']]
    // sortedInsights.sort(
    //     (a, b) => {
    //         return isInsightExistInDescription(description, a, false) - isInsightExistInDescription(description, b, false)
    //     })
    // console.log(sortedInsights)

    // insights
    sortedInsights.forEach((insight) => {
        insight['subInsights'].forEach((subInsight) => {
            let words = subInsight['phrases']
            let insightPos = isInsightExistInDescription(description, subInsight, true, currentPos)
            if(insightPos === -1){
                return
            }
            if (currentPos < insightPos) {
                formatInsights.push({
                    insightType: "none",
                    subType: 'none',
                    insightWords: description.slice(currentPos, insightPos),
                    pos: [currentPos, insightPos],
                    insightData: [],
                    value: ""
                })
            }
            formatInsights.push({
                insightType: insight['insightType'],
                subType: subInsight['type'],
                pos: [insightPos, insightPos + words.length],
                insightWords: description.slice(insightPos, insightPos + words.length),
                insightData: csv2JSON(subInsight['data'], spec),
                value: subInsight['value'] ? subInsight['value'] : ""
            })
            currentPos = insightPos + words.length
        })

    });

    if (currentPos < description.length) {
        formatInsights.push({
            insightType: "none",
            subType: 'none',
            insightWords: description.slice(currentPos, description.length),
            pos: [currentPos, description.length],
            insightData: []
        })
    }
    console.log(formatInsights)
    return formatInsights
}

function csv2JSON(data, spec) {
    let res = []
    console.log(data)
    for (const d of data) {
        let values = d.split(", ");
        if(values.length === 1) {
            values = d.split(",");
        }
        // console.log(values)
        let result = {};
        if(spec.type === 'pie'){
            console.log(values)
            result[spec.y] = parseFloat(values[1])
            result[spec.legend] = values[0]
            result["idx"] = spec.data.findIndex(p => {
                // console.log(parseFloat(p[spec.y]) === parseFloat(values[0]) && p[spec.legend] === values[1])
                return parseFloat(p[spec.y]) === parseFloat(values[1]) && p[spec.legend] === values[0]})
        }
        else if(values.length ===2){
            // console.log(values)
            result[spec.x] = values[0]
            result[spec.y] = parseFloat(values[1])
            result["idx"] = spec.data.findIndex(p => {
                // console.log(parseFloat(p[spec.y]) === parseFloat(values[1]) && p[spec.x] === values[0])
                return parseFloat(p[spec.y]) === parseFloat(values[1]) && p[spec.x] === values[0]})
        }
        else {
            result[spec.x] = values[0]
            result[spec.y] = parseFloat(values[1])
            result[spec.legend] = values[2]
            result["idx"] = spec.data.findIndex(p => {
                // console.log(p[spec.x] === values[0] && parseFloat(p[spec.y]) === parseFloat(values[1]) && p[spec.legend] === values[2])
                return p[spec.x] === values[0] && parseFloat(p[spec.y]) === parseFloat(values[1]) && p[spec.legend] === values[2]})
        }
        res.push(result)
    }
    console.log(res)
    return res; //JSON
}

function isInsightExistInDescription(description, insight, isSub, currentPos) {
    let pos = -1
    if(isSub) {
        pos = description.toLowerCase().indexOf(insight['phrases'].toLowerCase(), currentPos)
    }else {
        console.log(insight['subInsights'][0]['phrases'].toLowerCase())
        pos = description.toLowerCase().indexOf(insight['subInsights'][0]['phrases'].toLowerCase())
    }
    // console.log(pos)
    return pos
}

function getsSVGSpec(svgStr, chartType) {
    const svg = (new DOMParser())
        .parseFromString(svgStr, 'image/svg+xml')
        .querySelector('svg');
    let all_data = []
    let title = ''
    const domElList = Array.from(svg.querySelectorAll('*:not(g,svg,defs,defs *)')).filter(domEl => domEl.ariaHidden != "true");
    // console.log(domElList)
    let maxDataLen = 0
    domElList.forEach(domEl => {
        if (domEl.attributes.getNamedItem('ac-data') !== null) {
            const cur_data = JSON.parse(domEl.attributes.getNamedItem('ac-data').value.replace(/(&quot;)/g, "\""))
            // console.log(cur_data.length)
            all_data.push(cur_data)
            maxDataLen = Math.max(Object.keys(cur_data).length, maxDataLen)
        }
        // console.log(domEl.classList.contains('chart_title'))
        else if (domEl.classList.contains('title')) {
            title = domEl.textContent
        }
    })
    console.log(maxDataLen)

    // filter other data (e.g., line)
    const filteredData = all_data.filter(item => Object.keys(item).length === maxDataLen);

    let spec = {}
    spec['data'] = filteredData
    spec['title'] = title
    spec['type'] = chartType
    let legend = []
    if(chartType === 'pie') {
        spec['y'] = Object.keys(filteredData[0])[0]
        spec['legend'] = Object.keys(filteredData[0])[1]
    }
    else if(title.includes('Deaths')){
        spec['x'] = Object.keys(filteredData[0])[0]
        spec['y'] = Object.keys(filteredData[0])[1]
        legend = []
    }
    else {
        spec['x'] = Object.keys(filteredData[0])[0]
        spec['y'] = Object.keys(filteredData[0])[1]
        spec['legend'] = Object.keys(filteredData[0])[2]

        for (const d of filteredData) {
            let curLegend = d[spec.legend]
            if (!legend.includes(curLegend)) {
                legend.push(curLegend)
            }
        }
    }



    spec['column'] = filteredData.length / legend.length
    spec['row'] = legend.length
    console.log(spec);
    return spec
}

export const InsightUtils = {
    formatInsightsFromGPT,
    // generateAnimationJSONfromInsights,
    getsSVGSpec
}