import {ParamDefinition} from "@/core/entities/spec/parameter/ParamDefinition";
import JSON5 from 'json5';

export class ParamDefinitionParser {
  static reviver(key, value) {
    return key === "" ? ParamDefinitionParser.fromJSON(value) : value;
  }

  /**
   * @param {Object} json
   * @returns {ParamDefinition}
   */
  static fromJSON(json) {
    if (typeof json === 'string') {
      return JSON5.parse(json, ParamDefinitionParser.reviver);
    } else {
      const obj = new ParamDefinition();
      return Object.assign(obj, json);
    }
  }
}