export class ChartLayout {
  width
  height
  topRatio
  bottomRatio
  leftRatio
  rightRatio

  origin
  top
  bottom
  left
  right
  chartHeight
  chartWidth

  constructor({width, height,
                topRatio, bottomRatio,leftRatio, rightRatio}) {
    this.width = width
    this.height = height
    this.topRatio = topRatio
    this.bottomRatio = bottomRatio
    this.leftRatio = leftRatio
    this.rightRatio = rightRatio
    this.initLayout()
  }

  initLayout() {
    this.origin = {
      x: this.width * this.leftRatio,
      y: this.height * (1 - this.bottomRatio)
    }
    this.top = this.height * this.topRatio
    this.bottom = this.height * this.bottomRatio
    this.left = this.width * this.leftRatio
    this.right = this.width * this.rightRatio

    this.chartHeight = this.height - this.top - this.bottom
    this.chartWidth = this.width - this.left - this.right
  }
}
