import { computeUUID } from "../common/uuid"
import { Effect } from "../entities-core/Effect"
import { effectFactory } from "../entities-core/EffectFactory"
import { EffectGsap } from "../entities-core/EffectGsap"
import {Position} from "../const/Position"
import {deepCopy} from "@/utils/common/copy";

const defaultGrouping = {
  groupBy: 'id',
  position: Position.WITH,
  offset: 0,
}

/**
 * @typedef EffectCustom
 * @property {string} attribute
 * @property {number} from
 * @property {number} to
 * @property {?string} ease
 * @property {number} duration
 * @property {?number} offset
 * @property {?string[]} effLabels
 */
/**
 * @typedef EffectSpec
 * @property {string} type
 * @property {?number} duration
 * @property {?number} offset
 * @property {?string[]} effLabels
 */
/**
 * @param {(EffectCustom|EffectSpec)} rawObj
 * @returns {Effect}
 */
export function createEffectFrom(rawObj) {
  try {
    rawObj = deepCopy(rawObj)
    if (rawObj.attribute) {
      // EffectCustom
      let from = null, to = null
      if (rawObj.from !== undefined) {
        from = {}
        from[rawObj.attribute] = rawObj.from
      }
      if (rawObj.to !== undefined) {
        to = {}
        to[rawObj.attribute] = rawObj.to
      }

      let toOrFrom = (to !== null) ? to : from
      if (rawObj.ease) toOrFrom.ease = rawObj.ease
      if (rawObj.svgOrigin) toOrFrom.svgOrigin = rawObj.svgOrigin
      if (rawObj.transformOrigin) toOrFrom.transformOrigin = rawObj.transformOrigin
      if (rawObj.yoyo) {
        toOrFrom.yoyo = rawObj.yoyo
        toOrFrom.repeatDelay = rawObj.yoyoDelay || 0
        toOrFrom.repeat = 1
        rawObj.duration /= 2
      }
      console.log('custom', from, to)
      let genFunc = () => new EffectGsap({from, to})

      let effect = new Effect(computeUUID(), rawObj.type, genFunc,
        rawObj.duration, rawObj.offset, {}, rawObj.effLabels || [])
      effect.custom = rawObj

      return effect

    } else {
      // EffectSpec
      let effect = effectFactory.getEffect(rawObj.type)
      if (!effect) {
        console.error('No such effect: ' + rawObj.type)
      }

      if (rawObj.duration) effect.duration = rawObj.duration
      if (rawObj.offset) effect.offset = rawObj.offset
      if (rawObj.effLabels) effect.effLabels = rawObj.effLabels

      delete rawObj.type
      delete rawObj.duration
      delete rawObj.offset
      delete rawObj.effLabels
      effect.config = rawObj

      return effect
    }

  } catch (exception) {
    console.error(exception)
  }
}

export function fixEffects(effects) {
  return effects.map(e => {
    if (e instanceof Effect) return e
    return createEffectFrom(e)
  })
}

export function fixGroupingSettings(grouping) {
  if (!grouping) {
    return deepCopy(defaultGrouping)
  }
  let nestedGroup = grouping
  while (nestedGroup.grouping) {
    nestedGroup = nestedGroup.grouping
  }
  if (nestedGroup.groupBy !== 'id') {
    nestedGroup.grouping = deepCopy(defaultGrouping)
  }
  return grouping
}
