export function queryTTSAudioBaidu(params, callback, errorCallBack){
  // let url = 'http://tts.baidu.com/text2audio?lan=en&ie=UTF-8&spd=2&text="' + text + '"'
  let url = 'http://tts.baidu.com/text2audio'
  axios.get(url, {params: params})
    .then(rep => {
      callback(rep)
    }).catch(error => {
    errorCallBack(error)
  })
}

export function queryStaticAudio(fileName, callback, errorCallBack){
  let url = '../../data/audio/' + fileName
  axios.get(url)
      .then(rep => {
        callback(rep)
      }).catch(error => {
    errorCallBack(error)
  })
}
