import JSON5 from 'json5';

/**
 * @typedef Target
 * @type {NormalTarget | AnonymousTarget}
 */

export class Target {
  /** @type {Object | Parameter | (Object | Parameter)[]} */
  transform

  static getDefaultTarget() {
    return new NormalTarget();
  }

  static simplifyJSON(json) {
    if (typeof json !== 'object') {
      return json;
    }
    const result = {};
    // convert to normal form
    if ('selector' in json && json.selector !== '*' && json.selector.trim() !== '') {
      result.selector = json.selector;
    }
    if ('transform' in json) {
      let trans = json.transform;
      if (typeof trans === 'string' && trans.trim() !== '') {
        trans = JSON5.parse(trans);
      }
      if (typeof trans === 'object' && !Array.isArray(trans)) {
        result.transform = trans;
      }
      if (Array.isArray(trans) && trans.length !== 0) {
        for (let i = 0; i < trans.length; i++) {
          if (typeof trans[i] === 'string') {
            trans[i] = JSON5.parse(trans[i]);
          }
        }
        result.transform = trans;
      }
    }
    // simplify
    if (Object.keys(result).length === 0) {
      return undefined;   // use default
    }
    if (Object.keys(result).length === 1 && 'selector' in result) {
      return result.selector;
    }
    return result;
  }
}

export class AnonymousTarget {
  /** @type {string} */
  originalSelector
  /** @type {string | string[]} */
  subTags
}

export class NormalTarget extends Target {
  /** @type {string} */
  selector

  constructor(selector='*', transform=[]) {
    super();
    this.selector = selector;
    this.transform = transform;
  }
}

/**
 * @type TargetDefinition
 * @type {TargetCreation | TargetRedefinition | TargetRemoval}
 */

export class TargetCreation {
  /** @type {'new'} */
  operation
  /** @type {string} */
  tagName
  /** @type {string} */
  selector
  /** @type {string | Target[]} */
  subTags
}

export class TargetRedefinition {
}

export class TargetRemoval {
}
