import {BaseTransform} from "@/core/entities/spec/BaseTransform";

export class FilterTransform extends BaseTransform {
  /** @type {string} */
  type
  /** @type {string} */
  expr

  constructor() {
    super();
    this.type = 'filter';
    this.expr = 'true';
  }

  /**
   * @param {d3.Selection} selection
   * @returns {d3.Selection}
   */
  execute(selection) {
    const expr = this.expr
        .replaceAll('@classList', '__node__.classList')
        .replaceAll('@id', '__node__.getAttribute("id")')
        .replaceAll('@index', '__index__')
        .replaceAll('@datum', '(JSON.parse(__node__.getAttribute("ac-data")) ?? {})')
        .replaceAll('@el', '__node__');
    // console.log('filter expr', expr)
    const nodes = selection.nodes();
    // eslint-disable-next-line no-unused-vars
    const keepArray = nodes.map((__node__, __index__) => {
      // console.log(__node__, eval(expr));
      return eval(expr);
    })
    return selection.filter((d, i) => keepArray[i]);
  }
}

