import {PreviewWrapper} from "@/core/previewer/PreviewWrapper";
import {RenderContext} from "@/core/entities/context/RenderContext";
import {SimpleTargetModel} from "@/core/generator/target-model/SimpleTargetModel";

export class PreviewerManager {
  /** @type {TemplateLibrary} */
  templateLib
  /**
   * Mapping the template name to its preview wrapper (if exit)
   * @type {Map<String, PreviewWrapper>}
   */
  previewWrapperMap
  /**
   * Mapping previewer type to a corresponding prototype
   * @type {Map<String, BasePreviewer>}
   */
  previewerMap

  /**
   * @param {TemplateLibrary} templateLib
   */
  constructor(templateLib) {
    this.templateLib = templateLib;
    this.previewWrapperMap = new Map();
    this.previewerMap = new Map();
  }

  /**
   * @param {PreviewWrapper} wrapper
   */
  registerPreviewWrapper(wrapper) {
    this.previewWrapperMap.set(wrapper.template.templateName, wrapper);
  }

  /**
   * @param {BasePreviewer} previewer
   */
  registerPreviewer(previewer) {
    this.previewerMap.set(previewer.previewerType, previewer);
  }

  /**
   * @param {string} templateName
   * @param {d3.Selection} rootSel
   * @returns {BasePreviewer}
   */
  createDefaultPreviewer(templateName, rootSel) {
    const wrapper = this.getDefaultWrapper(templateName);

    const renderContext = new RenderContext(rootSel, this.templateLib);
    renderContext.targetModel = new SimpleTargetModel(renderContext.globalSelection);

    const previewer = this.previewerMap.get(wrapper.previewerType).clone();
    previewer.init(wrapper, renderContext);

    return previewer;
  }

  /**
   * @param {string} templateName
   * @returns {PreviewWrapper}
   */
  getDefaultWrapper(templateName) {
    let wrapper = this.previewWrapperMap.get(templateName)?.clone();
    if (!wrapper) {
      const template = this.templateLib.getTemplate(templateName);
      wrapper = new PreviewWrapper(template);
    }
    return wrapper;
  }
}