import {Compiler} from "@/core/compiler/Compiler";
import {buildInWrappers} from "@/core/previewer/build-in-wrapper";
import {buildInPreviewers} from "@/core/previewer/template-previewer";
import {buildInFilters} from "@/core/filters";
import gsap from 'gsap';
import {TemplateParser} from "@/core/parser/TemplateParser";

gsap.defaults({
  immediateRender: false,
})

const compiler = new Compiler();

/**
 * @param {Element | string} elementOrSelector
 * @param {Object} json
 * @param {Object<string, any>} vars
 * @returns {gsap.core.Timeline}
 */
function render(elementOrSelector, json, vars) {
  return compiler.compile(elementOrSelector, json, vars);
}

/**
 * @returns {PreviewerManager}
 */
function getPreviewerManager() {
  return compiler.previewerManager;
}

/**
 * @param {string} templateName
 * @returns {BaseTemplate}
 */
function getTemplate(templateName) {
  return compiler.templateLib.getTemplate(templateName);
}

/**
 * Reset the custom templates in lib
 */
function resetTemplateLib() {
  compiler.templateLib.reset();
}

/**
 * @param {...Object} templateJSON
 */
function registerTemplates(...templateJSON) {
  templateJSON.forEach(json => {
    const template = TemplateParser.fromJSON(json);
    compiler.templateLib.registerTemplates(template);
  });
}

/**
 * @param {string} templateName
 * @returns {string}
 */
function getTemplateClass(templateName) {
  const wrapper = getPreviewerManager().getDefaultWrapper(templateName);
  if (wrapper) {
    return wrapper.templateLabels[0];
  } else {
    return 'custom';
  }
}

/**
 * @returns {{templateName: string, isBuildIn: boolean}[]}
 */
function getTemplateInfoList() {
  const templates = [];
  compiler.templateLib.getAllBuildInTemplates().forEach(template => {
    templates.push({templateName: template.templateName, isBuildIn: true});
  });
  compiler.templateLib.getAllCustomTemplate().forEach(template => {
    templates.push({templateName: template.templateName, isBuildIn: false});
  });
  return templates;
}

/**
 * @param {string | HTMLElement} elementOrSelector
 * @param {string | Object} animationJSON
 * @param {Object?} variable
 * @returns {number}
 */
function computeDuration(elementOrSelector, animationJSON, variable) {
  return compiler.computeDuration(elementOrSelector, animationJSON, variable);
}

/**
 * @param {string | HTMLElement} elementOrSelector
 * @param {string} templateName
 * @returns {Set<Element>}
 */
function computePossibleElements(elementOrSelector, templateName) {
  return compiler.computePossibleElements(elementOrSelector, templateName);
}

export const aniChart = {
  compiler,
  buildInWrappers,
  buildInPreviewers,
  buildInFilters,
  render,
  getPreviewerManager,
  getTemplate,
  getTemplateInfoList,
  resetTemplateLib,
  registerTemplates,
  getTemplateClass,
  computeDuration,
  computePossibleElements,
};
