import {NativeTemplate} from "@/core/entities/spec/template/NativeTemplate";
import {ParamType} from "@/core/entities/spec/parameter/ParamType";
import {ParamDefinition} from "@/core/entities/spec/parameter/ParamDefinition";
import {NativeAnimation} from "@/core/entities/spec/animation/NativeAnimation";
import gsap from "gsap";

export class DynamicNumberTemplate extends NativeTemplate {
  constructor() {
    super();
    this.templateName = 'Dynamic-number';
    this.parameters = [
      new ParamDefinition('start', ParamType.NUMBER, false, 0),
      new ParamDefinition('end', ParamType.NUMBER, false, '@num'),
      new ParamDefinition('formatter', ParamType.STRING, false, 'auto'),
      new ParamDefinition('toFixed', ParamType.NUMBER, false, 0),
      new ParamDefinition('duration', ParamType.NUMBER, false, 2),
      new ParamDefinition('ease', ParamType.STRING, false, 'power3.out'),
      new ParamDefinition('stagger', ParamType.NUMBER, false, 0),
    ];
  }

  createAnimation() {
    const animation = new DynamicNumberAnimation();
    // transfer template settings to this native animation instance
    // as their parameters are the same
    animation.updateSettings(this.getParamValues());
    return animation;
  }
}

class DynamicNumberAnimation extends NativeAnimation {
  /**
   * @param {import('@/core/entities/context/Context').Context} context
   * @returns {gsap.core.Timeline | gsap.core.Tween}
   */
  create(context) {
    const {start, end, formatter, toFixed, ease, stagger, duration} = this.settings;

    const tl = gsap.timeline();
    const glyphs = context.getGlyphs(this.target);
    glyphs.forEach((glyph) => {

      let newFormatter
      if (formatter === 'auto') {
        const group = glyph.innerHTML.match(/([^\d]*)(\d+)(.*)/)
        if (group) {
          newFormatter = group[1] + '@num' + group[3];
        } else {
          newFormatter = '@num';
        }
      } else if (formatter.includes('@num')) {
        newFormatter = formatter;
      } else {
        newFormatter = '@num';
      }

      // console.log('>>>', {formatter, newFormatter, innerHTML: glyph.innerHTML})

      // parse from html if giving a reg string
      let computedStart = start, computedEnd = end;
      if (start === '@num' || end === '@num') {
        const group = glyph.innerHTML.match(newFormatter.replace('@num', '([\\d.]+)'))
        if (start === '@num') {
          computedStart = parseFloat(group[1]);
        }
        if (end === '@num') {
          computedEnd = parseFloat(group[1]);
        }
      }

      // let newToFixed;
      // if (toFixed === 'auto') {
      //   const group = glyph.innerHTML.match(/([\d]+)\.([\d]+)/)
      //   if (group) {
      //     newToFixed = group[2].length
      //   } else {
      //     newToFixed = 0;
      //   }
      // } else if (typeof toFixed === 'number') {
      //   newToFixed = toFixed
      // } else {
      //   newToFixed = 0;
      // }

      // console.log('>>>', {formatter, newFormatter, innerHTML: glyph.innerHTML, computedStart, computedEnd})

      let tw;
      const tempNumObj = {tempNum: computedStart};
      const onUpdate = () => {
        const num = tempNumObj.tempNum.toFixed(toFixed);
        const text = newFormatter.replaceAll('@num', num);
        glyphs.forEach(glyph => glyph.innerHTML = text);
      }
      onUpdate();   // call it firstly
      tw = gsap.to(tempNumObj, {tempNum: computedEnd, ease, duration, onUpdate});

      // console.log('dynamic number', glyphs, tw);
      tl.add(tw, '<' + stagger);
    })

    return tl;
  }
}
