export function getDefaultTargetTree() {
  let nodeId = 0;

  const addLeafChildren = (node, childClassesList) => {
    childClassesList.forEach(classes => {
      node.children.push(new TargetTreeNode(nodeId++, classes, true));
    });
  };

  const scene = new TargetTreeNode(nodeId++, 'scene');

  const background = new TargetTreeNode(nodeId++, 'background');
  scene.children.push(background);

  const chart = new TargetTreeNode(nodeId++, 'chart');
  scene.children.push(chart);

  const title = new TargetTreeNode(nodeId++, 'title');
  chart.children.push(title);
  addLeafChildren(title, ['main-title', 'sub-title']);

  const mark = new TargetTreeNode(nodeId++, 'mark');
  chart.children.push(mark);
  addLeafChildren(mark, ['bar', 'label bar-label', 'line', 'point', 'area', 'sector']);

  const axis = new TargetTreeNode(nodeId++, 'axis');
  chart.children.push(axis);

  const legend = new TargetTreeNode(nodeId++, 'legend');
  chart.children.push(legend);
  addLeafChildren(legend, ['legend-label', 'legend-symbol']);

  const xAxis = new TargetTreeNode(nodeId++, 'x-axis');
  axis.children.push(xAxis);
  addLeafChildren(xAxis, ['domain x-domain', 'label x-label', 'tick x-tick', 'symbol x-symbol', 'title x-title']);

  const yAxis = new TargetTreeNode(nodeId++, 'y-axis');
  axis.children.push(yAxis);
  addLeafChildren(yAxis, ['domain y-domain', 'label y-label', 'tick y-tick', 'symbol y-symbol', 'title y-title']);

  setFullClassesRecursively(scene);
  return TargetTreeNode.toJSON(scene);
}

function setFullClassesRecursively(node, parentStr) {
  let fullClasses;
  if (!parentStr) {
    fullClasses = node.classes;
  } else {
    fullClasses = parentStr + ' ' + node.classes;
  }
  node.fullClasses = fullClasses;
  node.children.forEach(child => setFullClassesRecursively(child, fullClasses));
}

class TargetTreeNode {
  id
  type
  classes
  fullClasses
  children
  isLeaf

  constructor(id, classes, isLeaf) {
    this.id = id;
    this.classes = classes;
    const classList = classes.split(' ');
    this.type = classList[classList.length - 1];
    this.children = [];
    this.isLeaf = isLeaf ?? false;
  }

  static toJSON(object) {
    return Object.assign({}, object, {
      children: object.children.map(child => TargetTreeNode.toJSON(child)),
    });
  }
}