import {BaseAnimation} from "@/core/entities/spec/BaseAnimation";
import gsap from 'gsap';

export class SyncAnimation extends BaseAnimation {
  /** @type {{sync: BaseAnimation[]}} */
  sequence

  /**
   * @param {BaseAnimation[]} sync
   */
  constructor(sync=[]) {
    super();
    this.type = 'sync';
    this.sequence = {sync}
  }

  /**
   * @param {BaseAnimation} animation
   * @returns {SyncAnimation}
   */
  append(animation) {
    this.sequence.sync.push(animation);
    return this;
  }

  computeDuration(context) {
    const {yoyo} = this.timing.getComputedTiming();
    if (this.timing?.duration) {
      return this.timing.duration;
    }
    let duration = 0;
    this.sequence.sync.forEach(ani => {
      const subDuration = ani.computeDuration(context.deriveBy(this.target));
      duration = Math.max(duration, subDuration + (ani.timing.start ?? 0) + (ani.timing.offset ?? 0));
    });
    if (yoyo) {
      return duration * (yoyo.repeat + 1) + yoyo.delay * yoyo.repeat;
    }
    return duration;
  }

  /**
   * @param {import('@/core/entities/context/Context').Context} context
   * @returns {Set<HTMLElement>}
   */
  computePossibleElements(context) {
    const set = new Set();
    this.sequence.sync.forEach(ani => {
      const subSet = ani.computePossibleElements(context.deriveByWithoutTransform(this.target));
      subSet.forEach(el => set.add(el));
    });
    return set;
  }

  /**
   * @param {import('../../context/Context').RenderContext} context
   * @returns {gsap.core.Timeline}
   */
  create(context) {
    const tl = gsap.timeline();
    this.sequence.sync.forEach(ani => {
      const subTl = ani.create(context.deriveBy(this.target));
      if (subTl === null) {
        return;
      }
      // if (ani.timing.duration) {
      //   subTl.duration(ani.timing.duration);
      // }
      // console.log('add ani: sync', ani, ani.timing.startPlusOffset());
      tl.add(subTl, ani.timing.startPlusOffset());
    });

    const {yoyo} = this.timing.getComputedTiming();
    if (this.timing?.duration) {
      tl.duration(this.timing.duration);
    }
    if (yoyo) {
      tl.yoyo(true);
      tl.repeat(yoyo.repeat);
      tl.repeatDelay(yoyo.delay);
    }
    return tl;
  }
}
