import {Animation} from "@/utils/entities-core/Animation"
import {AnimationTemplate} from "@/utils/entities-core/AnimationTemplate"
import {TemplateType} from "@/utils/const/TemplateType";

const animationJSON = [
  {
    "selector": ".axis",
    "grouping": {
      "groupByClass": "x",
      "position": "start with previous",
      "stagger": 0.3,
      "grouping": {
        "groupBy": "id",
        "position": "start with previous",
        "stagger": 0.01
      }
    },
    "effects": [
      {
        "type": "fade",
        "mode": "in",
        "duration": 0.6
      }
    ],
    "labels": [
      "enter"
    ],
    "position": "start after previous",
  },
  {
    "selector": ".bar,.bar-label",
    "grouping": {
      "groupByClass": "bar",
      "position": "start with previous",
      "stagger": 0.02,
      "grouping": {
        "groupBy": "id",
        "position": "start with previous",
        "stagger": 0.05
      }
    },
    "effects": [
      {
        // "type": "fade",
        // "mode": "in",
        "attribute": "opacity",
        "from": 0,
        "to": 0.6,
        "duration": 0.6
      },
      {
        "attribute": "fill",
        "from": 'red',
        "duration": 0.6
      }
    ],
    "labels": [
      "enter"
    ],
    "position": "start with previous",
    "offset": 0,
  },
]

const animationGen = function() {
  let jsonStr = JSON.stringify(animationJSON)
  return Animation.from(JSON.parse(jsonStr))
}

const barChartEnter3 = new AnimationTemplate(
  'bar chart-3',
  'Bar chart enter 3',
  animationGen,
  TemplateType.BAR_CHART,
  ['chart-level', 'enter', 'color']
)

export {
  barChartEnter3
}
