import {Parser} from "@/core/parser/Parser";
import {Generator} from "@/core/generator/Generator";
import * as d3 from 'd3';
import gsap from "gsap";
import {TemplateLibrary} from "@/core/library/TemplateLibrary";
import {PreviewerManager} from "@/core/previewer/PreviewerManager";
import {buildInTemplates} from "@/core/build-in";
import {buildInWrappers} from "@/core/previewer/build-in-wrapper";
import {buildInPreviewers} from "@/core/previewer/template-previewer";
import {AnimationParser} from "@/core/parser/AnimationParser";
import {JsonUtils} from "@/core/utils/JsonUtils";
import {ObjectUtils} from "@/utils/common/ObjectUtils";

function UserException(message) {
  this.message = message;
}

export class Compiler {
  /** @type {Parser} */
  parser
  /** @type {Generator} */
  generator
  /** @type {TemplateLibrary} */
  templateLib
  /** @type {PreviewerManager} */
  previewerManager

  constructor() {
    this.parser = new Parser();

    this.templateLib = new TemplateLibrary();
    buildInTemplates.forEach(t => this.templateLib.registerBuildInTemplate(t));

    this.previewerManager =  new PreviewerManager(this.templateLib);
    buildInWrappers.forEach(w => this.previewerManager.registerPreviewWrapper(w));
    buildInPreviewers.forEach(p => this.previewerManager.registerPreviewer(p));
  }

  /**
   * @param {string | HTMLElement} elementOrSelector
   * @param {string | Object} animationJSON
   * @param {Object<string, any>} vars
   * @returns {gsap.core.Timeline}
   */
  compile(elementOrSelector, animationJSON, vars={}) {
    console.log("animationJSON", animationJSON)

    if (animationJSON.variable != null && animationJSON.sequence != null) {
      animationJSON = this.createReplacedAnimationJSON(animationJSON, animationJSON.variable);
    }
    // check children
    ObjectUtils.traversalObject(animationJSON, (key, current, parent) => {
      if (current && current.variable != null && current.sequence != null) {
        parent[key] = this.createReplacedAnimationJSON(current, current.variable);
      }
    });
    const animation = AnimationParser.fromJSON(animationJSON);
    if (!animation) {
      throw new UserException('No Animation!');
    }

    const rootSel = d3.select(elementOrSelector);
    this.generator = new Generator();
    this.generator.init(rootSel, this.templateLib, animation);

    rootSel.selectAll('*').each(function() {
      this._original = {}
      if (typeof this.getCTM === 'function') {
        this._original.CTM = this.getCTM();
      }
    });
    const resultTimeline = this.generator.generate();

    const timeline = gsap.timeline({autoRemoveChildren: false, ...vars});
    timeline.add(resultTimeline);
    timeline.pause();
    return timeline;
  }

  /**
   * @param {string | HTMLElement} elementOrSelector
   * @param {string} templateName
   * @returns {Set<Element>}
   */
  computePossibleElements(elementOrSelector, templateName) {
    const originalRoot = d3.select(elementOrSelector).node();

    const rootSel = d3.select(originalRoot.cloneNode(true));    // type element is ok for d3.select
    this.generator = new Generator();
    this.generator.init(rootSel, this.templateLib, null);

    return this.generator.computePossibleElements(templateName);
  }

  /**
   * @param {string | HTMLElement} elementOrSelector
   * @param {string | Object} animationJSON
   * @param {Object?} variable
   * @returns {number}
   */
  computeDuration(elementOrSelector, animationJSON, variable) {
    if (variable != null) {
      animationJSON = this.createReplacedAnimationJSON(animationJSON, variable);
    }
    const animation = AnimationParser.fromJSON(animationJSON);

    const originalRoot = d3.select(elementOrSelector).node();

    const rootSel = d3.select(originalRoot.cloneNode(true));    // type element is ok for d3.select
    this.generator = new Generator();
    this.generator.init(rootSel, this.templateLib, animation);

    return this.generator.computeDuration() || 1;
  }

  createReplacedAnimationJSON(animationJSON, variable) {
    const retJSON = JsonUtils.deepCopy(animationJSON);
    JsonUtils.traversalObject(retJSON, (key, current, parent) => {
      if (typeof current !== 'string' || !current.includes('$')) {
        return;
      }
      current = current.trim();
      if (current.match(/^\$[0-9a-zA-Z]*$/)) {  // '$xxx'
        const paramName = current.substr(1);
        if (paramName in variable) {
          parent[key] = variable[paramName];
        }
      } else {
        let result = current;
        Object.keys(variable).forEach(paramName => {
          result = result.replaceAll('$' + paramName, variable[paramName]);
        })
        parent[key] = result;
      }
    });
    console.log("animationJSON", retJSON)
    return retJSON;
  }
}
