import {ChartShotAnimation} from "@/core/entities/spec/ChartShotAnimation";
import {TemplateParser} from "@/core/parser/TemplateParser";
import {AnimationParser} from "@/core/parser/AnimationParser";
import JSON5 from 'json5';

export class ChartShotAnimationParser {
  static reviver(key, value) {
    return key === "" ? ChartShotAnimationParser.fromJSON(value) : value;
  }

  /**
   * @param {Object} json
   * @returns {ChartShotAnimation}
   */
  static fromJSON(json) {
    if (typeof json === 'string') {
      return JSON5.parse(json, ChartShotAnimationParser.reviver);
    } else {
      const obj = new ChartShotAnimation();

      let templates;
      if (json.templates && Array.isArray(json.templates)) {
        templates = json.templates.map(t => TemplateParser.fromJSON(t));
      } else {
        templates = [];
      }
      const animation = AnimationParser.fromJSON(json.animation);
      return Object.assign(obj, json, {templates, animation});
    }
  }
}