import {NativeTemplate} from "@/core/entities/spec/template/NativeTemplate";
import {TemplateUsage} from "@/core/entities/spec/animation/TemplateUsage";
import {SyncAnimation} from "@/core/entities/spec/animation/SyncAnimation";
import {ParamType} from "@/core/entities/spec/parameter/ParamType";
import {ParamDefinition} from "@/core/entities/spec/parameter/ParamDefinition";

export class ChartWithoutMarkFadeIn2Template extends NativeTemplate {
  constructor() {
    super();
    this.templateName = 'Axes fade in';
    this.parameters = [
      new ParamDefinition('titleStagger', ParamType.NUMBER, false, 0.2),
      new ParamDefinition('symbolStagger', ParamType.NUMBER, false, 0),
      new ParamDefinition('axisElementStagger', ParamType.NUMBER, false, 0),
      new ParamDefinition('ease', ParamType.STRING, false, 'power1.out'),
    ]
  }

  createAnimation() {
    const titleFadeIn = new TemplateUsage('Fade-in')
      .setTarget('.title,.subtitle,.main-title,.sub-title')
      .set('stagger', this.getParamValue('titleStagger'));

    const xAxisSymbolFadeIn = new TemplateUsage('Fade-in')
      .setTarget('.x-axis.symbol')
      .set('stagger', this.getParamValue('symbolStagger'));
    const xAxisGrow = new TemplateUsage('Grow')
      .setTarget('.x-axis.domain')
      .updateSettings({direction: 'left', ease: this.getParamValue('ease')});
    const yAxisGrow = new TemplateUsage('Grow')
      .setTarget('.y-axis.domain')
      .updateSettings({ease: this.getParamValue('ease')});    // default direction is bottom

    const labelFadeIn = new TemplateUsage('Fade-in')
      .setTarget('.axis.label')
      .set('stagger', this.getParamValue('axisElementStagger'))
      .updateTiming({offset: 1});
    const tickFadeIn = new TemplateUsage('Fade-in')
      .setTarget('.axis.tick')
      .set('stagger', this.getParamValue('axisElementStagger'))
      .updateTiming({offset: 1});

    return new SyncAnimation()
      .append(titleFadeIn)
      .append(xAxisSymbolFadeIn)
      .append(xAxisGrow)
      .append(yAxisGrow)
      .append(labelFadeIn)
      .append(tickFadeIn);
  }
}
