import { NativeTemplate } from "@/core/entities/spec/template/NativeTemplate";
import { ParamType } from "@/core/entities/spec/parameter/ParamType";
import { ParamDefinition } from "@/core/entities/spec/parameter/ParamDefinition";
import { NativeAnimation } from "@/core/entities/spec/animation/NativeAnimation";
import gsap from "gsap";


export class TextTemplate extends NativeTemplate {
    constructor() {
        super();
        this.templateName = 'Text';
        this.parameters = [
            new ParamDefinition('duration', ParamType.NUMBER, false, 1),
            new ParamDefinition('ease', ParamType.STRING, false, 'power1.out'),
            new ParamDefinition('stagger', ParamType.NUMBER, false, 0),
            new ParamDefinition('displayTime', ParamType.NUMBER, false, 3),
            new ParamDefinition('x', ParamType.NUMBER, false, 50),
            new ParamDefinition('y', ParamType.NUMBER, false, 50),
            new ParamDefinition('fontSize', ParamType.NUMBER, false, 20),
            new ParamDefinition('fill', ParamType.STRING, false, "#000"),
            new ParamDefinition('textContent', ParamType.STRING, false, "text"),
        ];
    }

    createAnimation() {
        const animation = new TextAnimation();
        animation.updateSettings(this.getParamValues());
        return  animation
    }
}

class TextAnimation extends NativeAnimation {
    /**
     * @param {import('@/core/entities/context/Context').Context} context
     * @returns {gsap.core.Timeline | gsap.core.Tween}
     */
    create(context) {
        const {displayTime, ease, duration,  x, y, fill, fontSize, textContent} = this.settings;
        const tl = gsap.timeline()
        const root_sel = context.getRootSel();
        // console.log(`scale(${(reverseDirection? -1: 1)*scale}, ${scale})`)
        const el_sel = root_sel.select('svg').node() ? root_sel.select('svg') : root_sel;
        let newMaskRect = el_sel.append('text')
            .text(textContent)
            .classed(`anno-text-${textContent}`, true)
            .attr("x", x)
            .attr("y", y)
            .attr("font-size", fontSize)
            .attr('fill', fill);
        const text = newMaskRect.node()

        let tw;
        tw = gsap.fromTo(text, {opacity: 0}, {opacity: 1, ease, duration});
        tl.add(tw, 0);
        tw = gsap.to(text, {opacity: 0, ease, duration});

        tl.add(tw, '+=' + displayTime);
        return tl;
    }
}
