import {NativeTemplate} from "@/core/entities/spec/template/NativeTemplate";
import {ParamType} from "@/core/entities/spec/parameter/ParamType";
import {ParamDefinition} from "@/core/entities/spec/parameter/ParamDefinition";
import {NativeAnimation} from "@/core/entities/spec/animation/NativeAnimation";
import gsap from "gsap";
import {Timing} from "@/core/entities/spec/Timing";

export class CameraMoveTemplate extends NativeTemplate {
  constructor() {
    super();
    this.templateName = 'Camera-move';
    this.parameters = [
      new ParamDefinition('x', ParamType.ANY, false, '+=50'),    // number or string
      new ParamDefinition('y', ParamType.ANY, false, '+=50'),    // number or string
      new ParamDefinition('duration', ParamType.NUMBER, false, 1),
      new ParamDefinition('ease', ParamType.STRING, false, 'power1.out'),
      new ParamDefinition('yoyo', ParamType.OBJECT, false, null),
    ];
  }

  createAnimation() {
    const animation = new CameraMoveAnimation();
    // transfer template settings to this native animation instance
    // as their parameters are the same
    animation.updateSettings(this.getParamValues());
    return animation;
  }
}

class CameraMoveAnimation extends NativeAnimation {
  /**
   * @param {import('@/core/entities/context/Context').Context} context
   * @returns {gsap.core.Timeline | gsap.core.Tween}
   */
  create(context) {
    const {x, y, ease} = this.settings;
    let {duration, yoyo} = this.settings;

    let tw;
    const scene = context.renderContext.rootSel;

    const yoyoVars = {};
    if (yoyo) {
      if (yoyo === true) {
        // not object, use default
        yoyo = Timing.getDefaultYoyo();
      }
      yoyoVars.yoyo = true;
      yoyoVars.repeat = yoyo.repeat;
      yoyoVars.repeatDelay = yoyo.delay;
      duration /= (yoyoVars.repeat + 1);
    }

    // // change percentage to pixel
    // [from, to].forEach(vars => {
    //   if (vars != null && typeof from === 'object') {
    //     let {x, y} = vars;
    //     if (typeof from === 'string' && x.match(/.*([\d.]*%).*/)) {
    //       x = parseFloat(from.replace('%', ''));
    //       vars.x = x * scene.getBoundingClientRect().width;
    //     }
    //     if (typeof from === 'string' && y.match(/\s*(\d*)\.%\s*/)) {
    //       y = parseFloat(vars.replace('%', ''));
    //       vars.y = y * scene.getBoundingClientRect().height;
    //     }
    //   }
    // });

    const translateX = x.replace('+', '-').replace('-', '+');
    const translateY = y.replace('+', '-').replace('-', '+');

    const vars = {translateX, translateY, ease, duration};
    tw = gsap.to(scene.node(), vars);

    // console.log('camera move', scene, this.settings, tw);
    return tw;
  }
}
