import {ParamType} from "@/core/entities/spec/parameter/ParamType";

export class ParamDefinition {
  /** @type {string} */
  paramName
  /** @type {ParamType} */
  paramType
  /** @type {boolean} */
  required
  /** @type {*} */
  defaultValue

  /**
   * @param {string} paramName
   * @param {ParamType} paramType
   * @param {boolean} required
   * @param {*} defaultValue
   */
  constructor(
      paramName='param',
      paramType=ParamType.ANY,
      required=true,
      defaultValue=undefined,
  ) {
    this.paramName = paramName;
    this.paramType = /** @type {ParamType} */ paramType.toLowerCase();
    this.required = required;
    this.defaultValue = defaultValue;
  }

  /**
   * @param {*} value
   * @returns {boolean}
   */
  isAcceptable(value) {
    if (value === undefined && this.required) {
      return false;
    }
    switch (this.paramType) {
      case ParamType.NUMBER: return typeof value === "number";
      case ParamType.STRING: return typeof value === "string";
      case ParamType.OBJECT: return typeof value === 'object';
      case ParamType.ARRAY: return Array.isArray(value);
      case ParamType.ANY: return true;
      case ParamType.BOOLEAN: return typeof value === 'boolean';
      case ParamType.NUMBER_OR_ARRAY: return typeof value === 'number' || Array.isArray(value);
      case ParamType.STRING_OR_ARRAY: return typeof value === 'string' || Array.isArray(value);
    }
    return false;
  }
}