import {NativeTemplate} from "@/core/entities/spec/template/NativeTemplate";
import {TemplateUsage} from "@/core/entities/spec/animation/TemplateUsage";
import {SyncAnimation} from "@/core/entities/spec/animation/SyncAnimation";
import {ParamType} from "@/core/entities/spec/parameter/ParamType";
import {ParamDefinition} from "@/core/entities/spec/parameter/ParamDefinition";
import {NormalTarget} from "@/core/entities/spec/Target";

export class BarSymbolLabelBounceTemplate extends NativeTemplate {
  constructor() {
    super();
    this.templateName = 'Bar-bounce';
    this.parameters = [
      new ParamDefinition('symbol-ease', ParamType.STRING, false, 'back.out'),
      new ParamDefinition('bar-ease', ParamType.STRING, false, 'power1.out'),
      new ParamDefinition('duration', ParamType.NUMBER, false, 1.5),
    ]
  }

  createAnimation() {
    // const symbolZoomEnter = new TemplateUsage('basic-zoom')
    //   .setTarget('.axis.symbol')
    //   .updateSettings({
    //     ease: this.getParamValue('symbol-ease'),
    //     duration: this.getParamValue('duration'),
    //   });

    // const axisLabelFadeIn = new TemplateUsage('Fade-in')
    //   .setTarget('.x-axis.label')
    //   .set('duration', this.getParamValue('duration'));

    const barBonce = new TemplateUsage('Move-to')
    .setTarget('.bar')
    .updateSettings({
      y: '-=15',
      yoyo: true,
    })

    const barLabelBource = new TemplateUsage('Move-to')
      .setTarget('.bar-label,.label')
      .updateTiming({start: 0.4})
      .updateSettings({
        y: '-=15',
        yoyo: true,
      });

    return new SyncAnimation()
      // .append(symbolZoomEnter)
      // .append(axisLabelFadeIn)
      .append(barBonce)
      .append(barLabelBource)
      .setTarget(new NormalTarget('.bar,.axis.symbol,.bar-label,.label'));
  }
}
