import {Timing} from "@/core/entities/spec/Timing";
import {NormalTarget, Target} from "@/core/entities/spec/Target";

export class BaseAnimation {
  /** @type {Target} */
  target
  /** @type {Timing} */
  timing

  /** @type {string} */
  type
  /** @type {Object<string, any>} */
  settings

  /**
   * @param {Object<string, any>} settings
   */
  constructor(settings={}) {
    this.target = Target.getDefaultTarget();
    this.timing = new Timing();
    this.type = 'unknown';
    const defaultSettings = this.getDefaultSettings();
    this.settings = Object.assign(defaultSettings, settings);
  }

  /**
   * @param {Target | string} target
   * @returns {BaseAnimation} this
   */
  setTarget(target) {
    if (typeof target === 'string') {
      this.target = new NormalTarget(target);
    } else {
      this.target = target;
    }
    return this;
  }

  /**
   * @param {Object} target
   * @returns {BaseAnimation} this
   */
  updateTarget(target) {
    Object.assign(this.target, target);
    return this;
  }

  /**
   * @param {Timing} timing
   * @returns {BaseAnimation} this
   */
  setTiming(timing) {
    this.timing = timing;
    return this;
  }

  /**
   * @param {Timing | Object} timing
   * @returns {BaseAnimation} this
   */
  updateTiming(timing) {
    Object.keys(timing).forEach(key => {
      if (timing[key] !== undefined) {
        this.timing[key] = timing[key];
      }
    })
    return this;
  }

  /**
   * @returns {Object<string, any>}
   */
  getDefaultSettings() {
    return {};
  }

  /**
   * @param {Object<string, any>} settings
   * @returns {BaseAnimation} this
   */
  updateSettings(settings) {
    Object.assign(this.settings, settings);
    return this;
  }

  /**
   * @param {String} key
   * @param {*} value
   * @returns {BaseAnimation} this
   */
  set(key, value) {
    this.settings[key] = value;
    return this;
  }

  /**
   * @param {import('@/core/entities/context/Context').Context} context
   * @returns {number}
   */
  // eslint-disable-next-line no-unused-vars
  computeDuration(context) {
    throw 'Animation is not implemented';
  }

  /**
   * @param {import('@/core/entities/context/Context').Context} context
   * @returns {Set<HTMLElement>}
   */
  // eslint-disable-next-line no-unused-vars
  computePossibleElements(context) {
    throw 'Animation is not implemented';
  }

  /**
   * @param {import('@/core/entities/context/Context').Context} context
   * @returns {gsap.core.Timeline | gsap.core.Tween}
   */
  // eslint-disable-next-line no-unused-vars
  create(context) {
    throw 'Animation is not implemented';
  }
}
