import {NativeTemplate} from "@/core/entities/spec/template/NativeTemplate";
import {TemplateUsage} from "@/core/entities/spec/animation/TemplateUsage";
import {SyncAnimation} from "@/core/entities/spec/animation/SyncAnimation";
import {ParamType} from "@/core/entities/spec/parameter/ParamType";
import {ParamDefinition} from "@/core/entities/spec/parameter/ParamDefinition";
import {NormalTarget} from "@/core/entities/spec/Target";

export class ShowArrowLabelAnnoTemplate extends NativeTemplate {
  constructor() {
    super();
    this.templateName = 'Show-arrow-label';
    this.parameters = [
      new ParamDefinition('distance', ParamType.NUMBER, false, 30),
      new ParamDefinition('displayTime', ParamType.NUMBER, false, 3),
      new ParamDefinition('effect-duration', ParamType.NUMBER, false, 1.5),
    ]
  }

  createAnimation() {
    const arrowFloatEnter = new TemplateUsage()
      .useTemplate('Float')
      .setTarget('.arrow')
      .set('distance', this.getParamValue('distance'))
      .set('duration', this.getParamValue('effect-duration'));

    const labelFadeIn = new TemplateUsage('Fade-in')
      .setTarget('.label,.text')
      .set('duration', this.getParamValue('effect-duration'));

    const fadeOut = new TemplateUsage()
      .useTemplate('Fade-out')
      .setTarget('*')
      .updateTiming({start: this.getParamValue('displayTime')})

    return new SyncAnimation()
      .append(arrowFloatEnter)
      .append(labelFadeIn)
      .append(fadeOut)
      .setTarget(new NormalTarget('.annotation'));
  }
}
