export class Timing {
  /** @type {number | string} */
  start
  /** @type {number} */
  offset
  /** @type {number} */
  duration
  // /** @type {number} */
  // stagger
  /** @type {{repeat: number, delay: number} | boolean} */
  yoyo

  constructor() {
  }

  /**
   * @returns {Timing}
   */
  static getDefaultTiming() {
    const timing = new Timing();
    timing.start = 0;
    timing.offset = 0;
    timing.duration = 1;
    // timing.stagger = 0;
    timing.yoyo = undefined;
    return timing;
  }

  static simplifyJSON(json) {
    if (typeof json !== 'object') {
      return json;
    }
    const result = {};
    if ('start' in json && json.start !== 0 && json.start !== '0') {
      const num = parseFloat(json.start);
      if (!isNaN(num)) {
        result.start = num;
      }
    }
    if ('offset' in json && json.offset !== 0 && json.offset !== '0') {
      const num = parseFloat(json.offset);
      if (!isNaN(num)) {
        result.offset = num;
      }
    }
    if ('duration' in json && json.duration !== 0 && json.duration !== '0') {
      const num = parseFloat(json.duration);
      if (!isNaN(num)) {
        result.duration = num;
      }
    }
    if ('yoyo' in json) {
      const yoyo = Object.assign({}, Timing.getDefaultYoyo(), json.yoyo);
      if (yoyo.repeat !== 1 || yoyo.delay !== 0) {
        result.yoyo = json.yoyo;
      }
    }
    return Object.keys(result).length === 0 ? undefined : result;
  }

  /**
   * @returns {{delay: number, repeat: number}}
   */
  static getDefaultYoyo() {
    return {repeat: 1, delay: 0};
  }

  getComputedTiming() {
    const computedTiming = Timing.getDefaultTiming();

    Object.keys(this).forEach(key => {
      if (key === 'yoyo') {
        return;
      }
      if (this[key] != null) {
        computedTiming[key] = this[key];
      }
    });

    if (this.yoyo === true) {
      computedTiming.yoyo = Timing.getDefaultYoyo();
    } else if (this.yoyo && typeof this.yoyo === 'object') {
      computedTiming.yoyo = Timing.getDefaultYoyo();
      Object.keys(this.yoyo).forEach(key => {
        if (this.yoyo[key] != null) {
          computedTiming.yoyo[key] = this.yoyo[key];
        }
      });
    }

    return computedTiming;
  }

  startPlusOffset() {
    return (this.start ?? 0) + (this.offset ?? 0);
  }
}
