import {BasePreviewer} from "@/core/previewer/BasePreviewer";
import {PreviewerType} from "@/core/const/PreviewerType";
import * as d3 from 'd3';

export class CameraPreviewer extends BasePreviewer {
  constructor() {
    super();
    this.previewerType = PreviewerType.CAMERA;
  }

  // barGap*2 barWidth barGap barWidth barGap*2
  draw() {
    const rootSel = this.previewerContext.rootSel;
    const {width, height} = this.getCanvasSize();
    const barWidth = 25, barGap = 5;
    const barHeightList = [70, 83, 70, 110];
    const graphWidth = barHeightList.length * barWidth + (barHeightList.length + 3) * barGap;
    const left = (width - graphWidth) / 2;
    const bottom = (height + Math.max(...barHeightList)) / 2;
    const numTexts = ['first', 'second', 'third', 'forth'];

    const chartGrp = rootSel.append('g')
        .classed('scene chart-group group container', true);

    const barGrp = chartGrp.append('g')
        .classed('mark group', true);
    barGrp.selectAll('.bar.glyph').data(barHeightList).enter().append('rect')
        .each(function(d, i) {
          d3.select(this).classed(`chart mark bar ${numTexts[i]}`, true);
        })
        .attr('width', barWidth)
        .attr('height', d => d)
        .attr('x', (d, i) => left + (i + 2) * barGap + i * barWidth)
        .attr('y',  (d) => bottom - d)
        .attr('fill', '#409eff')
        .attr('rx', 5)
        .attr('ry', 5);

    chartGrp.append('line')
        .classed('chart line axis domain', true)
        .attr('x1', left)
        .attr('y1', bottom)
        .attr('x2', left + graphWidth)
        .attr('y2', bottom)
        .attr('stroke', '#808080')
        .attr('stroke-width', 4)
        .attr('stroke-linecap', 'round');

    // const decorationGrp = rootSel.append('g')
    //     .classed('decoration group', true);
    //
    // const strokeWidth = 6;
    // const lineLen = 30;   // the half len of each L symbol
    //
    // const mask = rootSel.append('defs')
    //     .append('mask')
    //     .attr('id', 'decoration-mask')
    // mask.append('rect')
    //     .attr('x', 0)
    //     .attr('y', 0)
    //     .attr('width', width)
    //     .attr('height', height)
    //     .attr('fill', 'white');
    // mask.append('rect')
    //     .attr('x', lineLen)
    //     .attr('y', 0)
    //     .attr('width', width - 2 * lineLen)
    //     .attr('height', height);
    // mask.append('rect')
    //     .attr('x', 0)
    //     .attr('y', lineLen)
    //     .attr('width', width)
    //     .attr('height', height - 2 * lineLen);
    //
    // decorationGrp.append('rect')
    //     .attr('mask', 'url(#decoration-mask)')
    //     .attr('x', strokeWidth * 1.2)
    //     .attr('y', strokeWidth * 1.2)
    //     .attr('width', width - 2.4 * strokeWidth)
    //     .attr('height', height - 2.4 * strokeWidth)
    //     .attr('rx', strokeWidth)
    //     .attr('ry', strokeWidth)
    //     .attr('fill', 'none')
    //     .attr('stroke', '#454545')
    //     .attr('stroke-width', strokeWidth)
  }
}
