import {BaseTransform} from "@/core/entities/spec/BaseTransform";
import * as d3 from 'd3';

export class SortTransform extends BaseTransform {
  /** @type {string} */
  type
  /** @type {string} */
  data
  /** @type {string} */
  attr
  /** @type {string} */
  expr
  /** @type {string} */
  order

  // /** @type {Target} */
  // target
  // /** @type {import('@/core/entities/context/Context').Context} */
  // context

  constructor() {
    super();
    this.type = 'sort';
    this.expr = '0';
  }

  /**
   * @param {d3.Selection} selection
   * @returns {d3.Selection}
   */
  execute(selection) {
    const reverse = this.order === 'dec';
    const nodes = selection.nodes();

    let expr;
    if (this.data) {
      expr = `__node__.__data__.${this.data}`
    } else if (this.attr) {
      expr = `__node__.getAttribute("${this.attr}")`;
    } else if (this.expr) {
      expr = this.expr
          .replaceAll('@classList', '__node__.classList')
          .replaceAll('@id', '__node__.getAttribute("id")')
          .replaceAll('@index', '__index__')
          .replaceAll('@datum', '__node__.__data__')
          .replaceAll('@el', '__node__');
    } else {
      expr = '0';
    }
    console.log('sort expr', expr);
    const compareFunc = (node, index) => {
      let __expr__ = expr, __order__ = this.order;
      if (node.getAttribute('ac-data') !== '' && this.data) {
        __expr__ = '(JSON.parse(__node__.getAttribute("ac-data")) ?? {}).' + this.data;
      }
      // eslint-disable-next-line no-unused-vars
      const func = function (__node__, __index__) {
        if (Array.isArray(__order__)) {
          return __order__.indexOf(eval(__expr__));
        }
        return eval(__expr__)
      };
      return func(node, index);
    };

    const indexMap = new Map();
    nodes.forEach((n, i) => indexMap.set(n, i));
    nodes.sort((n1, n2) => {
      const num = compareFunc(n1, indexMap.get(n1)) - compareFunc(n2, indexMap.get(n1));
      return reverse ? -num : num;
    })
    return d3.selectAll(nodes);
  }

}

