import {computeUUID} from "../common/uuid"
import {fixEffects, fixGroupingSettings} from "@/utils/parser/parser";
import {deepCopy} from "@/utils/common/copy";

class AniUnit {
  /** @type {(number|string)} */
  aniUnitId
  /** @type {string} */
  selector
  /** @type {object} */
  grouping
  /**
   * @typedef EffectCreator
   * @property {string} type
   * @property {object} config
   * @property {number} duration
   * @property {number} offset
   */
  /** @type {Effect[]} */
  effects
  /** @type {string[]} */
  labels
  /** @type {?string} */
  position
  /** @type {number} */
  offset

  constructor({aniUnitId, selector, grouping=null, effects=[],
                labels=[], position=null, offset=0}) {
    this.aniUnitId = aniUnitId
    this.selector = selector
    this.grouping = fixGroupingSettings(grouping)
    this.effects = fixEffects(effects)
    this.labels = labels
    this.position = position
    this.offset = offset
  }

  toJsonObj() {
    return {
      aniUnitId: this.aniUnitId,
      selector: this.selector,
      grouping: deepCopy(this.grouping),
      effects: this.effects.map(e => e.toJsonObj()),
      labels: deepCopy(this.labels),
      position: this.position,
      offset: this.offset,
    }
  }

  static from(rawObj) {
    try {
      if (!rawObj.aniUnitId) {
        rawObj.aniUnitId = computeUUID()
      }
      return new AniUnit(rawObj)
    } catch (exception) {
      console.error(exception)
    }
  }
}

export {
  AniUnit
}
