import {BasePreviewer} from "@/core/previewer/BasePreviewer";
import {PreviewerType} from "@/core/const/PreviewerType";

export class BlankPreviewer extends BasePreviewer {
  constructor() {
    super();
    this.previewerType = PreviewerType.BLANK;
  }

  draw() {
    const rootSel = this.previewerContext.rootSel;
    const {width, height} = this.getCanvasSize();
    rootSel.append('text')
        .attr('transform', 'translate(' + [width / 2, height / 2] + ')')
        .attr('text-anchor', 'middle')
        .attr('fill', 'gray')
        .attr('opacity', 0.5)
        .style('font-family', 'Verdana, Arial, Helvetica, sans-serif')
        .text('No preview is available');
  }
}
