// eslint-disable-next-line
import { ConsNode } from "./ConsNode"
// eslint-disable-next-line
import { Relation } from "./Relation"

class ConsEdge {
  /** @type {string} */
  id
  /** @type {ConsNode} */
  src
  /** @type {ConsNode} */
  dst
  /** @type {Relation} One of the type in Relation except ANY */
  relation

  constraint

  /**
   * @param {ConsNode} src
   * @param {ConsNode} dst
   * @param {Relation} relation
   */
  constructor(src, dst, relation) {
    this.id = ConsEdge.getId(src, dst)
    this.src = src
    this.dst = dst
    this.relation = relation
  }

  /**
   * @param {ConsNode} src
   * @param {ConsNode} dst
   */
  static getId(src, dst) {
    return `${src.id}-${dst.id}`
  }
}

export {
  ConsEdge
}
