import {NativeTemplate} from "@/core/entities/spec/template/NativeTemplate";
import {ParamType} from "@/core/entities/spec/parameter/ParamType";
import {ParamDefinition} from "@/core/entities/spec/parameter/ParamDefinition";
import {TemplateUsage} from "@/core/entities/spec/animation/TemplateUsage";

export class FadeInTemplate extends NativeTemplate {
  constructor() {
    super();
    this.templateName = 'Fade-in';
    this.parameters = [
      new ParamDefinition('duration', ParamType.NUMBER, false, 0.5),
      new ParamDefinition('ease', ParamType.STRING, false, 'power1.out'),
      new ParamDefinition('stagger', ParamType.NUMBER, false, 0),
    ];
  }

  createAnimation() {
    const animation = new TemplateUsage('Fade')
    animation.updateSettings(this.getParamValues());
    animation.updateSettings({fadeType: 'in'});
    return animation;
  }
}
