/* eslint-disable */

import {Animation} from "@/utils/entities-core/Animation";
import { Context } from "@/utils/entities/Context";
import {TemplateUsageParser} from "@/utils/parser/TemplateUsageParser";
import {aniTemplateFactory} from "@/utils/preset/ani-template";

export class Parser {
  /** @type {Context} */
  context
  src

  constructor(context, src) {
    this.context = context
    this.src = src
  }

  execute() {
    if (!this.src.templateUsageText || this.src.templateUsageText === '[]') {
      this.parseAnimationText()
    } else {
      this.parseTemplateUsage()
      console.log('usage ...')
    }
    this.parseRefinementText()
    this.parseConstraintsText()
    this.parseAlignConstraintsText()
  }

  parseTemplateUsage() {
    const templateUsageList = JSON.parse(this.src.templateUsageText)
    const animation = new Animation()
    templateUsageList.forEach(tu => {
      const templateUsage = TemplateUsageParser.from(tu)
      const aniTemplate = aniTemplateFactory.createAniTemplate(templateUsage.use)
      aniTemplate.configs = templateUsage.configs
      aniTemplate.createInstance().aniUnits.forEach(au => {
        animation.addAniUnit(au)
      })
    })
    this.context.animation = animation
  }

  parseAnimationText() {
    let aniJsonObj = JSON.parse(this.src.animationText)
    this.context.animation = Animation.from(aniJsonObj.aniUnits)
    console.log('animationText', this.context.animation)
  }

  parseRefinementText() {
    this.context.refinements = JSON.parse(this.src.refinementsText)
  }

  parseConstraintsText() {
    this.context.constraints = JSON.parse(this.src.constraintsText)
  }

  parseAlignConstraintsText() {
    this.context.alignConstraints = JSON.parse(this.src.alignConstraintsText)
  }
}