import {computeUUID} from "@/utils/common/uuid";
import {ParamType} from "@/core/entities/spec/parameter/ParamType";
import _ from "lodash";

export class BaseTemplate {
  /** @type {string} */
  templateName
  /** @type {ParamDefinition[]} */
  parameters

  /** @type {string} */
  type
  /** @type {Object<string, any>} */
  settings

  constructor() {
    // when not giving it a name...
    // hope we never use this
    this.templateName = computeUUID();
    this.parameters = [];
    this.type = 'unknown';
    this.settings = {};
  }

  /**
   * @returns {BaseTemplate}
   */
  clone() {
    return _.cloneDeep(this);
    
    // const obj = new this.constructor();
    // return Object.assign(obj, this);
  }

  /**
   * @param {Object<string, any>} settings
   * @param {boolean=true} toCheck
   * @returns {BaseAnimation}
   */
  instantiate(settings, toCheck=true) {
    // init settings first
    const defaultSettings = this.getDefaultSettings();
    if (settings) {
      this.settings = Object.assign(defaultSettings, settings);
    } else {
      this.settings = defaultSettings;
    }

    if (toCheck && !this.isValid()) {
      console.warn('Failed to instantiate using template ' + this.templateName
          + '. Params are not valid');
      return null;
    }
    return this.createAnimation();
  }

  getDefaultSettings() {
    const defaultSettings = {};
    this.parameters.forEach(p => defaultSettings[p.paramName] = p.defaultValue);
    return defaultSettings;
  }

  parseSettingsJSON(json) {
    if (!json || typeof json !== 'object') {
      return undefined;
    }
    const result = {};
    this.parameters.forEach(p => {
      if (p.paramName in json) {
        const value = json[p.paramName];
        if (value === undefined) {
          return;
        }
        switch(p.paramType) {
          case ParamType.NUMBER:
            if (typeof value === 'string') {
              const num = parseFloat(value);
              if (!isNaN(num)) {
                result[p.paramName] = num;
              }
            } else {
              result[p.paramName] = value;
            }
            break;
          case ParamType.STRING:
            if (value !== '') {
              result[p.paramName] = value;
            }
            break;
          default:
            result[p.paramName] = value;
        }
      }
    });
    return Object.keys(result).length === 0 ? undefined : result;
  }

  /**
   * @param {string} paramName
   * @returns {*}
   */
  getParamValue(paramName) {
    return this.getParamValues()[paramName];
  }

  /**
   * @returns {Object<string, any>}
   */
  getParamValues(paramNames=null) {
    if (paramNames == null || !Array.isArray(paramNames)) {
      return this.settings;
    }
    const ret = {};
    paramNames.forEach(paramName => {
      ret[paramName] = this.getParamValue(paramName);
    });
    return ret;
  }

  /**
   * @returns {boolean}
   */
  isValid() {
    return this.parameters.every(p => !p.required
        || this.getParamValue(p.paramName) !== undefined);
  }

  /**
   * @returns {BaseAnimation}
   */
  createAnimation() {
    throw 'Template is not implemented';
  }
}
