import {PreviewerType} from "@/core/const/PreviewerType";
import gsap from 'gsap';
import _ from "lodash";

export class BasePreviewer {
  /** @type {PreviewerType} */
  previewerType

  /** @type {PreviewWrapper} */
  previewWrapper
  /** @type {import('@/core/entities/context/RenderContext').RenderContext} */
  previewerContext

  /** @type {gsap.core.Timeline | gsap.core.Tween} */
  timeline

  constructor() {
    this.previewerType = PreviewerType.BLANK;
  }

  /**
   * @param {PreviewWrapper} previewWrapper
   * @param {import('@/core/entities/context/RenderContext').RenderContext} renderContext
   */
  init(previewWrapper, renderContext) {
    this.previewerContext = renderContext;
    this.previewWrapper = previewWrapper;
  }

  /**
   * @returns {BasePreviewer}
   */
  clone() {
    return _.cloneDeep(this);
    // return this.constructor();
  }

  render() {
    this.clear();

    const {width, height} = this.getCanvasSize();
    this.previewerContext.rootSel.attr('viewBox', `0 0 ${width} ${height}`);

    this.draw();

    setTimeout(() => this.apply(), 0);
  }

  /**
   * Clear the previewer
   */
  clear() {
    this.previewerContext.rootSel.selectAll('*').remove();
    this.previewerContext.globalSelection = null;
  }

  draw() {
    throw 'This previewer is abstract';
  }

  /**
   * @returns {{width: number, height: number}}
   */
  getCanvasSize() {
    return {width: 200, height: 150};
  }

  apply() {
    if (this.previewerType === PreviewerType.BLANK) {
      this.timeline = gsap.timeline();
      return;
    }
    this.previewerContext.globalSelection = this.previewerContext.rootSel.selectAll('*');
    const animation = this.previewWrapper.createPreviewAnimation();
    const rootContext = this.previewerContext.createRootContext();
    const context = rootContext.deriveBy(this.previewWrapper.target);
    this.timeline = animation.create(context);
    this.timeline
        .repeat(-1)
        .repeatDelay(1)
        .pause()
        .progress(this.previewWrapper.staticProgress);
  }

  pause() {
    if (!this.timeline) {
      console.warn('Previewer timeline is not ready');
      return;
    }
    this.timeline
        .pause()
        .progress(this.previewWrapper.staticProgress);
  }

  getDuration() {
    if (!this.timeline) {
      console.warn('Previewer timeline is not ready');
      return;
    }
    return this.timeline.duration();
  }

  restart() {
    if (!this.timeline) {
      console.warn('Previewer timeline is not ready');
      return;
    }
    this.timeline.restart();
  }
}
