import axios from "axios";

// backend url
const baseURL = 'api'

export function queryNLPParseResult(payload, callback, errorCallBack) {
  const url = `${baseURL}/nlp/parse`;
  console.log("post", payload)
  axios.post(url, payload)
    .then(rep => {
      callback(rep.data)
    }).catch(error => {
      errorCallBack(error)
    })
}

