import {Animation} from "@/utils/entities-core/Animation"
import {AnimationTemplate} from "@/utils/entities-core/AnimationTemplate"
import {TemplateType} from "@/utils/const/TemplateType";

const animationJSON = [
  {
    "selector": ".group.decoration",
    "grouping": {
      "groupBy": "id",
      "position": "start after previous"
    },
    "effects": [
      {
        "type": "fade",
        "mode": "in",
        "duration": 0.6,
        "effLabels": ["fade-in"],
      },
      {
        "attribute": "opacity",
        "from": 0,
        "duration": 0.6,
        "offset": 4,
        "effLabels": ["fade-out"],
      }
    ],
    "labels": [
      "emphasize"
    ],
    "position": "start after previous",
  },
]

const animationGen = function() {
  let jsonStr = JSON.stringify(animationJSON)
  return Animation.from(JSON.parse(jsonStr))
}

const decorationTemplate = new AnimationTemplate(
  'decoration-fade-in-then-out',
  'Decoration fade-in fade-out',
  animationGen,
  TemplateType.BAR_CHART,
  ['decoration', 'emphasize']
)

export {
  decorationTemplate
}
