import {NativeTemplate} from "@/core/entities/spec/template/NativeTemplate";
import {ParamType} from "@/core/entities/spec/parameter/ParamType";
import {ParamDefinition} from "@/core/entities/spec/parameter/ParamDefinition";
import {TemplateUsage} from "@/core/entities/spec/animation/TemplateUsage";
import {SyncAnimation} from "@/core/entities/spec/animation/SyncAnimation";

export class ZoomTemplate extends NativeTemplate {
  constructor() {
    super();
    this.templateName = 'Zoom';
    this.parameters = [
      new ParamDefinition('enterOrExit', ParamType.STRING, false, 'enter'),
      new ParamDefinition('duration', ParamType.NUMBER, false, 1),
      new ParamDefinition('FadeEase', ParamType.STRING, false, 'power1.inOut'),
      new ParamDefinition('zoomEase', ParamType.STRING, false, 'back.out'),
      new ParamDefinition('stagger', ParamType.NUMBER, false, 0),
    ];
  }

  createAnimation() {
    const enterOrExit = this.getParamValue('enterOrExit');

    if (enterOrExit === 'enter') {
      const fade = new TemplateUsage('Fade-in')
          .set('ease', this.getParamValue('FadeEase'))
          .set('duration', this.getParamValue('duration'))
          .set('stagger', this.getParamValue('stagger'));
      const zoom = new TemplateUsage('Basic-zoom-enter')
          .set('ease', this.getParamValue('zoomEase'))
          .set('duration', this.getParamValue('duration'))
          .set('stagger', this.getParamValue('stagger'));
      return new SyncAnimation([fade, zoom]);

    } else if (enterOrExit === 'exit') {
      const fade = new TemplateUsage('Fade-out')
          .set('ease', this.getParamValue('FadeEase'))
          .set('duration', this.getParamValue('duration'))
          .set('stagger', this.getParamValue('stagger'));
      const zoom = new TemplateUsage('basic-zoom-exit')
          .set('ease', this.getParamValue('zoomEase'))
          .set('duration', this.getParamValue('duration'))
          .set('stagger', this.getParamValue('stagger'));
      return new SyncAnimation([fade, zoom]);

    } else {
      console.warn('Wrong value of param enterOrExit', enterOrExit);
      return null;
    }

  }
}
