export class Context {
  /** @type {import('@/core/entities/context/RenderContext').RenderContext} */
  renderContext
  /** @type {d3.Selection} */
  parentSelection
  /** @type {boolean} */
  fakeRender
  parentTarget

  /**
   * @param {import('@/core/entities/context/RenderContext').RenderContext} renderContext
   * @param {d3.Selection} parentSelection
   * @param {boolean} fakeRender
   */
  constructor(renderContext, parentSelection, parentTarget, fakeRender=false) {
    this.renderContext = renderContext;
    this.parentSelection = parentSelection;
    this.parentTarget = parentTarget;
    this.fakeRender = fakeRender;
  }

  /**
   * @param {d3.Selection} selection
   * @returns {Context}
   */
  derive(selection, parentTarget) {
    return new Context(this.renderContext, selection, parentTarget, this.fakeRender);
  }

  /**
   * @param {Target} target
   * @returns {Context}
   */
  deriveBy(target) {
    const selection = this.getSelection(target);
    if (this.parentSelection.size() !== 0 && selection.size() === 0) {
      console.warn(`Empty select result. 
      parentSelector=${this.parentTarget?.selector ?? 'null'} 
      selector="${target.selector}"`, this);
    }
    return this.derive(selection, target);
  }

  /**
   * @param {Target} target
   * @returns {Context}
   */
  deriveByWithoutTransform(target) {
    if (target.selector.includes('$')) {
      // has param, ignore this target selection
      return this.derive(this.parentSelection);
    } else {
      const selection = this.getSelectionWithoutTransform(target);
      return this.derive(selection, target);
    }
  }

  /**
   * @param {Target} target
   * @returns {SVGGraphicsElement[]}
   */
  getGlyphs(target) {
    if (this.fakeRender) {
      return this.getSelection(target).nodes()
          .map(node => /** @type {SVGGraphicsElement} */ node.cloneNode(true));
    }
    return this.getSelection(target).nodes();
  }

  /**
   * @param {Target} target
   * @returns {SVGGraphicsElement[]}
   */
  getExclusiveGlyphs(target, selector) {
    let glyphs = this.renderContext.targetModel.getExclusiveGlyphs(this.renderContext.rootSel.selectAll(selector), this.parentTarget).nodes();
    if (this.fakeRender) {
      return glyphs.map(node => /** @type {SVGGraphicsElement} */ node.cloneNode(true));
    }
    return glyphs
  }

  /**
   * @param {SVGGraphicsElement} glyph
   * @returns {}
   */
  getBBoxPosition(glyph) {
    const root_sel = this.renderContext.rootSel;
    const svg_sel = root_sel.select('svg').node() ?? root_sel.node();
    const root_point = svg_sel.createSVGPoint();
    const glyph_ctm = glyph._original?.CTM ?? glyph.getCTM();
    const glyph_point = root_point.matrixTransform(glyph_ctm);
    const glyph_bbox = glyph.getBBox();

    var {x, y, width, height} = {
      x: glyph_point.x,
      y: glyph_point.y,
      width: glyph_bbox.width,
      height: glyph_bbox.height
    }

    // console.log("ERVIN", root_point, glyph.getCTM(), glyph_point, glyph._original.CTM);
    if (glyph.nodeName === 'text') {
      switch (glyph.getAttribute("text-anchor")) {
        case "start":
          // start at left bottom corner
          y = y - height * 0.75;
          break;
        case "end":
          // start at right bottom corner
          x = x - width;
          y = y - height * 0.75;
          break;
        case "middle":
          // start at middle bottom
          x = x - width / 2;
          y = y - height * 0.75;
          break;
        default:
          break;
      }
    } else if (glyph.nodeName === 'path') {
      y = y + height / 2
      // TODO: fix the x
    }

    return {x, y, width, height}
  }

  /**
   * @param {Target} target
   * @returns {d3.Selection}
   */
  getSelection(target) {
    return this.renderContext.targetModel.getGlyphs(this.parentSelection, target);
  }

  /**
   * @param {Target} target
   * @returns {d3.Selection}
   */
  getSelectionWithoutTransform(target) {
    return this.renderContext.targetModel.getGlyphsWithoutTransform(this.parentSelection, target);
  }

  /**
   * @param {string} templateName
   * @returns {BaseTemplate}
   */
  getTemplate(templateName){
    return this.renderContext.templateLib.getTemplate(templateName);
  }

  /**
   * @returns {d3.Selection}
   */
  getRootSel() {
    return this.renderContext.rootSel;
  }

  /**
   * @returns {d3.Selection}
   */
  getDefsSel() {
    return this.renderContext.getDefsSel();
  }

  /**
   * @returns {SVGElement}
   */
  getRootSvg() {
    const ret = this.renderContext.rootSel.node();
    return ret.tagName === 'svg' ? ret : ret.querySelector('svg');
  }
}
