import {NativeTemplate} from "@/core/entities/spec/template/NativeTemplate";
import {TemplateUsage} from "@/core/entities/spec/animation/TemplateUsage";
import {SyncAnimation} from "@/core/entities/spec/animation/SyncAnimation";

export class ShineTemplate extends NativeTemplate {
  constructor() {
    super();
    this.templateName = 'Shine';
    this.parameters = [
    ]
  }

  createAnimation() {

    const shine = new TemplateUsage('Change-color')
        // .setTarget('.legend')
        .updateSettings({
          fill: '#fff',
          yoyo: true,
          stagger: 0.03
        });

    return new SyncAnimation()
        .append(shine);
  }
}
