function indicesToString(startIdx, endIdx) {
  const range = (start, end) => Array.from({length: end - start}, (_, i) => i + start);
  const indices = range(startIdx, endIdx + 1);
  return '[' + indices.join(',') + ']';
}

function indicesDictToTargetPos(indices_dict) {
  const wordTargetIconPos = [];

  Object.keys(indices_dict).forEach(k => {
    const indices = JSON.parse(k);
    const targets = indices_dict[k];

    wordTargetIconPos.push({
      startIdx: indices[0],
      endIdx: indices[indices.length-1],
      indices: k,
      target: targets,
      icon: faLink,
      id: computeUUID(),
    });
  });
  return wordTargetIconPos;
}

function TargetPosToindicesDict(wordTargetIconPos) {
  const indices_dict = new Object();

  wordTargetIconPos.forEach((iconPos) => {
    indices_dict[iconPos.indices] = iconPos.target;
  });
  return indices_dict;
}

function getUseToAniMap(aniChart) {
  const wrappers = aniChart.buildInWrappers;
  const useToAniMap = new Object();
  wrappers.forEach(wrapper => {
    const use = wrapper.getTemplateName();
    const ani_type = wrapper.animationType;
    useToAniMap[use] = ani_type;
  });
  return useToAniMap;
}

