// actions
const actions = {
  queryNLPParseResult({state, commit}) {
    commit('changeNLPLinking', true);
    const transcript = deepCopy(state.transcriptSrc);
    transcript.words.forEach(word => delete word.selected);
    var dataGroup = {}
    this.state.target.groupedTargetsMap.forEach((value, key) => {
      dataGroup[key] = Array.from(value);
    });
    const payload = {
      target_model: state.scene.targetModel,
      transcript,
      dataGroup,
    };
    queryNLPParseResult(payload, resp => {
      commit('changeNLPLinking', false);
      commit('computeWordIdxTargets', resp.indices_dict);
    }, errorResp => {
      commit('changeNLPLinking', false);
      Notification({title: `NLP Parse failed`,
        message: errorResp.message, type: 'warning'})
      console.log(errorResp);
    })
  },
  queryNLPGenerateResult({state, commit}) {
    commit('changeNLPLoading', true);

    const transcript = deepCopy(state.transcriptSrc);
    transcript.words.forEach(word => delete word.selected);
    var dataGroup = {}
    this.state.target.groupedTargetsMap.forEach((value, key) => {
      dataGroup[key] = Array.from(value);
    });
    const indices_dict = TargetPosToindicesDict(state.wordTargetIconPos);
    const payload = {
      target_model: state.scene.targetModel,
      transcript,
      dataGroup,
      indices_dict
    };

    console.log('nlp payload', payload);
    queryNLPGenerateResult(payload, resp => {
      console.log('nlp resp', resp);
      const animationJSON = resp.AnimationJSON;

      commit('changeNLPLoading', false);

      state.wordTargetIconPos = [];

      commit('updateEditorUsingAnimationJSON', animationJSON ?? {});
      commit('changeAnimationJSONRenderSign');
      Notification({title: `Generate finished`, type: 'success'})
    }, errorResp => {
      commit('changeNLPLoading', false);
      Notification({title: `Generate failed`,
        message: errorResp.message, type: 'warning'})
      console.log(errorResp);
    })
  },
}

