  updateChartSize(state) {
    const svg = state.svgEl;
    const selectionSvg = state.selectionSvgEl;
    const aniSvg = state.aniSvgEl;
    if (!svg || !selectionSvg || !aniSvg) return;
    const chart = svg.querySelector('svg');
    if (!chart) return;

    const width = chart.getAttribute('width');
    const height = chart.getAttribute('height');

    const w = Math.max(0, state.windowWidth - state.leftPanelWidth - state.targetPanelWidth);
    const h = Math.max(0, state.windowHeight - state.headerHeight - state.footerHeight);

    const wFactor = w / width * 0.84, hFactor = h / height * 0.84;
    const factor = Math.min(wFactor, hFactor);

    svg.setAttribute('width', width * factor);
    svg.setAttribute('height', height * factor)
    svg.setAttribute('viewBox', `0 0 ${width} ${height}`);

    selectionSvg.setAttribute('width', width * factor);
    selectionSvg.setAttribute('height', height * factor)
    selectionSvg.setAttribute('viewBox', `0 0 ${width} ${height}`);

    aniSvg.setAttribute('width', width * factor);
    aniSvg.setAttribute('height', height * factor)
    aniSvg.setAttribute('viewBox', `0 0 ${width} ${height}`);
  },
