import {FadeTemplate} from "@/utils/preset/ani-template/chart-level/FadeTemplate";
import {WipeTemplate} from "@/utils/preset/ani-template/chart-level/WipeTemplate";
import {ScaleTemplate} from "@/utils/preset/ani-template/chart-level/ScaleTemplate";
import {ZoomTemplate} from "@/utils/preset/ani-template/chart-level/ZoomTemplate";
import {FloatTemplate} from "@/utils/preset/ani-template/chart-level/FloatTemplate";

const aniTemplateFactory = new AniTemplateFactory()

aniTemplateFactory.register(new BarChartEnterTemplate())
