import {BasePreviewer} from "@/core/previewer/BasePreviewer";
import {PreviewerType} from "@/core/const/PreviewerType";

export class MovePreviewer extends BasePreviewer {
  constructor() {
    super();
    this.previewerType = PreviewerType.MOVE;
  }

  draw() {
    const rootSel = this.previewerContext.rootSel;
    const {width, height} = this.getCanvasSize();
    const radius = 20;
    const distance = 70;

    rootSel.append('circle')
        .classed('start circle shadow', true)
        .attr('fill', '#409eff')
        .attr('r', radius)
        .attr('cx', (width - distance) / 2)
        .attr('cy', height / 2)
        .attr('opacity', 0.5);
    rootSel.append('circle')
        .classed('target circle', true)
        .attr('fill', '#409eff')
        .attr('r', radius)
        .attr('cx', (width - distance) / 2)
        .attr('cy', height / 2);
  }
}
