import {PreviewerType} from "@/core/const/PreviewerType";
import {AnimationType} from "@/core/const/AnimationType";
import {Target} from "@/core/entities/spec/Target";
import {TargetParser} from "@/core/parser/TargetParser";
import {JsonUtils} from "@/core/utils/JsonUtils";

export class PreviewWrapper {
  /** @type {BaseTemplate} */
  template
  /** @type {PreviewerType} */
  previewerType
  /** @type {AnimationType} */
  animationType
  /** @type {String[]} */
  templateLabels
  /** @type {Object<String, any>} */
  templateSettings
  /** @type {Target} */
  target
  /** @type {number} */
  staticProgress
  /** @type {Object<String, any>} */
  previewSettings

  constructor(
      template,
      previewerType=PreviewerType.BLANK,
      animationType=AnimationType.OTHER,
      templateLabels=[],
      templateSettings={},
      target=undefined,
      staticProgress=1,
      previewSettings={},
  ) {
    this.type = 'previewer';
    this.template = template;
    this.previewerType = previewerType;
    this.animationType = animationType;
    this.templateLabels = templateLabels;
    this.templateSettings = templateSettings;
    this.previewSettings = previewSettings;
    this.staticProgress = staticProgress;

    if (!target) {
      this.target = Target.getDefaultTarget();
    } else if (target instanceof Target) {
      this.target = target;
    } else {
      this.target = TargetParser.fromJSON(target);
    }
  }

  /**
   * @returns {PreviewWrapper}
   */
  clone() {
    return new PreviewWrapper(
        this.template.clone(),
        this.previewerType,
        this.animationType,
        [...this.templateLabels],
        JsonUtils.deepCopy(this.templateSettings),
        Object.assign(new Target, this.target),
        this.staticProgress,
        JsonUtils.deepCopy(this.previewSettings),
    );
  }

  /**
   * @returns {string}
   */
  getTemplateName() {
    return this.template.templateName;
  }

  /**
   * @returns {string}
   */
  getTemplateType() {
    return this.template.type;
  }

  /**
   * @returns {ParamDefinition[]}
   */
  getTemplateParameters() {
    return this.template.parameters;
  }

  /**
   * @returns {BaseAnimation}
   */
  createPreviewAnimation() {
    if (this.template != null) {
      return this.template.instantiate(this.templateSettings);
    } else {
      console.warn(`Can't create animation since preview template is null`)
      return null;
    }
  }
}
