import {NativeTemplate} from "@/core/entities/spec/template/NativeTemplate";
import {TemplateUsage} from "@/core/entities/spec/animation/TemplateUsage";
import {SyncAnimation} from "@/core/entities/spec/animation/SyncAnimation";
import {ParamType} from "@/core/entities/spec/parameter/ParamType";
import {ParamDefinition} from "@/core/entities/spec/parameter/ParamDefinition";

export class HighlightSectorTemplate extends NativeTemplate {
  constructor() {
    super();
    this.templateName = 'Highlight-sector';
    this.parameters = [
        new ParamDefinition('duration', ParamType.NUMBER, false, 1),
    ]
  }

  createAnimation() {
    // const sectorLineDrawPath = new TemplateUsage('Draw-path')
    //   .setTarget('.sector-line');

    // const sectorLabelFloat = new TemplateUsage('Float')
    //   .setTarget('.sector-label');

    const annoLabelDynamicNumber = new TemplateUsage('show-dynamic-number')
        .setTarget('.anno-label,.sector-label')
        .set('start', '@num')
        .set('end', '@num')
        .set('formatter', '@num%');

    const sectorHighlight = new TemplateUsage('Highlight')
        .setTarget('.sector,.arc,.pie')
        .set('centerSelector', '.sector,.arc,.pie');

    return new SyncAnimation()
      // .append(sectorLineDrawPath)
      // .append(sectorLabelFloat)
      .append(annoLabelDynamicNumber)
      .append(sectorHighlight)
      ;
  }
}
