import {NativeTemplate} from "@/core/entities/spec/template/NativeTemplate";
import {ParamType} from "@/core/entities/spec/parameter/ParamType";
import {ParamDefinition} from "@/core/entities/spec/parameter/ParamDefinition";
import {TemplateUsage} from "@/core/entities/spec/animation/TemplateUsage";

export class MoveToTemplate extends NativeTemplate {
  constructor() {
    super();
    this.templateName = 'Move-to';
    this.parameters = [
      new ParamDefinition('x', ParamType.ANY, false, '+=0'),    // number or string
      new ParamDefinition('y', ParamType.ANY, false, '+=0'),    // number or string
      new ParamDefinition('duration', ParamType.NUMBER, false, 1),
      new ParamDefinition('ease', ParamType.STRING, false, 'power1.out'),
      new ParamDefinition('stagger', ParamType.NUMBER, false, 0),
      new ParamDefinition('yoyo', ParamType.OBJECT, false, null),
    ];
  }

  createAnimation() {
    const animation = new TemplateUsage('move')
    const toVars = {
      x: this.getParamValue('x'),
      y: this.getParamValue('y'),
    };
    animation.set('to', toVars);
    animation.set('duration', this.getParamValue('duration'));
    animation.set('ease', this.getParamValue('ease'));
    animation.set('stagger', this.getParamValue('stagger'));
    animation.set('yoyo', this.getParamValue('yoyo'));
    return animation;
  }
}
