import { NativeTemplate } from "@/core/entities/spec/template/NativeTemplate";
import { TemplateUsage } from "@/core/entities/spec/animation/TemplateUsage";
import { SyncAnimation } from "@/core/entities/spec/animation/SyncAnimation";
import { ParamType } from "@/core/entities/spec/parameter/ParamType";
import { ParamDefinition } from "@/core/entities/spec/parameter/ParamDefinition";

export class PieWithLegendEnterTemplate extends NativeTemplate {
  constructor() {
    super();
    this.templateName = 'Pie with legend';
    this.parameters = [
      // new ParamDefinition('titleStagger', ParamType.NUMBER, false, 0.2),
      new ParamDefinition('sectorStagger', ParamType.NUMBER, false, 1),
      new ParamDefinition('legendStagger', ParamType.NUMBER, false, 0),
      new ParamDefinition('labelStagger', ParamType.NUMBER, false, 1),
    ]
  }

  createAnimation() {
    // const titleFadeIn = new TemplateUsage('Fade-in')
    //   .setTarget('.title')
    //   .set('stagger', this.getParamValue('titleStagger'));

    const sectorWheel = new TemplateUsage('simple-sector-wheel')
    .setTarget('.sector,.arc,.pie')
    .set('centerSelector', '.sector,.arc,.pie')
    // .set('stagger', this.getParamValue('sectorStagger'))
    .set('duration', 1);
      // .updateTiming({start: -1});

    // const drawPath = new TemplateUsage('Draw-path')
    //   .setTarget('.sector-line,.arc-line')
    //   .set('stagger', this.getParamValue('lineStagger'));

    const labelFloat = new TemplateUsage('Fade-in')
      .setTarget('.sector-label,.arc-label,.label');
      // .updateTiming({start: -1});
      // .set('stagger', this.getParamValue('labelStagger'));

    const legendFlyIn = new TemplateUsage('Fly-in')
      .setTarget('.legend');
      // .updateTiming({start: -2});
      // .set('stagger', this.getParamValue('legendStagger'));

    return new SyncAnimation()
      // .append(titleFadeIn)
      .append(legendFlyIn)
      .append(sectorWheel)
      .append(labelFloat);
  }
}
