import {TargetParser} from "@/core/parser/TargetParser";
import {SyncAnimation} from "@/core/entities/spec/animation/SyncAnimation";
import {ConcatAnimation} from "@/core/entities/spec/animation/ConcatAnimation";
import {GroupAnimation} from "@/core/entities/spec/animation/GroupAnimation";
import {TemplateUsage} from "@/core/entities/spec/animation/TemplateUsage";
import JSON5 from 'json5';
import {Timing} from "@/core/entities/spec/Timing";

export class AnimationParser {
  static reviver(key, value) {
    return key === "" ? AnimationParser.fromJSON(value) : value;
  }

  /**
   * @param {Object | string} json
   * @returns {BaseAnimation}
   */
  static fromJSON(json) {
    if (typeof json === 'string') {
      return JSON5.parse(json, AnimationParser.reviver);
    } else if ('sequence' in json) {
      return AnimationParser.createSequenceAnimation(json);
    } else if ('use' in json) {
      return AnimationParser.createTemplateUsage(json);
    } else {
      return null;
    }
  }

  /**
   * @param {Object} json
   * @returns {SyncAnimation | ConcatAnimation | GroupAnimation}
   */
  static createSequenceAnimation(json) {
    let obj, sequence;
    if ('sync' in json.sequence && Array.isArray(json.sequence.sync)) {
      obj = new SyncAnimation();
      sequence = {
        sync: json.sequence.sync.map(subObj => AnimationParser.fromJSON(subObj))
      };

    } else if ('concat' in json.sequence && Array.isArray(json.sequence.concat)) {
      obj = new ConcatAnimation();
      sequence = {
        concat: json.sequence.concat.map(subObj => AnimationParser.fromJSON(subObj))
      };

    } else if ('animation' in json.sequence) {
      obj = new GroupAnimation();
      sequence = {
        grouping: json.sequence.grouping,
        animation: AnimationParser.fromJSON(json.sequence.animation)
      };

    } else {
      console.warn('create sequence animation failed', json);
      return null;
    }

    const target = TargetParser.fromJSON(json.target);
    const timing = Object.assign(obj.timing, json.timing ?? {});
    if (timing.yoyo === true) {
      timing.yoyo = Timing.getDefaultYoyo();
    }
    return Object.assign(obj, json, {target, sequence, timing});
  }

  /**
   * @param {Object} json
   * @returns {TemplateUsage}
   */
  static createTemplateUsage(json) {
    const obj = new TemplateUsage();
    const target = TargetParser.fromJSON(json.target);
    const timing = Object.assign(obj.timing, json.timing ?? {});
    if (timing.yoyo === true) {
      timing.yoyo = Timing.getDefaultYoyo();
    }
    const settings = Object.assign(obj.settings, json.settings ?? {});
    return Object.assign(obj, json, {target, timing, settings});
  }
}