import {BasePreviewer} from "@/core/previewer/BasePreviewer";
import {PreviewerType} from "@/core/const/PreviewerType";
import * as d3 from 'd3';
import {ChartLayout} from "@/core/utils/ChartLayout";

export class PathPreviewer extends BasePreviewer {
  constructor() {
    super();
    this.previewerType = PreviewerType.PATH;
  }

  draw() {
    const rootSel = this.previewerContext.rootSel;
    const {width, height} = this.getCanvasSize();

    const scatterdata=[
        [5,10],
        [40,10],
        [40,35],
        [5,35],
        [5,10],

    ];
    const layout = new ChartLayout({
      width, height,
      topRatio: 0.30,
      bottomRatio: 0.17,
      leftRatio: 0.2,
      rightRatio: 0.2
    });
    const barWidthRatio = 0.8;
    const barHeightRatio = 0.8;

    const xScale = d3.scaleLinear()
        .domain([0, d3.max(scatterdata, d => d[0]) / barWidthRatio])
        .range([0, layout.chartWidth])
        // .padding(barWidthRatio);
    const yScale = d3.scaleLinear()
        .domain([0, d3.max(scatterdata, d => d[1]) / barHeightRatio])
        .range([0, layout.chartHeight])

    const chartGrp = rootSel.append('g')
        .classed('chart-group', true);


    // create annotation
    const annotationGrp = chartGrp.append('g')
        .classed('annotation-group', true);


    for (var i=0;i<scatterdata.length;i++){
        scatterdata[i][0]=xScale(scatterdata[i][0]);
        scatterdata[i][1]=layout.chartHeight-yScale(scatterdata[i][1]);
    }

    annotationGrp.append('polyline')
        .classed('chart annotation path', true)
        .attr('transform', `translate(${layout.left},${layout.top})`)
        .attr("points",scatterdata)
        .attr('fill','none')
        .attr('stroke', '#409eff')
        .attr('stroke-width', 4)
        .attr('stroke-linecap', 'round');
        
  }
}
