import {NativeTemplate} from "@/core/entities/spec/template/NativeTemplate";
import {ParamType} from "@/core/entities/spec/parameter/ParamType";
import {ParamDefinition} from "@/core/entities/spec/parameter/ParamDefinition";
import {NativeAnimation} from "@/core/entities/spec/animation/NativeAnimation";
import {ArrayUtils} from "@/utils/common/ArrayUtils";
import gsap from "gsap";

export class HighlightTemplate extends NativeTemplate {
  constructor() {
    super();
    this.templateName = 'Highlight';
    this.parameters = [
      new ParamDefinition('duration', ParamType.NUMBER, false, 1),
      new ParamDefinition('ease', ParamType.STRING, false, 'power1.out'),
      new ParamDefinition('stagger', ParamType.NUMBER, false, 0)
    ];
  }

  createAnimation() {
    const animation = new HighlightAnimation();
    // transfer template settings to this native animation instance
    // as their parameters are the same
    animation.updateSettings(this.getParamValues());
    return animation;
  }
}

class HighlightAnimation extends NativeAnimation {
  getDefaultSettings() {
    return {
      duration: 1,
      ease: 'power1.out',
      stagger: 0
    };
  }
  /**
   * @param {import('@/core/entities/context/Context').Context} context
   * @returns {gsap.core.Timeline | gsap.core.Tween}
   */
  create(context) {

    const {ease, stagger, duration} = this.settings;
    duration
    let tw;

    const selectGlyphs = context.getGlyphs(this.target);
    let selectType = null;
    for (let glyph of selectGlyphs) {
      let type = ArrayUtils.getLastItem(glyph.classList);
      // console.log("ERVIN1", type)
      if (selectType === null) {
        selectType = type;
      } else if (selectType != type) {
        selectType = null;
        break;
      }
    }
    let selector = selectType === null ? '*' : `.${selectType}, .mark`;

    const glyphs = context.getExclusiveGlyphs(this.target, selector);
    const vars = {opacity: 0.13, ease, duration: 0.5, stagger};
    const tl = gsap.timeline()

    tw = gsap.to(glyphs, vars);
    tw.yoyo(true);
    tw.repeat(1);
    tw.repeatDelay(3);
    tl.add(tw, 0)

    return tl;
  }
}
