import { NativeTemplate } from "@/core/entities/spec/template/NativeTemplate";
import { ParamType } from "@/core/entities/spec/parameter/ParamType";
import { ParamDefinition } from "@/core/entities/spec/parameter/ParamDefinition";
import { NativeAnimation } from "@/core/entities/spec/animation/NativeAnimation";
import gsap from "gsap";
import { ANNOTATION_CLASS } from "@/core/const/Element";

export class ArrowTemplate extends NativeTemplate {
    constructor() {
        super();
        this.templateName = 'Arrow';
        this.parameters = [
            new ParamDefinition('reverseDirection', ParamType.BOOLEAN, false, false),
            new ParamDefinition('duration', ParamType.NUMBER, false, 1),
            new ParamDefinition('ease', ParamType.STRING, false, 'power1.out'),
            new ParamDefinition('stagger', ParamType.NUMBER, false, 0),
            new ParamDefinition('displayTime', ParamType.NUMBER, false, 3),
            new ParamDefinition('x', ParamType.NUMBER, false, 50),
            new ParamDefinition('y', ParamType.NUMBER, false, 50),
            new ParamDefinition('color', ParamType.STRING, false, "#000"),
            new ParamDefinition('scale', ParamType.NUMBER, false, 1),
        ];
    }

    createAnimation() {
        const animation = new ArrowAnimation();
        // transfer template settings to this native animation instance
        // as their parameters are the same
        animation.updateSettings(this.getParamValues());
        return animation;
    }
}

class ArrowAnimation extends NativeAnimation {
    /**
     * @param {import('@/core/entities/context/Context').Context} context
     * @returns {gsap.core.Timeline | gsap.core.Tween}
     */
    create(context) {
        const {displayTime, ease, duration, reverseDirection, x, y, color, scale} = this.settings;
        reverseDirection
        const tl = gsap.timeline()
        const root_sel = context.getRootSel();
        // console.log(`scale(${(reverseDirection? -1: 1)*scale}, ${scale})`)
        const el_sel = root_sel.select('svg').node() ? root_sel.select('svg') : root_sel;
        let newMaskRect = el_sel.append('path')
            // let a = d3.select(sel.parentNode).append('rect')
            .attr('transform', `translate(${x}, ${y}), scale(${scale}, ${(reverseDirection? -1: 1)*scale})`)
            .classed(ANNOTATION_CLASS, true)
            .attr('d', 'M16 9L12 5M12 5L8 9M12 5V19')
            .attr('stroke-width', 2)
            .attr('stroke-linecap', 'round')
            .attr('fill', 'none')
            .attr('stroke', color);
        const arrow = newMaskRect.node()

        let tw;
        tw = gsap.fromTo(arrow, {opacity: 0}, {opacity: 1, ease, duration});
        tl.add(tw, 0);
        tw = gsap.to(arrow, {opacity: 0, ease, duration});

        tl.add(tw, '+=' + displayTime);
        return tl;
    }
}
