import {NativeTemplate} from "@/core/entities/spec/template/NativeTemplate";
import {ParamType} from "@/core/entities/spec/parameter/ParamType";
import {ParamDefinition} from "@/core/entities/spec/parameter/ParamDefinition";
import {NativeAnimation} from "@/core/entities/spec/animation/NativeAnimation";
import gsap from "gsap";
import * as d3 from 'd3';
import {Direction} from "@/core/const/Direction";
import {computeUUID} from "@/core/utils/uuid";
import {MASK_CLASS} from "@/core/const/Element";

export class WipeTemplate extends NativeTemplate {
  constructor() {
    super();
    this.templateName = 'Wipe';
    this.parameters = [
      new ParamDefinition('enterOrExit', ParamType.STRING, false, 'enter'),
      new ParamDefinition('direction', ParamType.STRING, false, Direction.BOTTOM),
      new ParamDefinition('duration', ParamType.NUMBER, false, 1),
      new ParamDefinition('ease', ParamType.STRING, false, 'power1.out'),
      new ParamDefinition('stagger', ParamType.NUMBER, false, 0),
      new ParamDefinition('yoyo', ParamType.OBJECT, false, null),
    ];
  }

  createAnimation() {
    const animation = new WipeAnimation();
    // transfer template settings to this native animation instance
    // as their parameters are the same
    animation.updateSettings(this.getParamValues());
    return animation;
  }
}

class WipeAnimation extends NativeAnimation {
  /**
   * @param {import('@/core/entities/context/Context').Context} context
   * @returns {gsap.core.Timeline | gsap.core.Tween}
   */
  create(context) {
    const glyphs = context.getGlyphs(this.target);
    const defs = context.getDefsSel();
    const tl = gsap.timeline()
    const {stagger} = this.settings;
    glyphs.forEach((glyph, i) => {
      const mask = this.addMaskRectToGlyph(defs, glyph);
      const tw = this.createTwForMask(mask, glyph);
      const position = i === 0 ? 0 : ('<' + stagger);
      tl.add(tw, position);
    })
    return tl;
  }

  /**
   * @param {d3.Selection} defs
   * @param {SVGGraphicsElement} glyph
   * @returns {SVGGraphicsElement} the mask rect of glyph
   */
  addMaskRectToGlyph(defs, glyph) {
    if (!glyph.getAttribute('id')) {
      glyph.setAttribute('id', computeUUID());
    }
    const maskId = 'mask-' + glyph.getAttribute('id');
    let mask = defs.select(`#${maskId}`);
    if (mask.empty()) {
      mask = defs.append('mask')
          .attr('id', maskId)
          .classed(MASK_CLASS, true)
          .attr('maskUnits', 'userSpaceOnUse');
      glyph.setAttribute('mask', `url(#${maskId})`);
    }

    const {enterOrExit} = this.settings;
    const wipeRectSel = mask.select('.wipe');
    if (!wipeRectSel.empty() && enterOrExit === 'exit') {
      return wipeRectSel.node();
    }

    const bbox = glyph.getBBox();
    let strokeWidth = parseFloat(glyph.getAttribute('stroke-width') ?? '0');
    d3.select(glyph).selectAll('*').each(function () {
      const tempWidth = parseFloat(this.getAttribute('stroke-width') ?? '0');
      strokeWidth = Math.max(tempWidth, strokeWidth);
    });

    let x, y, width, height;
    width = bbox.width + strokeWidth * 2;
    height = bbox.height + strokeWidth * 2;
    x = bbox.x - strokeWidth;
    y = bbox.y - strokeWidth;

    const newMaskRect = mask.append('rect')
        .attr('transform', 'translate(' + [x, y] + ')')
        .attr('class', 'Wipe')
        .attr('width', width)
        .attr('height', height)
        .attr('stroke-width', strokeWidth)
        .attr('fill', 'white')
        .attr('stroke', 'white')

    return newMaskRect.node();
  }

  /**
   * Same as Grow effect, but applied on mark
   * @param {SVGGraphicsElement} mask
   * @param {SVGGraphicsElement} glyph
   * @returns {gsap.core.Tween|null}
   */
  createTwForMask(mask, glyph) {
    const {enterOrExit, direction, ease, duration, yoyo} = this.settings;
    const bbox = mask.getBBox();

    let offsetFactorX, offsetFactorY;

    switch (direction) {
      case Direction.TOP:
        offsetFactorX = 0;
        offsetFactorY = -1;
        break;
      case Direction.BOTTOM:
        offsetFactorX = 0;
        offsetFactorY = 1;
        break;
      case Direction.LEFT:
        offsetFactorX = -1;
        offsetFactorY = 0;
        break;
      case Direction.RIGHT:
        offsetFactorX = 1;
        offsetFactorY = 0;
        break;
    }

    const strokeWidth = parseFloat(glyph.getAttribute('stroke-width') || '0');
    const vars = {
      x: '+=' + (offsetFactorX * (bbox.width + strokeWidth)),
      y: '+=' + (offsetFactorY * (bbox.height + strokeWidth)),
      duration,
      ease
    };

    if (yoyo) {
      vars.yoyo = true;
      vars.repeatDelay = yoyo.delay ?? 0;
      vars.repeat = yoyo.repeat ?? 1;
    }

    // console.log('Wipe', mask, vars);
    return enterOrExit === 'exit' ? gsap.to(mask, vars) : gsap.from(mask, vars);
  }
}
