import {RenderContext} from "@/core/entities/context/RenderContext";
import {SimpleTargetModel} from "@/core/generator/target-model/SimpleTargetModel";

export class Generator {
  /** @type {RenderContext} */
  renderContext
  /** @type {BaseAnimation} */
  animationSpec

  /**
   * @param {d3.Selection} rootSel
   * @param {TemplateLibrary} templateLib
   * @param {BaseAnimation} animationSpec
   */
  init(rootSel, templateLib, animationSpec) {
    this.renderContext = new RenderContext(rootSel, templateLib);
    this.animationSpec = animationSpec;
  }

  /**
   * @returns {gsap.core.Timeline}
   */
  generate() {
    // console.log('spec', this.animationSpec);

    // create target module
    this.renderContext.targetModel = new SimpleTargetModel(this.renderContext.globalSelection);

    // console.log('renderContext', this.renderContext);
    this.renderContext.clean();
    const rootContext = this.renderContext.createRootContext();
    return this.animationSpec.create(rootContext);
  }

  computeDuration() {
    // create target module
    this.renderContext.targetModel = new SimpleTargetModel(this.renderContext.globalSelection);
    this.renderContext.clean();
    const rootContext = this.renderContext.createRootContext(true);
    return this.animationSpec.computeDuration(rootContext);
  }

  /**
   * @param {string} templateName
   * @returns {Set<HTMLElement>}
   */
  computePossibleElements(templateName) {
    const template = this.renderContext.getTemplate(templateName);
    if (!template) {
      return new Set();
    }
    const ani = template.instantiate({}, false);

    // create target module
    this.renderContext.targetModel = new SimpleTargetModel(this.renderContext.globalSelection);
    this.renderContext.clean();
    const rootContext = this.renderContext.createRootContext(true);

    return ani.computePossibleElements(rootContext);
  }
}
