import {AnimationParser} from "@/core/parser/AnimationParser";
import {BaseTemplate} from "@/core/entities/spec/BaseTemplate";
import {JsonUtils} from "@/core/utils/JsonUtils";
import {EmptyAnimation} from "@/core/entities/spec/animation/EmptyAnimation";

export class JsonTemplate extends BaseTemplate {
  /** @type {{}} */
  animation

  constructor(animation=null) {
    super();
    this.animation = animation;
    this.type = 'json';
  }

  /**
   * @returns {BaseAnimation}
   */
  createAnimation() {
    if (!this.animation) {
      return new EmptyAnimation();
    }
    // get a copy of animation declaration
    const animation = JSON.parse(JSON.stringify(this.animation));
    // set all parameters
    const settings = this.settings;
    JsonUtils.traversalObject(animation, (key, current, parent) => {
      if (typeof current !== 'string' || !current.includes('$')) {
        return;
      }
      current = current.trim();
      if (current.match(/^\$[0-9a-zA-Z]*$/)) {  // '$xxx'
        const paramName = current.substr(1);
        if (paramName in settings) {
          parent[key] = settings[paramName];
        }
      } else {
        let result = current;
        Object.keys(settings).forEach(paramName => {
          result = result.replaceAll('$' + paramName, settings[paramName]);
        })
        parent[key] = result;
      }
    })
    return AnimationParser.fromJSON(animation);
  }
}
