import {TemplateType} from "@/utils/const/TemplateType";
import {deepCopy} from "@/utils/common/copy";

export class AbstractAniTemplate {
  /** @type {(number|string)} */
  templateId
  /** @type {string} */
  name
  /** @type {TemplateType} */
  type
  /** @type {string[]} */
  tempLabels
  /** @type {object} */
  defaults
  /** @type {object} */
  configs

  constructor(templateId, name, type=TemplateType.UNKNOWN, tempLabels=[], defaults={}) {
    this.templateId = templateId
    this.name = name
    this.type = type
    this.tempLabels = tempLabels
    this.defaults = defaults
  }

  /**
   * @returns {object}
   */
  getMergedConfigs() {
    let config = deepCopy(this.configs) || {}
    Object.keys(this.defaults).forEach(k => {
      if (config[k] === undefined) {
        config[k] = this.defaults[k]
      }
    })
    return config
  }

  /**
   * @returns {AbstractAniInstance | null}
   */
  createInstance() {
    console.error('Not implement')
    return null
  }

  checkValid() {
    console.error('Not implement')
    return false
  }
}
