import {JsonTemplate} from "@/core/entities/spec/template/JsonTemplate";
import {ParamDefinitionParser} from "@/core/parser/ParamDefinitionParser";
import JSON5 from 'json5';

export class TemplateParser {
  static reviver(key, value) {
    return key === "" ? TemplateParser.fromJSON(value) : value;
  }

  /**
   * @param {Object} json
   * @returns {BaseTemplate}
   */
  static fromJSON(json) {
    if (typeof json === 'string') {
      return JSON5.parse(json, TemplateParser.reviver);
    }
    if (typeof json !== "object") {
      return null;
    }
    const obj = new JsonTemplate();
    let parameters;
    if (json.parameters && Array.isArray(json.parameters)) {
      parameters = json.parameters.map(p => ParamDefinitionParser.fromJSON(p));
    } else {
      parameters = [];
    }
    return Object.assign(obj, json, {parameters});
  }
}
