import { NativeTemplate } from "@/core/entities/spec/template/NativeTemplate";
import { ParamType } from "@/core/entities/spec/parameter/ParamType";
import { ParamDefinition } from "@/core/entities/spec/parameter/ParamDefinition";
import { NativeAnimation } from "@/core/entities/spec/animation/NativeAnimation";
import gsap from "gsap";
import { computeUUID } from "@/core/utils/uuid";
import { ANNOTATION_CLASS } from "@/core/const/Element";
import * as d3 from 'd3';

export class CircleTemplate extends NativeTemplate {
    constructor() {
        super();
        this.templateName = 'Circle';
        this.parameters = [
            new ParamDefinition('reverseDirection', ParamType.BOOLEAN, false, false),
            new ParamDefinition('duration', ParamType.NUMBER, false, 1),
            new ParamDefinition('ease', ParamType.STRING, false, 'power1.out'),
            new ParamDefinition('stagger', ParamType.NUMBER, false, 0),
            new ParamDefinition('displayTime', ParamType.NUMBER, false, 3),
            new ParamDefinition('r', ParamType.NUMBER, false, 15),
            new ParamDefinition('stroke', ParamType.STRING, false, "#000"),
        ];
    }

    createAnimation() {
        const animation = new CircleAnimation();
        // transfer template settings to this native animation instance
        // as their parameters are the same
        animation.updateSettings(this.getParamValues());
        return animation;
    }
}

class CircleAnimation extends NativeAnimation {
    /**
     * @param {import('@/core/entities/context/Context').Context} context
     * @returns {gsap.core.Timeline | gsap.core.Tween}
     */
    create(context) {
        const {displayTime, ease, stagger, duration, reverseDirection, r, stroke} = this.settings;
        reverseDirection
        const glyphs = context.getGlyphs(this.target);
        const tl = gsap.timeline()
        glyphs.forEach((glyph, i) => {
            const circle = this.addMaskRectToGlyph(context, glyph, r, stroke);

            // const vars = {opacity: 0, ease, duration, stagger};
            // let tw;
            // tw = gsap.from(circle, vars);
            // tl.add(tw, i === 0 ? 0 : ('<' + stagger));
            // tw = gsap.to(circle, vars);
            // tl.add(tw, '+=' + displayTime);

            let len = 0;
            try {
                len = circle.getTotalLength();
                // eslint-disable-next-line no-empty
            } catch(e) {
            }
            circle.setAttribute('stroke-dasharray', len);

            let tw;

            tw = gsap.fromTo(circle, {strokeDashoffset: reverseDirection ? -len : len},
                {strokeDashoffset: 0, ease, duration});
            tl.add(tw, i === 0 ? 0 : ('<' + stagger));

            tw = gsap.to(circle, {strokeDashoffset: reverseDirection ? len : -len, ease, duration});
            tl.add(tw, '+=' + displayTime);
        })
        return tl;
    }

    /**
     * @param {import('@/core/entities/context/Context').Context} context
     * @param {SVGGraphicsElement} glyph
     * @returns {SVGGraphicsElement} the mask rect of glyph
     */
     addMaskRectToGlyph(context, glyph, r, stroke) {
        if (!glyph.getAttribute('id')) {
          glyph.setAttribute('id', computeUUID());
        }

        // const {x, y, width, height} = context.getBBoxPosition(glyph);
        // const root_sel = context.getRootSel();
        // const el_sel = root_sel.select('svg').node() ? root_sel.select('svg') : root_sel;
        let newMaskRect = d3.select(glyph.parentNode).append('ellipse')
            // let a = d3.select(sel.parentNode).append('rect')
            .attr('transform', glyph.getAttribute('transform')+ 'translate(0,0)')
            .classed(ANNOTATION_CLASS, true)
            .attr('rx', r-3)
            .attr('ry', r-3)
            .attr('stroke-width', 2.5)
            .attr('fill', 'none')
            .attr('stroke', stroke);

        // let newMaskRect = root_sel.select('svg').append('rect')
        //     // let a = d3.select(sel.parentNode).append('rect')
        //     .attr('transform', 'translate(' + [x, y] + ')')
        //     .classed(ANNOTATION_CLASS, true)
        //     .attr('width', width)
        //     .attr('height', height)
        //     .attr('stroke-width', 2)
        //     .attr('fill', 'none')
        //     .attr('stroke', 'black')

        return newMaskRect.node();
      }
}
