// import JSON5 from 'json5';

// initial state
import {Scene} from "@/utils/common/Scene";
import {aniChart} from "@/core";
import {Notification} from "element-ui";
import {deepCopy} from "@/utils/common/copy";
import {AudioUtils} from "@/utils/audio/AudioUtils";
import {TargetUtils} from "@/utils/target/TargetUtils";
import {VegaTargetUtils} from "@/utils/target/VegaTargetUtils";
import {InsightUtils} from "@/utils/insight/InsightUtils.js";
import { SVGUtils } from "../../utils/svg/SVGUtils";
import { VegaSVGUtils } from "../../utils/svg/VegaSVGUtils";


const state = () => ({
  isInitialized: false,

  scenes: [],
  selectedScene: null,
  canvasHeight: 500,
  canvasWidth: 800,

  canvasRenderSign: 0,

  sceneEl: null,

  animationJSON: null,
  timeline: null,
  running: false,

  useAudio: true,
  audioSrc: '',
  audioEl: null,
  audioReady: false,
})

// getters
const getters = {
}

// actions
const actions = {
  async compile({commit, dispatch}) {
    await dispatch('createAudioSrc');
    commit('createAnimationJSON');
    commit('createTimeline');
  },
  createAudioSrc({state, commit}) {
    if (state.scenes.some(s => !s.audioSrc)) {
      console.warn('Missing audio resources');
      commit('setAudioSrc', '');
    }
    const audioBlobList = state.scenes
        .map(scene => scene.audioStr)
        .map(dataURI => AudioUtils.dataURItoBlob(dataURI))

    const promiseList = audioBlobList.map(blob => AudioUtils.blobToAudioBuffer(blob));
    return Promise.all(promiseList)
        .then(audioBufferList => {
          console.log(audioBufferList);

          // set audio duration
          audioBufferList.map((ab, i) => {
            const duration = AudioUtils.computeAudioDuration(ab)
            commit('setSceneAudioDuration', {sceneIndex: i, duration});
          });

          const extendTimes = audioBufferList.map(() => 1);
          const newAudioBuffer = AudioUtils.concatAudio(audioBufferList, extendTimes);
          const newAudioBlob = AudioUtils.audioBufferToBlob(newAudioBuffer);
          const audioSrc = window.URL.createObjectURL(newAudioBlob);
          commit('setAudioSrc', audioSrc);
        });
  },
}

// mutations
const mutations = {
  initProjectView(state) {
    state.scenes = [];
    this.state.resource.projectSpec.scenes.forEach(sceneSpec => {
      const scene = new Scene();
      Object.assign(scene.spec, sceneSpec);
      const chartId = sceneSpec.charts[0].source.replace('./', '').replace('.svg', '');   // temp version
      scene.chartId = chartId;

      scene.initialSvgStr = this.state.resource.svgStrMap.get(chartId);
      scene.svgStr = this.state.resource.svgStrMap.get(chartId);
      scene.audioStr = this.state.resource.audioStrMap.get(chartId);
      scene.transcript = this.state.resource.transcriptMap.get(chartId);
      scene.targetModel = this.state.resource.targetModelMap.get(chartId);
      scene.linking = this.state.resource.linkingMap.get(chartId);
      if(chartId.includes('line') || chartId.includes('bar') || chartId.includes('pie')) {
        scene.insights = this.state.resource.insightsMap.get(chartId);
      }

      if (!scene.targetModel) {
        // if live charts or not
        // console.log(chartId.includes('line') || chartId.includes('bar') || chartId.includes('pie'))
        if(chartId.includes('line') || chartId.includes('bar') || chartId.includes('pie')) {
          const {svgStr, targetModel} = scene.chartId.includes("vega") ?
            VegaTargetUtils.updateSVGModel(scene.initialSvgStr, scene.chartId):
            TargetUtils.updateSVGModel(scene.initialSvgStr, scene.chartId);
          const spec = InsightUtils.getsSVGSpec(svgStr, chartId.split('-')[0])
          scene.description = InsightUtils.formatInsightsFromGPT(scene.insights, spec)
          if(scene.chartId.includes("vega")){
            scene.svgStr = VegaSVGUtils.annotateSVGUsingInsights(scene.description, spec, svgStr).svgstr
            scene.insightDict = VegaSVGUtils.annotateSVGUsingInsights(scene.description, spec, svgStr).dict
          }
          else{
            SVGUtils.annotateSVGUsingInsights(scene.description, spec, svgStr);
          }
          if(scene.chartId.includes("vega")) {
            scene.targetModel = targetModel;
          }
          this.commit('resource/updateSVGModel', {chartId, svgStr, targetModel: deepCopy(targetModel)}, {root: true});
        }
        else {
          // TODO: classify normal svg and vega lite svg
          const {svgStr, targetModel} = VegaTargetUtils.updateSVGModel(scene.svgStr, scene.chartId);
          // const {svgStr, targetModel} = scene.chartId.includes("vega") ?
          // VegaTargetUtils.updateSVGModel(scene.initialSvgStr, scene.chartId):
          // TargetUtils.updateSVGModel(scene.initialSvgStr, scene.chartId);
          scene.svgStr = svgStr;
          scene.targetModel = targetModel;
          this.commit('resource/updateSVGModel', {chartId, svgStr, targetModel: deepCopy(targetModel)}, {root: true});
        }
      }

      state.scenes.push(scene);
    });
    state.selectedScene = state.scenes[0];

    state.isInitialized = true;
    this.commit('project/changeCanvasRenderSign');
  },
  changeSelectedScene(state, scene) {
    state.selectedScene = scene;
  },

  addScene(state, {charts}) {
    const newScene = new Scene();
    state.scenes.push(newScene);

    if (charts) {
      newScene.spec.charts = charts;
    }
  },

  mountSceneElement(state, sceneEl) {
    state.sceneEl = sceneEl;
  },

  createTimeline(state) {
    // reset template lib
    aniChart.resetTemplateLib();
    state.scenes.map(s => s.spec.templates).forEach(templates => {
      if (!templates) {
        return;
      }
      aniChart.registerTemplates(...templates);
    });

    if (state.timeline) {
      state.timeline.kill();
      state.timeline.clear();
      state.timeline = null;
    }

    try {
      state.timeline = aniChart.render(state.sceneEl, state.animationJSON, {});

      const onCompleteFunc = function() {
        state.running = false;
        state.timeline.pause();
      };
      state.timeline.eventCallback('onComplete', onCompleteFunc);

      // Notification({title: `Compile finished`,
      //   message: 'Play the animation!', type: 'success'});
    } catch (exception) {
      alert(exception)
      console.error(exception)
      Notification({title: `Compile failed`,
        message: exception.message, type: 'warning'});
    }
  },

  createAnimationJSON(state) {
    const sceneSVGList = [...state.sceneEl.querySelectorAll(':scope > svg')].reverse();
    const concat = [];

    state.scenes.forEach((scene, sceneIndex) => {
      const sceneContainerId = sceneSVGList[sceneIndex].id;

      concat.push({
        target: `#scene-cover-${sceneIndex}`,
        use: 'fade-out',
        timing: {duration: 0.5, offset: sceneIndex === 0 ? 0 : -0.5},
      });

      let animation;
      if (scene.spec.animation.sequence.sync.length !== 0) {
        animation = deepCopy(scene.spec.animation);
      } else {
        animation = {
          use: 'empty',
          timing: {duration: 2},
        }
      }
      animation.target = '.' + scene.chartId;
      const subSync = {
        sequence: {
          sync: [
            animation,
            {
              use: "empty",
              timing: {duration: scene.audioDuration},
            }
          ]
        }
      }
      concat.push(subSync);

      concat.push({
        target: `#${sceneContainerId}`,
        use: 'fade-out',
        timing: {duration: 0.5, offset: 1},
      });
    });

    state.animationJSON = {
      sequence: {concat},
    };
  },

  changeCanvasRenderSign(state) {
    state.canvasRenderSign ^= 1;
  },

  // audio
  mountAudioElement(state, audioEl) {
    state.audioEl = audioEl;
  },
  setAudioSrc(state, audioSrc) {
    state.audioSrc = audioSrc
    if (state.setAudioReadyFunc) {
      state.audioEl.removeEventListener("canplaythrough", state.setAudioReadyFunc, false)
    } else {
      state.setAudioReadyFunc = function() {
        state.audioReady = true
      }
    }
    state.audioReady = false;
    state.audioEl.addEventListener("canplaythrough", state.setAudioReadyFunc, false);
  },
  changeUseAudio(state, useAudio) {
    state.useAudio = useAudio;
  },
  changeAudioReady(state, isReady) {
    state.audioReady = isReady
  },
  setSceneAudioDuration(state, {sceneIndex, duration}) {
    state.scenes[sceneIndex].audioDuration = duration;
  },

  // timeline
  playAnimation(state) {
    state.running = true;
    state.timeline.resume();

    if (state.audioReady && state.useAudio) {
      state.audioEl.play()
    }
  },
  pauseAnimation(state) {
    state.running = false;
    state.timeline.pause();

    if (state.audioReady && state.useAudio) {
      state.audioEl.pause()
    }
  },
  restartAnimation(state) {
    state.running = true;
    state.timeline.restart();

    if (state.audioReady && state.useAudio) {
      state.audioEl.pause()
      state.audioEl.currentTime = 0
      state.audioEl.play()
    }
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
