import {NativeTemplate} from "@/core/entities/spec/template/NativeTemplate";
import {ParamType} from "@/core/entities/spec/parameter/ParamType";
import {ParamDefinition} from "@/core/entities/spec/parameter/ParamDefinition";
import {NativeAnimation} from "@/core/entities/spec/animation/NativeAnimation";
import gsap from "gsap";

export class CustomEffectTemplate extends NativeTemplate {
  constructor() {
    super();
    this.templateName = 'custom-effect';
    this.parameters = [
      new ParamDefinition('attr', ParamType.STRING_OR_ARRAY),
      new ParamDefinition('from', ParamType.ANY, false, null),
      new ParamDefinition('to', ParamType.ANY, false, null),
      new ParamDefinition('ease', ParamType.STRING, false, 'power1.out'),
      new ParamDefinition('stagger', ParamType.NUMBER, false, 0),
    ];
  }

  isValid() {
    if (!super.isValid()) {
      return false;
    }
    const p = this.getParamValues();
    if (!p.attr) {
      return false;
    }
    if (!Array.isArray(p.attr)) {
      return p.from !== null || p.to !== null;
    } else {
      if (p.from !== null && p.to !== null) {
        return Array.isArray(p.from) && p.from.length === p.attr.length
            && Array.isArray(p.to) && p.to.length === p.attr.length;
      } else if (p.from !== null) {
        return Array.isArray(p.from) && p.from.length === p.attr.length;
      } else if (p.to !== null) {
        return Array.isArray(p.to) && p.to.length === p.attr.length;
      }
      return false;
    }
  }

  createAnimation() {
    const animation = new CustomEffectAnimation();
    // transfer template settings to this native animation instance
    // as their parameters are the same
    animation.updateSettings(this.getParamValues());
    return animation;
  }
}

class CustomEffectAnimation extends NativeAnimation {
  /**
   * @param {import('@/core/entities/context/Context').Context} context
   * @returns {gsap.core.Timeline | gsap.core.Tween}
   */
  create(context) {
    const {attr, from, to, ease, stagger} = this.settings;
    const {duration, yoyo} = this.timing.getComputedTiming();
    const glyphs = context.getGlyphs(this.target);

    let attrArray, fromArray, toArray;
    let tw;

    if (!Array.isArray(attr)) {
      attrArray = [attr];
      if (from !== null) {
        fromArray = [from];
      }
      if (to !== null) {
        toArray = [to];
      }
    } else {
      attrArray = attr;
      fromArray = from;
      toArray = to;
    }

    const merge = (target, keys, values) => keys.forEach((k, i) => target[k] = values[i]);

    if (from != null && to != null) {
      const fromVars = {};
      merge(fromVars, attrArray, fromArray);
      const toVars = {ease, duration, stagger};
      merge(toVars, attrArray, toArray);
      tw = gsap.fromTo(glyphs, fromVars, toVars);

    } else if (from != null) {
      const fromVars = {ease, duration, stagger};
      merge(fromVars, attrArray, fromArray);
      tw = gsap.from(glyphs, fromVars);

    } else if (to != null) {
      const toVars = {ease, duration, stagger};
      merge(toVars, attrArray, toArray);
      tw = gsap.to(glyphs, toVars);

    } else {
      return null;
    }

    if (yoyo) {
      tw.yoyo(true);
      tw.repeat(yoyo.repeat);
      tw.repeatDelay(yoyo.delay);
    }

    return tw;
  }
}
